import FavoriteIcon from "@mui/icons-material/Favorite";
import FavoriteBorderIcon from "@mui/icons-material/FavoriteBorder";
import ShoppingCartIcon from "@mui/icons-material/ShoppingCart";
import {
  Box,
  Button,
  CardActionArea,
  CardActions,
  IconButton,
  Tooltip,
} from "@mui/material";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Typography from "@mui/material/Typography";
import React, { useState } from "react";
import { MarketApi } from "~/apis";
import { MarkList } from "~/apis/types";
import { App } from "~/intance";
import { deleteReq, postReq, ResponseStatus } from "~/utils/axios";
import { message } from "~/utils/message";
import { previewChannel } from "~/utils/previewChannel";
import ConfirmModal from "../common/confirm";
import { HStack } from "../common/stack";
import BuyItemPanel from "./buyItem";

export enum CollectStatus {
  True = 0,
  False = 1,
  Disable = 2,
  None = 3,
}

interface ContentItemProps {
  shop: MarkList;
  applyStart: (s) => void;
  update: () => void;
  isBuy: boolean | null;
  isCollect: CollectStatus;
}

let onlyBuy = false;

const ContentItem: React.FC<ContentItemProps> = ({
  shop,
  applyStart,
  update,
  isCollect,
  isBuy,
}) => {
  const [showBtn, setShowBtn] = useState(false);
  const [startBuy, setStartBuy] = useState(false);

  const buy = async () => {
    let res = await postReq(MarketApi.buy, { productCode: shop.product_code });
    if (res.code === ResponseStatus.Ok) {
      message.success("商品购买成功");
      update();
      return true;
    }
    return false;
  };
  const like = async () => {
    if (isCollect === CollectStatus.True) {
      let res = await deleteReq(MarketApi.cancelCollect + shop.product_code);
      if (res.code === ResponseStatus.Ok) {
        message.success("取消收藏成功");
        update();
      }
    } else if (isCollect === CollectStatus.False) {
      let res = await postReq(MarketApi.collect, {
        productCode: shop.product_code,
      });
      if (res.code === ResponseStatus.Ok) {
        message.success("商品收藏成功");
        update();
      }
    }
  };

  const preview = (uuid: string) => {
    if (!uuid) {
      message.error("确实商品数据");
      return;
    }
    const iHeight = 800;
    const iWidth = 800;
    //获得窗口的垂直位置
    const iTop = (window.screen.availHeight - 30 - iHeight) / 2;
    //获得窗口的水平位置
    const iLeft = (window.screen.availWidth - 10 - iWidth) / 2;

    const w = window.open(
      "about:blank",
      // "newWindow",
      `menubar=0,scrollbars=1, resizable=1,status=1,titlebar=0,toolbar=0,location=no,width=${iWidth},height=${iHeight},left=${iLeft},top=${iTop}`
    );
    w.location.href = "/shop.html?code=" + uuid;
    const callback = (evt) => {
      if (evt?.data.type === "close_preview") {
        w.close();
        previewChannel.removeEventListener("message", callback);
      }
    };
    previewChannel.addEventListener("message", callback);
  };
  const apply = async () => {
    if (isBuy) {
      applyStart(shop);
    } else {
      onlyBuy = false;
      setStartBuy(true);
    }
  };

  const handleEnter = () => {
    setShowBtn(true);
  };
  const handleOut = () => {
    setShowBtn(false);
  };

  const handleBuyAndApply = async () => {
    const res = await buy();
    if (res) {
      setStartBuy(false);
      if (!onlyBuy) applyStart(shop);
    }
  };

  const renderCollectBtn = () => {
    if (isCollect === CollectStatus.None) return;

    return (
      <Tooltip
        title={isCollect === CollectStatus.True ? "取消收藏" : "收藏"}
        placement="bottom"
      >
        <span>
          <IconButton
            disabled={isCollect === CollectStatus.Disable}
            style={
              isCollect === CollectStatus.Disable
                ? { pointerEvents: "none" }
                : {}
            }
            onClick={like}
          >
            {isCollect === CollectStatus.True ? (
              <FavoriteIcon color="error" />
            ) : (
              <FavoriteBorderIcon />
            )}
          </IconButton>
        </span>
      </Tooltip>
    );
  };

  const onDragstart = (event: React.DragEvent) => {
    shop.isBuy = typeof isBuy === "boolean" ? isBuy : true;
    event.dataTransfer.setData(
      "text/plain",
      JSON.stringify({
        key: "shop",
        shop: shop,
      })
    );
  };

  return (
    <Card
      sx={{ position: "relative" }}
      onMouseEnter={handleEnter}
      onMouseLeave={handleOut}
      onDragStart={onDragstart}
    >
      <CardActionArea>
        <CardMedia
          component="img"
          height="140"
          image={
            shop.pic
              ? App.prefix + shop.real_code + "/" + shop.pic
              : "https://mui.com/static/images/cards/contemplative-reptile.jpg"
          }
          alt="green iguana"
        />
        <CardContent>
          <Typography gutterBottom variant="subtitle1" component="div">
            {shop.product_name || "无效素材"}
          </Typography>
          <Typography variant="body2">{shop.possessor_code}</Typography>
        </CardContent>
      </CardActionArea>
      <CardActions sx={{ justifyContent: "space-between" }}>
        <Typography sx={{ color: "#424B54" }}>
          积分:
          <span style={{ color: "#FF6600", marginLeft: 5 }}>{shop.score}</span>
        </Typography>
        <HStack>
          {renderCollectBtn()}

          {(typeof isBuy !== "boolean" || !isBuy) && (
            <Tooltip title="购买" placement="bottom">
              <span>
                <IconButton
                  onClick={() => {
                    setStartBuy(true);
                    onlyBuy = true;
                  }}
                  sx={{ color: "#FF6600" }}
                  disabled={isBuy === null}
                >
                  <ShoppingCartIcon />
                </IconButton>
              </span>
            </Tooltip>
          )}
        </HStack>
      </CardActions>
      {showBtn && (
        <Box
          sx={{
            position: "absolute",
            left: 0,
            right: 0,
            bottom: 0,
            top: 20,
            margin: "auto",
            width: "20%",
            height: 30,
            display: "flex",
            justifyContent: "center",
          }}
        >
          <Button
            variant="contained"
            sx={{ mr: 1 }}
            onClick={() => preview(shop.product_code)}
          >
            预览
          </Button>
          <Button variant="contained" onClick={apply}>
            应用
          </Button>
        </Box>
      )}
      {startBuy && (
        <ConfirmModal
          open={startBuy}
          onOk={handleBuyAndApply}
          onCancel={() => {
            setStartBuy(false);
          }}
          content={<BuyItemPanel shop={shop} />}
          title="您即将购买"
        />
      )}
    </Card>
  );
};
export default ContentItem;
