import { defaultCode } from "./../components/commonTab/defaultCode";
import { rootStore } from "./RootStore";
import { message } from "~/utils/message";
import { IDirReturnDataList, IFileReturnData } from "~/apis/types";
import { FileApi, DirSourceType, AppApi } from "~/apis";
import {
  deleteReq,
  getReq,
  IResponseResult,
  postReq,
  putReq,
  ResponseStatus,
} from "~/utils/axios";
import { computed, makeAutoObservable, runInAction, toJS } from "mobx";
import { IComponentData } from "~/components/editorModal/types";
import {
  getUniupComponentType,
  isRegisterComponent,
  registerComponent,
} from "~/utils/component";
import { InDb, StoreName } from "~/utils/db";
import { unRegisterComponent } from "./../utils/component";
import {
  createFileDir,
  editorFile,
  getFileDetail,
  getFileInfo,
  getFileList,
  updateFile,
  uploadFile,
} from "~/apis/fileReq";
import { getFileUrl } from "~/utils/url";
import { IDataItemProps } from "~/components/dataList/types";

const VERSION = 1;

export class CommonStore {
  dirList: IFileReturnData[] = [];
  assetMap = new Map<string, string>();
  usedAssets = new Set<string>();
  private _open = false;
  private _current: IComponentData = null;
  private _selectId = 0;
  private collKeys = new Set<number>();
  private _prefix = "";
  constructor() {
    makeAutoObservable(this, { assetMap: false, usedAssets: false });
  }
  get selectId() {
    return this._selectId;
  }
  set selectId(v: number) {
    this._selectId = v;
  }
  get open() {
    return this._open;
  }
  set open(v: boolean) {
    this._open = v;
  }
  get current() {
    return this._current;
  }
  set current(v: IComponentData) {
    if (!v) {
      this._current = null;
      return;
    }
    if (this._current) {
      Object.assign(this._current, v);
    } else this._current = toJS(v);
  }
  startEditor(dirId: number, code?: string) {
    this.open = true;
    this.current = {
      code: code ?? defaultCode,
      id: undefined,
      title: undefined,
      propertys: [],
    };
    this.selectId = dirId;
  }
  toggleExpand(k: number) {
    if (this.collKeys.has(k)) this.collKeys.delete(k);
    else {
      this.collKeys.add(k);
      this.updateDirList(k);
    }
    this.collKeys = new Set(toJS(this.collKeys));
  }
  @computed
  isExpand(k: number) {
    return this.collKeys.has(k);
  }
  get prefix() {
    return this._prefix;
  }
  set prefix(v: string) {
    this._prefix = v;
  }
  loadLocal = async () => {
    const db = await InDb.getIdb();
    const cs = await db.getAll(StoreName.Component);
    cs.map((c) => registerComponent(c));
    return cs;
  };
  async updateDirList(
    key: number | number[],
    list: IFileReturnData[] = this.dirList
  ) {
    if (!Array.isArray(key)) {
      key = [key];
    }
    const kMap = new Map<number, IFileReturnData[]>();
    for (const k of key) {
      let list = await getFileList(DirSourceType.Component, k);
      if (!list) list = [];
      kMap.set(k, list);
    }

    for (const d of list) {
      if (kMap.has(d.id)) {
        const children: IDataItemProps[] = [];
        for (const c of kMap.get(d.id)) {
          const info = await getFileInfo(c.id);
          const json = info.json_data;
          let componentData: any = {};
          if (json) {
            const jsonData = JSON.parse(json);
            componentData = jsonData.data;
            componentData.camera = jsonData.camera;
            registerComponent({ ...componentData, id: c.id });
          }
          children.push({
            ...componentData,
            id: c.id,
            title: c.name,
            preview: getFileUrl(c, true, true),
            data: {
              id: c.id,
              component: true,
              key: getUniupComponentType(c.id),
              name: c.name,
              modelId: c.uuid,
            },
            moduleIds: info?.resources ?? "",
          });
        }
        runInAction(() => {
          d.children = children;
        });
      }
    }
    runInAction(() => {
      this.dirList = list;
    });
  }
  getComponents(nodes: IDirReturnDataList[] = []) {
    if (!nodes) return [];
    return nodes.map((file) => {
      const json = JSON.parse(file.data.jsonData);
      const d = json?.version ? json.data : json;
      registerComponent({ ...d, id: file.data.ID });
      return {
        ...d,
        id: file.data.ID,
        title: file.title,
        preview: file.data.picUrl,
        data: {
          id: file.data.ID,
          component: true,
          key: getUniupComponentType(file.data.ID),
          name: file.title,
        },
      };
    });
  }
  async loadUsedComponent(ids: number[] = []) {
    for (const id of ids) {
      if (isRegisterComponent(id)) continue;
      const data = await getFileDetail(id);
      if (data) {
        const componentData = JSON.parse(data);
        const d = componentData.data;
        registerComponent({ ...d, id });
      }
    }
  }
  /**dir curd */
  addDir() {
    const newData = {
      name: "新建目录",
      node_type: "dir",
      id: -1,
      editor: true,
    };

    this.dirList.push(newData);
  }
  async handleDir(data: IFileReturnData) {
    let result: IResponseResult;
    if (data.id === -1) {
      result = await createFileDir({
        name: data.name,
        sourceType: DirSourceType.Component,
      });
    } else {
      result = await editorFile(data);
    }

    if (result.code === ResponseStatus.Ok) {
      this.load();
    }
  }
  startRenameDir() {
    for (const dir of this.dirList) {
      if (dir.id === this._selectId) {
        dir.editor = true;
        return;
      }
    }
  }
  async deleteDir() {
    const result = await deleteReq(FileApi.delete, {
      data: { id: this._selectId },
    });

    if (result.code === ResponseStatus.Ok) {
      this.selectId = 0;
      this.load();
    }
  }

  async load(query?: string) {
    const list = await getFileList(DirSourceType.Component, 0, query);
    if (list) {
      await this.updateDirList(Array.from(this.collKeys), list);
    }
  }
  async addComponent(newCom: IComponentData, resource: string, camera: any[]) {
    if (!this._selectId) {
      message.error("没选择目录");
      return;
    }

    registerComponent(newCom);

    // const db = await InDb.getIdb();
    // db.put(StoreName.Component, newCom.id, newCom);
    const result = await uploadFile({
      name: newCom.title,
      jsonData: JSON.stringify({
        version: VERSION,
        data: this.getCompontData(newCom),
        camera,
      }),
      sourceType: DirSourceType.Component,
      moduleIds: resource,
      projectId: rootStore.projectId,
      pid: this._selectId,
    });

    if (result.code === ResponseStatus.Ok) {
      // this.load();
      return result.data?.[0]?.id;
    }
  }
  updateComponent(newCom: IComponentData, camera: any[]) {
    return runInAction(async () => {
      registerComponent(toJS(newCom));
      // db.put(StoreName.Component, com.id, toJS(com));
      const result = await updateFile({
        id: newCom.id,
        name: newCom.title,
        sourceType: DirSourceType.Component,
        resources: newCom.moduleIds,
        json_data: JSON.stringify({
          version: VERSION,
          data: this.getCompontData(newCom),
          camera,
        }),
        pid: this._selectId,
      });

      return result.code === ResponseStatus.Ok;
    });
  }
  async deleteComponent(component: IComponentData) {
    unRegisterComponent(component);
    // const db = await InDb.getIdb();
    // db.delete(StoreName.Component, component.id);
    const result = await deleteReq(FileApi.delete, {
      data: { id: component.id },
    });

    if (result.code === ResponseStatus.Ok) {
      this.load();
      message.success("删除成功");
    }
  }
  private getCompontData(data: Partial<IComponentData>): IComponentData {
    return { code: data.code, propertys: data.propertys };
  }
  async getPrefix() {
    const result = await getReq(AppApi.prefix);
    if (result.code === ResponseStatus.Ok) {
      this.prefix = result.data.prefix;
    }
  }
}

export const commonStore = new CommonStore();
