import { debounceCallback } from "~/utils/time";
import { postReq, putReq } from "~/utils/axios";
import { Utils } from "@hc/engine";
import {
  action,
  computed,
  makeAutoObservable,
  observable,
  runInAction,
  toJS,
  when,
} from "mobx";
import { AppApi, DirSourceType } from "~/apis";
import {
  deleteApp,
  getAppDetailInfo,
  getAppList,
  updateApp,
  uploadApp,
  uploadAppPreview,
} from "~/apis/appReq";
import { IDataItemProps } from "~/components/dataList/types";
import { App } from "~/intance";
import { message } from "~/utils/message";
import { getFileUrl, updateLocationUrl } from "~/utils/url";

import { ResponseStatus } from "./../utils/axios";
import { commonStore } from "./commonStore";
import { rootStore } from "./RootStore";
import { Bus } from "~/utils/bus";

const VERSION = 1;

export type IScriptProps = {
  name: string;
  code: string;
  id: number;
};

export class FileStore {
  isOpenModal = false;
  fileList: IDataItemProps[] = [];
  currentFileId = 0;
  currentFileName = "未命名场景";
  loadingEntity = { loading: false, value: 0 };
  selectedModels: IDataItemProps[] = [];
  modelIds: Set<string> = new Set();
  componentIds: Set<number> = new Set();
  saveNewFile = null;
  selectFile = { id: 0, name: "" };
  codeList: IScriptProps[] = [];
  url2d = "";
  private _editorScriptId = 0;
  private _loading = false;

  constructor() {
    makeAutoObservable(this, {
      modelIds: false,
      componentIds: false,
      saveNewFile: false,
    });

    const previewChannel = new BroadcastChannel("preview_channel");
    previewChannel.addEventListener("message", (evt) => {
      if (evt.data === "load") {
        previewChannel.postMessage({
          type: "data",
          data: App.intance?.saveFile?.(),
          components: Array.from(this.componentIds),
        });
      }
    });
  }
  get Url2d() {
    return this.url2d;
  }
  set Url2d(v: string) {
    this.url2d = v;
  }
  get loading() {
    return this._loading;
  }
  set loading(v: boolean) {
    this._loading = v;
  }
  set SelectFile(v: { id?: number; name?: string }) {
    Object.assign(this.selectFile, v);
  }
  get editorScriptId() {
    return this._editorScriptId;
  }
  set editorScriptId(id: number) {
    this._editorScriptId = id;
  }
  addUseModel(id: string) {
    if (id) this.modelIds.add(id);
  }
  async addFile(fileName: string, jsonData?: any) {
    this.currentFileName = fileName;
    const data = {
      title: fileName,
      id: -1,
      preview: "",
      data: { key: "file" },
    };

    if (this.saveNewFile) {
      jsonData = this.saveNewFile;
      this.saveNewFile = null;
    } else {
      this.reset();
      if (!jsonData) App.intance.new();
    }

    const result = await uploadApp({
      name: fileName,
      pic_key: "icon.png",
      jsonData: JSON.stringify({
        version: 1,
        data: jsonData,
        componentIds: Array.from(this.componentIds),
      }),
      sourceType: DirSourceType.Files,
      resources: Array.from(this.modelIds).join(","),
      projectId: rootStore.projectId,
    });

    if (result.code === ResponseStatus.Ok) {
      data.id = result.data.id;
      data.preview = result.data.uuid + "/icon.png";
      await uploadAppPreview(result.data.id, () =>
        App.intance.Viewer.getPreviewUrl()
      );
      this.fileList.push(data);
      if (this.fileList.length === 1) {
        this.setAppIndex(result.data.id);
      }
      this.currentFileId = result.data.id;
      return this.currentFileId;
    } else if (result.code === ResponseStatus.Error) {
      if (result.error) {
        message.error(result.error[0]);
        return false;
      }
    }
  }
  @action
  async save(updatePreview = true) {
    const fileData = App.intance.saveFile();
    const data = {
      version: VERSION,
      data: fileData,
      components: Array.from(this.componentIds),
      scripts: toJS(this.codeList),
      url2d: this.url2d,
    };
    if (updatePreview && this.currentFileId)
      await uploadAppPreview(this.currentFileId, () =>
        App.intance.Viewer.getPreviewUrl()
      );

    if (this.currentFileId) {
      for (const f of this.fileList) {
        if (f.id === this.currentFileId) {
          return runInAction(async () => {
            Object.assign(f, {
              data: data,
              preview: f.preview + "?t=" + Date.now(),
            });
            const result = await updateApp({
              id: this.currentFileId,
              jsonData: JSON.stringify(data),
              name: this.currentFileName,
              resources: Array.from(this.modelIds).join(","),
              project_id: rootStore.projectId,
            });

            return result.code === ResponseStatus.Ok;
          });
        }
      }
    } else {
      this.saveNewFile = fileData;
      this.isOpenModal = true;
      // return this.addFile(this.currentFileName || "未命名场景", fileData);
    }
    return false;
  }
  async updateFileName(name: string, id = this.currentFileId) {
    this.currentFileName = name;
    for (const f of this.fileList) {
      if (f.id === id) {
        Object.assign(f, {
          title: name,
        });
        debounceCallback(async () => {
          const result = await updateApp({
            id: id,
            name: name,
            project_id: rootStore.projectId,
          });

          return result.code === ResponseStatus.Ok;
        }, 300);
        return true;
      }
    }
    return false;
  }
  @action
  async load(id: number) {
    this.reset();
    this.currentFileId = id;

    const fileData = await getAppDetailInfo(id);
    if (fileData?.json_data) {
      this.currentFileName = fileData.name;
      const data = JSON.parse(fileData.json_data);
      this.url2d = data.url2d;
      this.componentIds = data.components
        ? new Set(toJS(data.components))
        : new Set();

      observable(this.codeList).replace(data.scripts ?? []);

      fileData.resources.split(",").forEach((id) => this.modelIds.add(id));

      await commonStore.loadUsedComponent(data.components);
      App.intance.loadFile(data?.data ?? null);
    }
  }
  @computed
  getIsSelect(id: number) {
    return this.currentFileId === id;
  }
  async loadFileList(name?: string) {
    this.fileList.length = 0;
    this.loading = true;

    await when(() => !!rootStore.projectId);

    const list = await getAppList(rootStore.projectId, name);

    this.loading = false;
    if (list) {
      return runInAction(() => {
        for (const f of list) {
          if (f.file_key) continue;
          this.fileList.push({
            id: f.id,
            title: f.name,
            preview: getFileUrl(f, true),
            data: { key: "file" },
          });
        }
      });
    }
  }
  async deleteFile(id: number) {
    Utils.arrayRemoveIf(this.fileList, (f) => f.id === id);
    if (this.currentFileId === id) {
      this.currentFileId = undefined;
      updateLocationUrl("id", "");
      Bus.trigger({ type: "Delete-File", target: null });
    }

    const status = await deleteApp(id);

    if (status) {
      this.SelectFile = { id: 0, name: "" };
      message.success("删除文件成功");
    }
  }

  exportFile() {
    const data = App.intance.saveFile();
    Utils.FileSystem.writeFile(
      this.currentFileName + ".hc3d",
      JSON.stringify({
        version: VERSION,
        components: Array.from(this.componentIds),
        data,
        type: "hc3d",
      })
    );
  }
  importFile() {
    Utils.FileSystem.chooseFile({
      filter: "hc3d",
      callback: (fileList) => {
        if (fileList[0]) {
          const reader = new FileReader();
          reader.readAsText(fileList[0]);
          reader.onload = (e) => {
            try {
              const json = JSON.parse(reader.result as string);
              if (json.type === "hc3d") {
                App.intance.loadFile(json.data);
                this.componentIds = new Set(json.components ?? []);
              }
            } catch (err) {
              console.log("err: ", err);
            }
          };
        }
      },
    });
  }
  @action
  openModal(isadd = false) {
    if (isadd) {
      this.currentFileId = undefined;
    }
    this.isOpenModal = true;
  }
  closeModal() {
    this.isOpenModal = false;
  }
  @action
  setLoadProcess(loading: boolean, value = 0) {
    Object.assign(this.loadingEntity, { loading, value });
  }

  async copy(id: number, project?: number) {
    const res = await postReq(AppApi.copy, {
      src_id: id,
      project_id: project ?? rootStore.projectId,
    });

    return res.code === ResponseStatus.Ok;
  }
  async move(project: number, id: number, isCopy: boolean) {
    if (isCopy) {
      return await this.copy(id, project);
    } else {
      const res = await putReq(AppApi.update, {
        id: id,
        project_id: project,
      });
      return res.code === ResponseStatus.Ok;
    }
  }
  async setAppIndex(id: number) {
    postReq(AppApi.preview, { id }).then((r) => {
      rootStore.appIndexId = id;
    });
  }
  set SelectedModels(selectedModels: IDataItemProps[]) {
    this.selectedModels = selectedModels;
  }
  get SelectedModels() {
    return this.selectedModels;
  }
  reset() {
    this.currentFileId = undefined;
    this.modelIds.clear();
    this.codeList.length = 0;
  }
  appendCodeScript(sp: IScriptProps) {
    this.codeList.push(sp);
  }
  updateCodeScript(sp: IScriptProps) {
    for (const s of this.codeList) {
      if (s.id === sp.id) {
        Object.assign(s, sp);
      }
    }
  }
  deleteScript(id: number) {
    Utils.arrayRemoveIf(this.codeList, (sp) => sp.id === id);
  }
}

export const fileStore = new FileStore();
