import {
  Button,
  Checkbox,
  DialogActions,
  DialogContent,
  FormControlLabel,
  Typography,
} from "@mui/material";
import Dialog from "@mui/material/Dialog";
import DialogTitle from "@mui/material/DialogTitle";
import * as React from "react";
import { ProjectApi } from "~/apis";
import { ProjectData, ProjectList } from "~/apis/types";
import { useStores } from "~/contexts/store-context";
import { rootStore } from "~/stores/RootStore";
import { getReq, ResponseStatus } from "~/utils/axios";
import { message } from "~/utils/message";

import SelectProjectList from "../common/projectList";

interface MoveAppPanelProps {
  onClose: () => void;
  open: boolean;
  target: number;
}

const MoveAppPanel: React.FC<MoveAppPanelProps> = ({
  onClose,
  open,
  target,
}) => {
  const { fileStore } = useStores();
  const [page, setPage] = React.useState({
    page: 1,
    limit: 20,
    project_type: "3d",
  });
  const [copy, setCopy] = React.useState(false);
  const [list, setList] = React.useState<ProjectList[]>([]);
  const [pid, setPid] = React.useState(null);

  const update = async () => {
    const data = await getReq<ProjectData>(ProjectApi.list, { params: page });
    if (data.code === ResponseStatus.Ok) {
      setList(data.data.list);
    } else {
      setList([]);
    }
  };

  const select = (id: number) => {
    setPid(id);
  };

  const move = async () => {
    if (!pid) return;
    onClose();
    const res = await fileStore.move(pid, target, copy);
    if (res) {
      message.success("应用移动成功");
      if (!copy) {
        fileStore.loadFileList();
      }
    }
  };

  React.useEffect(() => {
    update();
  }, [page]);

  return (
    <Dialog onClose={onClose} open={open}>
      <DialogTitle>移动到</DialogTitle>
      <DialogContent>
        <Typography>项目</Typography>
        <SelectProjectList
          onClick={select}
          disableId={rootStore.projectId}
          selectId={pid}
        />
        <FormControlLabel
          label="创建并移动副本"
          checked={copy}
          control={<Checkbox />}
          onChange={(evt, c) => setCopy(c)}
        />
      </DialogContent>
      <DialogActions>
        <Button variant="contained" onClick={move}>
          确定
        </Button>
        <Button color="secondary" variant="contained" onClick={onClose}>
          取消
        </Button>
      </DialogActions>
    </Dialog>
  );
};
export default MoveAppPanel;
