import React from "react";
import Icon from "@mui/material/Icon";
import { Tooltip } from "@mui/material";
export interface IconAliProps {
  name: string;
  color?: string;
  fontSize?: number;
}
const IconAli: React.FC<IconAliProps> = ({ name, color, fontSize }) => {
  return (
    <Icon
      className={"iconfont " + name}
      sx={{
        fontSize: fontSize || 18,
        color: color || "#000",
      }}
    ></Icon>
  );
};
export default IconAli;
