import { IVariableOption } from "@hc/engine";
import { makeAutoObservable, observable, toJS } from "mobx";
import { ProjectApi } from "~/apis";
import { getReq, putReq, ResponseStatus } from "~/utils/axios";
import { message } from "~/utils/message";

import { rootStore } from "./RootStore";

let varIndex = 1;

export class GlobalVarStore {
  private _open = false;
  variables: IVariableOption[] = [
    // { key: "is3D", value: 1, desc: "判断是否进入3D页面" },
  ];
  oldOvariables: IVariableOption[] = [];
  globalId: number;
  private currentIndex = -1;
  constructor() {
    makeAutoObservable(this, {
      oldOvariables: false,
      globalId: false,
    });
  }
  get Open() {
    return this._open;
  }
  set Open(v: boolean) {
    if (v) {
      this.oldOvariables = toJS(this.variables);
    }
    this._open = v;
  }
  get CurrentIndex() {
    return this.currentIndex;
  }
  set CurrentIndex(v: number) {
    this.currentIndex = v;
  }
  setVariable(v: IVariableOption[]) {
    observable(this.variables).replace(v);
  }
  add() {
    let newKey = "newVar" + varIndex++;
    if (this.variables.find((v) => v.key === newKey)) {
      newKey += "(1)";
    }
    this.currentIndex =
      this.variables.push({
        key: newKey,
        value: "",
        desc: "",
      }) - 1;
  }
  delete() {
    if (this.currentIndex >= 0) {
      this.variables.splice(this.currentIndex, 1);
      this.currentIndex = this.currentIndex - 1 < 0 ? 0 : this.currentIndex - 1;
    }
  }
  update(key: string, newVar: Partial<IVariableOption>) {
    const par = this.variables.find((v) => v.key === key);
    if (par) {
      Object.assign(par, newVar);
    }
  }
  up() {
    const index = this.currentIndex;
    if (index > 0) {
      [this.variables[index], this.variables[index - 1]] = [
        this.variables[index - 1],
        this.variables[index],
      ];
      this.currentIndex -= 1;
    }
  }
  down() {
    const index = this.currentIndex;
    if (index < this.variables.length - 1) {
      [this.variables[index], this.variables[index + 1]] = [
        this.variables[index + 1],
        this.variables[index],
      ];
      this.currentIndex += 1;
    }
  }
  confirm() {
    this._open = false;
    this.save();
  }
  async load(projectId: number) {
    const pdetail = await getReq(ProjectApi.detail + projectId);
    if (pdetail.code === ResponseStatus.Ok && pdetail.data?.info) {
      const variable = pdetail.data.info.variable;
      if (variable) {
        this.variables = JSON.parse(variable);
      }
    }
  }
  async save() {
    const res = await putReq(ProjectApi.edit, {
      id: rootStore.projectId,
      variable: JSON.stringify(toJS(this.variables)),
    });

    if (res.code === ResponseStatus.Ok) {
      message.success("变量保存成功");
    }
  }
  cancel() {
    this.reset();
    this._open = false;
  }
  reset() {
    observable(this.variables).replace(this.oldOvariables);
  }
}

export const globalVarStore = new GlobalVarStore();
