import React from "react";
import { createRoot } from "react-dom/client";
import APP from "./App";
import { AppConfig, CMD } from "@hc/engine";
import commands from "./commands/commands";
import { ErrorMonitoring, LOCAL_URL_REG } from "./utils/errorMonitoring";
import OIDC from "~/utils/oidc/oidcLogin";
import checkVerUpdate from "./utils/checkVerUpdate";

console.info(`当前版本:${Build_VERSION}`);
window["EDITOR_VERSION"] = Build_VERSION;

AppConfig.assetURL = ASSET_URL;
AppConfig.Texture_URL = "https://am-img.gkiiot.com/editor/";
AppConfig.SkyBox_URL = "https://am-img.gkiiot.com/editor/";

checkVerUpdate();

window.addEventListener(
  "mousewheel",
  function (event: MouseEvent) {
    if (event.ctrlKey === true || event.metaKey) {
      event.preventDefault();
    }
  },
  { passive: false }
);

//暂时关闭解密
// eslint-disable-next-line no-constant-condition
if (false && "serviceWorker" in navigator) {
  navigator.serviceWorker
    .register("/sw.js")
    .then(function (reg) {
      // registration worked
      console.log("Registration succeeded. Scope is " + reg.scope);
    })
    .catch(function (error) {
      // registration failed
      console.log("Registration failed with " + error);
    });
}

new ErrorMonitoring();

//注册命令
CMD.registerCommand(commands);

export const root = createRoot(document.getElementById("app"));
OIDC.init(() => {
  root.render(<APP />);
});
