import { TextField } from "@mui/material";
import React from "react";
import { HStack } from "./stack";

interface RangeProps {
  range: { min: any; max: any };
  onChange: (k: string, v: string, name?: string) => void;
  name?: string;
}

const inputProps = {
  sx: {
    height: 24,
    borderRadius: 1,
    background: "#F4F4F4",
    border: 0,
    width: 80,
  },
};

const Range: React.FC<RangeProps> = ({ range, onChange, name }) => {
  return (
    <HStack>
      <TextField
        size="small"
        value={range?.min}
        InputProps={inputProps}
        onChange={(evt) => onChange("min", evt.target.value, name)}
      />
      ~
      <TextField
        size="small"
        value={range?.max}
        InputProps={inputProps}
        onChange={(evt) => onChange("max", evt.target.value, name)}
      />
    </HStack>
  );
};
export default Range;
