import { Box, Stack, MenuItem } from "@mui/material";
import React, { useEffect, useState } from "react";
import { DirSourceType, FileApi } from "~/apis";
import {
  createFileDir,
  deleteFile,
  editorFile,
  getFileInfo,
  getFileList,
} from "~/apis/fileReq";

import DragCard from "../common/dragCard";
import DataList from "../dataList";
import Preview from "./preview";
import { useStores } from "~/contexts/store-context";
import { IDataItemProps } from "../dataList/types";
import useContextMenu from "~/hooks/useContextMenu";
import { message } from "~/utils/message";
import { getFileUrl } from "~/utils/url";
import { IFileReturnData } from "~/apis/types";
import CollapseContainer from "../common/collapseContainer";
import { deleteReq, IResponseResult, ResponseStatus } from "~/utils/axios";
import SpaceAddHint from "../common/spaceAddHint";

interface EffectProps {}

const EffectPanel: React.FC<EffectProps> = () => {
  const { rootStore } = useStores();
  const [open, setOpen] = useState(false);
  const [openMenu, closeMenu, MenuContainer] = useContextMenu();
  const [currentData, setCurrentData] = useState<IDataItemProps>(null);
  const [loading, setLoading] = useState(false);
  const [dirlist, setDirList] = useState<IFileReturnData[]>([]);
  const [selectDirId, setSelectDirId] = useState<number>(null);
  const [collkeys, setCollkeys] = useState<Set<number>>(new Set());
  const [query, setQuery] = useState("");

  const handleOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
    setCurrentData(null);
  };

  const handleDbClick = (data: IDataItemProps) => {
    setCurrentData(data);
    handleOpen();
  };

  const getClickFile = (el: HTMLElement) => {
    let dataId = el.getAttribute("data-id");

    if (dataId) {
      return Number(dataId);
    }

    while (el && !el.hasAttribute("data-id")) {
      el = el.parentElement;
      if (!el || el.id === "leftPanel") return null;
    }

    dataId = el.getAttribute("data-id");

    if (dataId) {
      return Number(dataId);
    }
  };

  const handleUpdate = async () => {
    setLoading(true);
    let list = await getFileList(DirSourceType.Particles, 0);
    if (list) {
      list = list.filter((d) => d.node_type === "dir");
    }
    list && updateNodeList(Array.from(collkeys), list);
    setDirList(list);
    setLoading(false);
  };

  const updateNodeList = async (
    key: number | number[],
    list: IFileReturnData[] = dirlist
  ) => {
    if (!Array.isArray(key)) {
      key = [key];
    }

    const rootDirs: IFileReturnData[] = [];
    const childrenDirs: IFileReturnData[] = [];
    for (const d of list) {
      if (d.pid === 0) {
        rootDirs.push(d);
      } else {
        childrenDirs.push(d);
      }
    }

    if (!query) {
      const kMap = new Map<number, IFileReturnData[]>();
      for (const k of key) {
        let list = await getFileList(DirSourceType.Particles, k, query);
        if (!list) list = [];
        kMap.set(
          k,
          list.filter((l) => l.pid !== 0)
        );
      }

      for (const d of list) {
        if (kMap.has(d.id)) {
          const children: IDataItemProps[] = [];
          for (const c of kMap.get(d.id)) {
            const info = await getFileInfo(c.id);
            const json = info.json_data;
            const jsonData = JSON.parse(json);
            children.push({
              title: c.name,
              data: {
                key: "particle",
                json: jsonData,
              },
              preview: getFileUrl(c, true, true),
              id: c.id,
            });
          }
          d.children = children;
        }
      }

      setDirList([...list]);
    } else {
      for (const node of childrenDirs) {
        const pd = rootDirs.find((r) => r.id === node.pid);
        if (pd) {
          if (!pd.children) pd.children = [];
          const info = await getFileInfo(node.id);
          const json = info.json_data;
          const jsonData = JSON.parse(json);
          pd.children.push({
            title: node.name,
            data: {
              key: "particle",
              json: jsonData,
            },
            preview: getFileUrl(node, true, true),
            id: node.id,
          });
        }
      }
      setDirList(rootDirs);
    }
  };

  const handleDelete = async () => {
    const status = await deleteFile(currentData.id);

    if (status) {
      message.success("删除特效成功");
      setCurrentData(null);
      handleUpdate();
    }
    closeMenu();
  };

  const handleAddDir = () => {
    closeMenu();
    setDirList((list) => {
      const newData = {
        name: "新建目录",
        node_type: "dir",
        id: -1,
        editor: true,
      };
      return [...list, newData];
    });
  };

  const handleRenameNode = () => {
    closeMenu();
    if (!selectDirId) return;
    const node = dirlist.find((d) => d.id === selectDirId);
    if (node) {
      node.editor = true;
    }
    setDirList([...dirlist]);
  };

  const handleDeleteNode = async () => {
    closeMenu();
    if (!selectDirId) return;
    const result = await deleteReq(FileApi.delete, {
      data: { id: selectDirId },
    });

    if (result.code === ResponseStatus.Ok) {
      setSelectDirId(0);
      handleUpdate();
    }
  };

  const handleDir = async (data: IFileReturnData) => {
    let result: IResponseResult<any>;

    if (data.id === -1) {
      result = await createFileDir({
        name: data.name,
        sourceType: DirSourceType.Particles,
      });
    } else {
      result = await editorFile(data);
    }

    if (result.code === ResponseStatus.Ok) {
      handleUpdate();
    }
  };

  const handleExpand = async (key: number, evt: React.MouseEvent) => {
    evt.stopPropagation();
    if (evt.button === 0) {
      if (collkeys.has(key)) {
        collkeys.delete(key);
      } else {
        collkeys.add(key);
        handleUpdate();
      }
      setCollkeys(new Set(collkeys));
    }

    setSelectDirId(key);
  };

  const handleMouseDown = (a: IDataItemProps, evt: React.MouseEvent) => {
    evt.stopPropagation();
    setCurrentData(a);
  };
  const clear = () => {
    setCurrentData(null);
  };

  useEffect(() => {
    handleUpdate();
  }, []);

  return (
    <Stack sx={{ width: "100%" }}>
      {/* <Box>
        <DataList
          component={DragCard}
          list={particles}
          onAddData={handleOpen}
          onContextMenu={openMenu}
          onDoubleClick={(f) => handleDbClick(f)}
          componentProps={(f) => ({
            "data-id": f.id,
            elevation: f.id === currentData?.id ? 5 : 1,
          })}
          loading={loading}
          canPreview
        />
      </Box> */}
      {dirlist.length === 0 && (
        <SpaceAddHint onClick={handleAddDir} title="新建目录开始创建" />
      )}
      <Box
        sx={{ height: "100%", overflow: "auto" }}
        onContextMenu={openMenu}
        onMouseDown={clear}
      >
        {dirlist.map(
          (dir) =>
            dir.node_type === "dir" && (
              <CollapseContainer
                title={dir.name}
                open={collkeys.has(dir.id)}
                onExpand={handleExpand}
                target={dir.id}
                key={dir.id}
                editor={dir.editor}
                onBlur={(evt) => {
                  if (evt.target.value) dir.name = evt.target.value;
                  handleDir(dir);
                }}
                select={dir.id === selectDirId}
              >
                <DataList
                  component={DragCard}
                  onContextMenu={openMenu}
                  list={dir.children ?? []}
                  componentProps={(f) => ({
                    "data-id": f.id,
                  })}
                  canPreview
                  onMouseDown={handleMouseDown}
                  onAddData={handleOpen}
                  onDoubleClick={(f) => handleDbClick(f)}
                />
              </CollapseContainer>
            )
        )}
      </Box>
      {open && selectDirId && (
        <Preview
          open={open}
          current={currentData}
          onClose={handleClose}
          onUpdate={handleUpdate}
          projectId={rootStore.projectId}
          dirId={selectDirId}
        />
      )}
      <MenuContainer>
        {!currentData && (
          <MenuItem key="new" onClick={handleAddDir}>
            新建目录
          </MenuItem>
        )}
        {!!selectDirId && !currentData && (
          <MenuItem key="rename" onClick={handleRenameNode}>
            重命名
          </MenuItem>
        )}
        {!!selectDirId && !currentData && (
          <MenuItem key="delete-dir" onClick={handleDeleteNode}>
            删除目录
          </MenuItem>
        )}
        {currentData && <MenuItem onClick={handleDelete}>删除</MenuItem>}
      </MenuContainer>
    </Stack>
  );
};
export default EffectPanel;
