import { DialogContent, DialogTitle, TextField } from "@mui/material";
import Dialog from "@mui/material/Dialog";
import React, { useState } from "react";
import { fileStore } from "~/stores/fileStore";

interface ShareProps {
  onClose: () => void;
  open: boolean;
}

const CodeInput: React.FC<ShareProps> = ({ onClose, open }) => {
  const [secret, setSecret] = useState("");

  const onEnter = () => {
    switch (secret.toUpperCase()) {
      case "EXPORT":
        fileStore.exportFile();
        break;
      case "IMPORT":
        fileStore.importFile();
        break;
      case "DEBUGSCRIPT":
        localStorage.setItem("DEBUGSCRIPT", "1");
        location.reload();
        break;
      case "DEBUGBBL":
        localStorage.setItem("DEBUGBBL", "1");
        location.reload();
        break;
    }
  };
  const handleKeyDown = (e: React.KeyboardEvent) => {
    if (e.code === "Enter") {
      onEnter();
      onClose();
    }
  };

  return (
    <Dialog open onClose={onClose}>
      <DialogTitle>命令框</DialogTitle>
      <DialogContent>
        <TextField
          value={secret}
          onChange={(evt) => setSecret(evt.target.value)}
          onKeyDown={handleKeyDown}
        />
      </DialogContent>
    </Dialog>
  );
};
export default CodeInput;
