import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
} from "@mui/material";
import React from "react";
import DialogHeader from "./dialogHeader";

interface ConfirmModalProps {
  open: boolean;
  onOk: () => void;
  onCancel: () => void;
  title: string;
  content: React.ReactNode;
}

const ConfirmModal: React.FC<ConfirmModalProps> = ({
  open,
  onOk,
  onCancel,
  title,
  content,
}) => {
  return (
    <Dialog
      sx={{ "& .MuiDialog-paper": { width: "80%", maxHeight: 435 } }}
      maxWidth="xs"
      open={open}
      keepMounted
    >
      <DialogHeader title={title} onClose={onCancel} />
      <DialogContent dividers>
        {typeof content === "string" ? (
          <DialogContentText id="confirm-dialog-description">
            {content}
          </DialogContentText>
        ) : (
          content
        )}
      </DialogContent>
      <DialogActions sx={{ justifyContent: "space-around" }}>
        <Button
          autoFocus
          onClick={onOk}
          variant="contained"
          sx={{ width: "30%" }}
        >
          确认
        </Button>
        <Button
          sx={{ width: "30%" }}
          color="inherit"
          variant="contained"
          onClick={onCancel}
        >
          取消
        </Button>
      </DialogActions>
    </Dialog>
  );
};
export default ConfirmModal;
