import { Popover, PopoverProps } from "@mui/material";
import React from "react";

interface BlurPopoverProps {
  children: React.ReactNode;
  onClose: () => void;
}

const BlurPopover: React.FC<BlurPopoverProps & PopoverProps> = ({
  children,
  onClose,
  ...rest
}) => {
  const handleBlur = (evt: React.FocusEvent) => {
    //@ts-ignore
    let el = evt.relatedTarget as HTMLElement;
    const rootEl = evt.currentTarget as HTMLElement;
    let isBlur = false;
    do {
      if (el === rootEl) {
        isBlur = true;
        break;
      }
      el = el?.parentElement;
    } while (el);

    if (!isBlur) {
      onClose();
    }
  };

  return (
    <Popover
      onBlur={handleBlur}
      sx={{
        pointerEvents: "none",
        "& .MuiPaper-root": { zIndex: 9999, pointerEvents: "auto" },
      }}
      hideBackdrop
      onClose={onClose}
      {...rest}
    >
      {children}
    </Popover>
  );
};
export default BlurPopover;
