import React, { useEffect } from "react";

import {
  AppBar,
  Box,
  Container,
  Toolbar,
  Typography,
  Menu,
  MenuItem,
  Button,
  Link,
} from "@mui/material";
import { styled } from "@mui/material/styles";

import { userStore } from "~/stores/UserStore";
import { menuList } from "~/config/header";
import SIcon from "~/components/SIcon";
import { getReq, ResponseStatus } from "~/utils/axios";
import { confirm } from "~/utils/confirm";

const SelfToolbar = styled(Toolbar)(({ theme }) => ({
  display: "flex",
  justifyContent: "space-between",
  alignItems: "center",
  width: "100%",
}));

const TopMenu = styled("div")(({ theme }) => ({
  display: "flex",
  justifyContent: "space-around",
  alignItems: "center",
  "& .TopMenuItem": {
    padding: "10px 20px",
    "& a": {
      color: "#fff",
      textDecoration: "none",
    },
  },
}));

const SlefMenuItem = styled(MenuItem)(({ theme }) => ({
  fontSize: "13px",
  width: "300px",
  "&:hover": {
    backgroundColor: "rgba(25, 118, 210, 0.2)",
  },
}));

const UserPane = styled(Box)(({ theme }) => ({
  display: "flex",
  justifyContent: "space-between",
  alignItems: "center",
  cursor: "pointer",
  "& .vip-img": {
    width: "18px",
    height: "18px",
    borderRadius: "50%",
    position: "absolute",
    bottom: 0,
    right: 0,
  },
}));

const Avatar = styled("img")(({ theme }) => ({
  width: "36px",
  height: "36px",
  borderRadius: "50%",
  marginRight: "5px",
}));

const UserInfo = styled(Box)(({ theme }) => ({
  display: "flex",
  justifyContent: "space-between",
  alignItems: "center",
  fontSize: "12px",
  fontFamily: "Source Han Sans CN",
  color: "#424B54",
  marginBottom: "10px",
  width: "100%",
  padding: "6px 16px",
  userSelect: "none",
  "& .avatar": {
    display: "flex",
    "& p": {
      margin: 0,
    },
    "& .user-id": {
      color: "#999",
    },
  },
  "& .score": {
    color: "#FF6600",
  },
}));

type UserProps = {
  account_num?: string;
  balance?: number;
  user_code?: string;
};

function Header(props) {
  const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);
  const [userInfo, setUserInfo] = React.useState<UserProps>({});
  const open = Boolean(anchorEl);
  const handleClick = (event: React.MouseEvent<HTMLDivElement>) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };

  const logout = () => {
    confirm({
      title: `提示`,
      content: "确定退出登录吗？",
      onOk: () => {
        logoutMethod();
      },
    });
  };

  const logoutMethod = () => {
    handleClose();
    userStore.logOut();
  };

  const goToPage = (url: string, type?: string | "_self") => {
    window.open(url, type);
  };

  const getUserScore = async () => {
    const res = await getReq("trade/account");
    if (res.code === ResponseStatus.Ok) {
      setUserInfo(res.data);
    }
  };

  useEffect(() => {
    getUserScore();
  }, []);

  return (
    <AppBar sx={{ background: "#0066cc", position: "unset" }}>
      <Container maxWidth={false} sx={{ minHeight: "30px" }}>
        <SelfToolbar disableGutters variant="dense">
          <Typography
            variant="h6"
            noWrap
            component="div"
            sx={{
              mr: 2,
              color: "#fff",
              display: {
                xs: "none",
                md: "flex",
              },
              fontSize: "16px",
              fontFamily: "Source Han Sans CN",
              fontWeight: "bold",
              alignItems: "center",
              justifyContent: "center",
            }}
          >
            <img
              style={{ marginRight: "10px" }}
              src="/static/images/logo-white.png"
            />
          </Typography>
          <TopMenu>
            {menuList &&
              menuList.map((item, index) => (
                <span key={index} className="TopMenuItem">
                  <Link href={item.href}>{item.label}</Link>
                </span>
              ))}
          </TopMenu>
          {props.children ? (
            <Box sx={{ flex: 1, display: "flex", justifyContent: "flex-end" }}>
              {props.children}
            </Box>
          ) : userStore.UserName ? (
            <>
              {/* <Button
                sx={{ color: "#fff", textTransform: "none" }}
                variant="text"
                onClick={handleClick}
              >
                {userStore.UserName}
              </Button> */}
              <Box
                onMouseEnter={handleClick}
                onMouseLeave={handleClose}
                // onClick={handleClick}
              >
                <UserPane>
                  <Box sx={{ marginRight: "5px", position: "relative" }}>
                    <Avatar src="/static/images/avatar.jpeg" />
                    <img className="vip-img" src="/static/images/member.png" />
                  </Box>
                  <SIcon
                    type="icon-3Dxiajiantou"
                    sx={{
                      color: "#fff",
                      fontSize: "12px",
                      fontWeight: "bold",
                    }}
                  ></SIcon>
                </UserPane>
                <Menu
                  id="basic-menu"
                  anchorEl={anchorEl}
                  open={open}
                  onClose={handleClose}
                  sx={{ "& a": { color: "#333", textDecoration: "none" } }}
                >
                  {/* <SlefMenuItem> */}
                  <UserInfo>
                    <Box className="avatar">
                      <Avatar src="/static/images/avatar.jpeg" />
                      <Box>
                        <p>{userStore.UserName}</p>
                        <p className="user-id">ID: {userInfo.account_num}</p>
                      </Box>
                    </Box>
                    <div className="score">积分：{userInfo.balance}</div>
                  </UserInfo>
                  {/* </SlefMenuItem> */}
                  <SlefMenuItem
                    onClick={() => goToPage(MH_URL + "personal-center.html")}
                  >
                    <SIcon type="icon-3Dkehufenxi"></SIcon>个人主页
                  </SlefMenuItem>
                  <SlefMenuItem onClick={logout}>
                    <SIcon type="icon-3Dtuichu1"></SIcon>退出登录
                  </SlefMenuItem>
                </Menu>
              </Box>
            </>
          ) : null}
        </SelfToolbar>
      </Container>
    </AppBar>
  );
}

export default Header;
