import { CMD } from "@hc/engine";
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  TextField,
  Typography,
} from "@mui/material";
import React, { useState } from "react";
import { App } from "~/intance";
import { message } from "~/utils/message";

import DialogHeader from "../common/dialogHeader";
import { HStack } from "../common/stack";

interface MarkProps {
  onClose: () => void;
  open: boolean;
}

const ArrayPanel: React.FC<MarkProps> = ({ onClose, open }) => {
  const [object, setObject] = useState({
    cx: "",
    cy: "",
    cz: "",
    sx: "",
    sy: "",
    sz: "",
  });

  const change = (evt) => {
    setObject({ ...object, [evt.target.name]: evt.target.value });
  };

  const handleClose = () => {
    onClose();
  };

  const arrayEns = (cx: number, sx: number, dir: string) => {
    const entitys = App.intance.SelectEntitys;
    for (let i = 0; i < cx; i++) {
      for (const en of entitys) {
        const ec = en.clone();
        const pos = ec.Position;
        pos[dir] += sx * (i + 1);
        ec.Position = pos;
        App.intance.Database.ModelSpace.append(ec);
      }
    }
  };

  const handleConfirm = () => {
    const cx = parseFloat(object.cx);
    const cy = parseFloat(object.cy);
    const cz = parseFloat(object.cz);
    const sx = parseFloat(object.sx);
    const sy = parseFloat(object.sy);
    const sz = parseFloat(object.sz);
    const entitys = App.intance.SelectEntitys;
    if (entitys.length > 0) {
      CMD.CommandWrap(() => {
        console.log("cx: ", cx);
        if (cx && sx) {
          arrayEns(cx, sx, "x");
        }
        if (cy && sy) {
          arrayEns(cy, sy, "y");
        }
        if (cz && sz) {
          arrayEns(cz, sz, "z");
        }
      }, "阵列");
    } else {
      message.warn("请先选择要阵列的实体！");
    }

    handleClose();
  };

  return (
    <Dialog open={open} maxWidth="md" PaperProps={{ sx: { height: "300px" } }}>
      <DialogHeader onClose={handleClose} title="阵列" />
      <DialogContent>
        <Typography>数量</Typography>
        <HStack>
          <TextField label="x" name="cx" value={object.cx} onChange={change} />
          <TextField label="y" name="cy" value={object.cy} onChange={change} />
          <TextField label="z" name="cz" value={object.cz} onChange={change} />
        </HStack>
        <Typography>间距</Typography>
        <HStack>
          <TextField label="x" name="sx" value={object.sx} onChange={change} />
          <TextField label="y" name="sy" value={object.sy} onChange={change} />
          <TextField label="z" name="sz" value={object.sz} onChange={change} />
        </HStack>
      </DialogContent>
      <DialogActions>
        <HStack>
          <Button
            onClick={handleConfirm}
            autoFocus
            variant="contained"
            sx={{ mr: 2 }}
          >
            确定
          </Button>
          <Button
            autoFocus
            onClick={handleClose}
            variant="contained"
            color="error"
          >
            取消
          </Button>
        </HStack>
      </DialogActions>
    </Dialog>
  );
};
export default ArrayPanel;
