import { userStore } from "~/stores/UserStore";
import { fileStore } from "./../stores/fileStore";
import { getRenderInfo } from "./device";

//正在关闭页面,此时不需要报告请求错误
let unloading = false;

window.addEventListener("unload", () => {
  unloading = true;
});

export class ErrorMonitoring {
  constructor() {
    window.addEventListener("error", (e) => {
      console.dir("e: ", e);
      ReportError(e.error?.stack ?? e);
    });
    window.addEventListener("unhandledrejection", (e) => {
      console.dir("e: ", e);
      ReportError(e.reason?.stack ?? e);
    });
  }
}

let stackSet = new Set();

export const LOCAL_URL_REG = /^[\d.:(localhost)]+$/;

const rednerInfo = getRenderInfo();

export async function ReportError(stack: any, msg = "未捕获的错误!") {
  try {
    if (unloading) return;

    if (LOCAL_URL_REG.test(window.location.hostname)) {
      console.log(stack);
      return;
    }

    if (stackSet.has(stack)) return;
    stackSet.add(stack);

    let lastFileKey = fileStore.currentFileId;
    let fileName = fileStore.currentFileName;

    await fetch("https://cicd3d.gkiiot.com/report", {
      method: "POST",
      mode: "cors",
      body: JSON.stringify({
        type: "error",
        stack,
        href: window.location.href,
        path: window.location.pathname,
        file: lastFileKey,
        msg: msg,
        version: Build_VERSION,
        fileName,
        userAgent: navigator.userAgent,
        user: userStore.UserName,
        renderInfo: rednerInfo,
      }),
      headers: {
        "content-type": "application/json",
        origin: location.origin,
      },
    });

    console.log("上报成功");
  } catch (error) {
    console.log("上报失败", error);
  }
}
