import { createContext, useContext, Dispatch } from "react";
import type { Entity, BaseGui, BaseDom } from "@hc/engine";

export type IEditorState = {
  type?: string;
  entity: Entity;
  gui?: BaseGui;
  dom?: BaseDom;
  position?: { x: string; y: string; z?: string };
  rotation?: { x: string; y?: string; z?: string };
  scale?: { x: string; y: string; z?: string };
  name?: string;
  visiable?: boolean;
  pickable?: boolean;
  enableShadow?: boolean;
  render2DType: "dom" | "gui" | "texture";
  other?: Record<string, any>;
};

export const EditorContext = createContext<{
  editorState: IEditorState;
  dispatch: Dispatch<{ type: string } & Partial<IEditorState>>;
}>(null);

export const InitEditorValue: IEditorState = {
  entity: null,
  gui: null,
  dom: null,
  position: { x: "", y: "", z: "" },
  rotation: { x: "", y: "", z: "" },
  scale: { x: "", y: "", z: "" },
  name: "",
  visiable: true,
  pickable: true,
  enableShadow: true,
  type: "",
  render2DType: "dom",
};

export const useEditorContext = () => {
  return useContext(EditorContext);
};
