import { Box, Typography } from "@mui/material";
import React from "react";
import { MarkList } from "~/apis/types";
import { IMG_CDN } from "~/utils/url";

interface BuyItemPanelProps {
  shop: MarkList;
}

const BuyItemPanel: React.FC<BuyItemPanelProps> = ({ shop }) => {
  return (
    <Box textAlign="center">
      <Typography sx={{ color: "#222", mb: 2 }} variant="h6">
        当前素材: {shop.product_name}
      </Typography>
      <Box
        sx={{
          backgroundImage: `url(${IMG_CDN}coin.svg)`,
          backgroundSize: "50%",
          backgroundRepeat: "no-repeat",
          backgroundPositionX: "center",
          display: "flex",
          alignItems: "center",
          flexDirection: "column",
          justifyContent: "center",
        }}
        height="150px"
      >
        <Typography sx={{ color: "#744400" }} variant="h4">
          {shop.score}
        </Typography>
        <Typography sx={{ color: "#744400" }} variant="h6">
          本次消耗积分
        </Typography>
      </Box>
    </Box>
  );
};
export default BuyItemPanel;
