import { Card, Typography } from "@mui/material";
import React from "react";
import AddCardIcon from "@mui/icons-material/AddCard";

interface SpaceAddHintProps {
  onClick: () => void;
  title: string;
}

const SpaceAddHint: React.FC<SpaceAddHintProps> = ({ onClick, title }) => {
  return (
    <Card
      sx={{
        textAlign: "center",
        pt: 1,
        border: "2px dashed #ccc",
        p: 2,
        m: 1,
      }}
      onClick={onClick}
    >
      <AddCardIcon sx={{ fontSize: 60 }} />
      <Typography variant="subtitle2">{title}</Typography>
    </Card>
  );
};
export default SpaceAddHint;
