import { Box, Stack, TextField } from "@mui/material";
import { observer } from "mobx-react-lite";
import React from "react";
import CommonSelect from "~/components/common/select";
import { useInteractionStoresContext } from "~/contexts/interactionStoreContext";
import { useStores } from "~/contexts/store-context";

interface OpenDetailProps {}

const OpenDetail: React.FC<OpenDetailProps> = () => {
  const store = useInteractionStoresContext();
  const { fileStore, rootStore } = useStores();
  const detail = store.CurrentActionDetail;

  const hanldeChange = (evt) => {
    store.updateState(() => {
      detail[evt.target.name] = evt.target.value;
    });
  };

  const SelectTarget = () => (
    <CommonSelect
      options={[
        { label: "新窗口", value: "_blank" },
        { label: "当前窗口", value: "_self" },
      ]}
      label="打开在"
      name="target"
      value={detail.target ?? ""}
      onChange={hanldeChange}
    />
  );

  const renderForm = () => {
    switch (detail.type) {
      case "url":
        return (
          <>
            <TextField
              label="输入要打开的地址"
              name="value"
              value={detail.value}
              onChange={hanldeChange}
            />
            <SelectTarget />
          </>
        );
      case "file":
        return (
          <>
            <CommonSelect
              options={fileStore.fileList.map((f) => ({
                label: f.title,
                value: f.id,
              }))}
              label="选择页面"
              value={detail.value}
              name="value"
              onChange={hanldeChange}
            />
            <SelectTarget />
          </>
        );
      case "reload":
      case "back":
        return null;
    }
    return null;
  };

  return (
    <Stack spacing={2}>
      <CommonSelect
        options={[
          { label: "链接到url", value: "url" },
          { label: "链接到场景", value: "file" },
          { label: "重新加载", value: "reload" },
          { label: "返回上一页", value: "back" },
        ]}
        value={detail.type}
        name="type"
        onChange={hanldeChange}
      />
      {renderForm()}
    </Stack>
  );
};
export default observer(OpenDetail);
