import {
  Box,
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  IconButton,
  Paper,
} from "@mui/material";
import DeleteOutline from "@mui/icons-material/DeleteOutline";
import React, { useState } from "react";
import DialogHeader from "../common/dialogHeader";
import { HStack } from "../common/stack";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import EditorCell from "../editorModal/editorCell";
import { CommonSelectEntity } from "../common/commonInteraction";
import { CMD, ObjectId, StackPanelGui } from "@hc/engine";
import { App } from "~/intance";

interface MarkProps {
  onClose: () => void;
  open: boolean;
}

let idIndex = 0;

const MarkPanel: React.FC<MarkProps> = ({ onClose, open }) => {
  const [props, setProps] = useState<
    {
      id?: number;
      label: string;
      key: string;
      value: string;
      unit: string;
    }[]
  >([]);
  const [object, setObject] = useState<ObjectId<any> | null>(null);

  const handleAdd = () => {
    const id = idIndex++;
    setProps([
      ...props,
      {
        id: id,
        label: "未命名",
        key: "key" + id,
        value: "",
        unit: "",
      },
    ]);
  };

  const handleDelete = (index) => {
    setProps((ls) => {
      ls.splice(index, 1);
      return [...ls];
    });
  };

  const handleClose = () => {
    onClose();
  };

  const handleConfirm = () => {
    if (props.length === 0) {
      handleClose();
      return;
    }
    CMD.CommandWrap(() => {
      const gui = new StackPanelGui();
      gui.DataList = props;
      if (object) {
        gui.attachEntity(object);
      }
      App.intance.Database.GuiSpace.append(gui);
    }, "创建标注");

    handleClose();
  };

  const handleChange = (evt, prop) => {
    prop[evt.target.name] = evt.target.value;
    setProps([...props]);
  };

  return (
    <Dialog open={open} maxWidth="md" PaperProps={{ sx: { height: "522px" } }}>
      <DialogHeader onClose={handleClose} title="添加标注" />
      <DialogContent>
        <HStack>
          <Button onClick={handleAdd}>添加</Button>
        </HStack>
        <TableContainer component={Paper} sx={{ height: "calc(100% - 40px)" }}>
          <Table sx={{ minWidth: 650 }} aria-label="simple table" stickyHeader>
            <TableHead>
              <TableRow>
                <TableCell width="22.5%" align="center">
                  属性名称
                </TableCell>
                <TableCell width="22.5%" align="center">
                  属性键
                </TableCell>
                <TableCell width="22.5%" align="center">
                  值
                </TableCell>
                <TableCell width="22.5%" align="center">
                  单位
                </TableCell>
                <TableCell width="10%" align="center">
                  操作
                </TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {props.map((vb, index) => (
                <TableRow key={vb.id}>
                  <EditorCell
                    size="small"
                    align="center"
                    name="label"
                    value={vb.label}
                    onChange={(evt) => {
                      handleChange(evt, vb);
                    }}
                  />
                  <EditorCell
                    size="small"
                    align="center"
                    value={vb.key}
                    name="key"
                    onChange={(evt) => {
                      handleChange(evt, vb);
                    }}
                  />
                  <EditorCell
                    size="small"
                    align="center"
                    name="value"
                    value={
                      typeof vb.value === "boolean"
                        ? String(vb.value)
                        : vb.value
                    }
                    onChange={(evt) => {
                      handleChange(evt, vb);
                    }}
                  />
                  <EditorCell
                    size="small"
                    align="center"
                    name="unit"
                    value={vb.unit}
                    onChange={(evt) => {
                      handleChange(evt, vb);
                    }}
                  />
                  <TableCell size="small" align="center">
                    <IconButton
                      onClick={() => {
                        handleDelete(index);
                      }}
                    >
                      <DeleteOutline />
                    </IconButton>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </DialogContent>
      <DialogActions sx={{ justifyContent: "space-between" }}>
        <CommonSelectEntity
          value={object}
          label="绑定到实体"
          onChange={(evt, opt) => {
            setObject(App.intance.Database.getObjectId(opt.value));
          }}
        />
        <HStack>
          <Button
            onClick={handleConfirm}
            autoFocus
            variant="contained"
            sx={{ mr: 2 }}
          >
            确定
          </Button>
          <Button
            autoFocus
            onClick={handleClose}
            variant="contained"
            color="error"
          >
            取消
          </Button>
        </HStack>
      </DialogActions>
    </Dialog>
  );
};
export default MarkPanel;
