import React, { useState } from "react";
import { TextField, Box, SxProps } from "@mui/material";
import { Color, SketchPicker } from "react-color";
import Masking from "./masking";

interface ColorPickInputProps {
  color: string | { r: number; g: number; b: number; a: number };
  onChange: (c, evt?) => void;
  InputProps?: any;
  value?: string;
  sx?: SxProps;
}

const SketchPicker2 = SketchPicker as any;

const ColorPickInput: React.FC<ColorPickInputProps> = ({
  color,
  onChange,
  InputProps,
  value,
  sx = {},
}) => {
  const [open, setOpen] = useState(false);

  const handleFocus = () => {
    setOpen(true);
  };
  const handleBlur = () => {
    setOpen(false);
  };

  let background = value;

  if (background?.length !== 7) {
    if (typeof color === "object") {
      background = `rgba(${color.r},${color.g},${color.b},${color.a})`;
    } else background = color;
  }

  return (
    <Box sx={{ position: "relative", width: 165, ...sx }}>
      <TextField
        size="small"
        onFocus={handleFocus}
        value={value || color}
        sx={{ background: background, borderRadius: 1 }}
        onChange={(e) => onChange({ hex: e.target.value })}
        InputProps={{
          sx: {
            height: 24,
            borderRadius: 1,
            border: 0,
          },
        }}
      />
      {open && (
        <>
          <Box sx={{ position: "absolute", right: 0, zIndex: 10 }}>
            <>
              <SketchPicker2
                color={color}
                onChange={onChange}
                presetColors={[
                  "#33334C",
                  "#D0021B",
                  "#F5A623",
                  "#F8E71C",
                  "#8B572A",
                  "#7ED321",
                  "#417505",
                  "#BD10E0",
                  "#9013FE",
                  "#4A90E2",
                  "#50E3C2",
                  "#B8E986",
                  "#000000",
                  "#4A4A4A",
                  "#9B9B9B",
                  "#FFFFFF",
                ]}
                disableAlpha={false}
                onBlur={handleBlur}
              />
            </>
          </Box>
          <Masking onClick={handleBlur} zIndex={9} />
        </>
      )}
    </Box>
  );
};
export default ColorPickInput;
