import { BaseDom, BaseGui, CMD, Entity, HCFiler, IVector3 } from "@hc/engine";
import { App } from "~/intance";
import { getCopyText } from "~/utils/utils";

export function subVec(v1: IVector3, v2: IVector3) {
  return { x: v2.x - v1.x, y: v2.y - v1.y, z: v2.z - v1.z };
}

export class PasteCommand implements CMD.Command {
  exec() {
    getCopyText().then((res) => {
      if (res) {
        try {
          const { data, pt } = JSON.parse(res);
          // const nowPt = App.intance.Editor.mouseCtrl.WorldPoint;
          // const moveVec = subVec(pt, nowPt);
          const filer = new HCFiler(data);
          const count = filer.read();
          const list: Entity[] = [];
          const domlist: BaseDom[] = [];
          const guilist: BaseGui[] = [];
          for (let i = 0; i < count; i++) {
            const en = filer.readObject();
            en.ObjectId = null;
            if (en instanceof Entity) {
              // en.translate(moveVec);
              list.push(en);
              App.intance.Database.ModelSpace.append(en);
            } else if (en instanceof BaseGui) {
              guilist.push(en);
              App.intance.Database.GuiSpace.append(en);
            } else if (en instanceof BaseDom) {
              domlist.push(en);
              App.intance.Database.DomSpace.append(en);
            }
          }
          if (list.length > 0)
            App.intance.Editor.selectControl.SelectEntitys = list;
          else if (domlist.length > 0) {
            App.intance.Editor.selectControl.SelectDOM = domlist[0];
          } else if (guilist.length > 0) {
            App.intance.Editor.selectControl.SelectGui = guilist[0];
          }
        } catch (err) {
          console.log("err: ", err);
        }
      }
    });
  }
}
