import { Box, InputAdornment, TextField } from "@mui/material";
import { observer } from "mobx-react-lite";
import React from "react";
import { CommonSelectEntity } from "~/components/common/commonInteraction";
import CommonSelect from "~/components/common/select";
import VectorPanel from "~/components/common/vector3Panel";
import { useInteractionStoresContext } from "~/contexts/interactionStoreContext";
import { TypeOptions } from "./options";

interface MoveActionDetailProps {}

const MoveActionDetail: React.FC<MoveActionDetailProps> = observer((props) => {
  const store = useInteractionStoresContext();

  const detail = store.CurrentActionDetail;

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    store.updateState(() => {
      detail[e.target.name] = e.target.value;
    });
  };
  const handleChangeVector = (k: string, v: string) => {
    store.updateState(() => {
      detail.target[k] = v;
    });
  };
  const handleSelect = (evt: React.ChangeEvent<HTMLInputElement>) => {
    store.updateState(() => {
      detail[evt.target.name] = evt.target.value;
    });
  };

  return (
    <Box>
      <CommonSelectEntity value={detail} />
      <CommonSelect
        label="移动"
        options={TypeOptions}
        value={detail.type}
        onChange={handleSelect}
        name="type"
        sx={{ my: 1 }}
      />
      <VectorPanel
        sx={{ flex: 1 }}
        vector={detail.target}
        onChange={handleChangeVector}
      />
      {/* <TextField
        size="small"
        fullWidth
        select
        value="无"
        label="动画"
        sx={{ my: 1 }}
      >
        {["无", "线性"].map((c) => (
          <MenuItem key={c} value={c}>
            {c}
          </MenuItem>
        ))}
      </TextField> */}
      <TextField
        value={detail.time}
        hiddenLabel
        variant="outlined"
        size="small"
        name="time"
        onChange={handleChange}
        InputProps={{
          startAdornment: (
            <InputAdornment position="start">时间</InputAdornment>
          ),
          endAdornment: <InputAdornment position="start">毫秒</InputAdornment>,
        }}
      />
    </Box>
  );
});

export default MoveActionDetail;
