import {
  EmiterShape,
  EmiterType,
  ObjectId,
  ParticleSystem,
  SubEmitter,
  Utils,
  IGradient,
} from "@hc/engine";
import {
  Box,
  Button,
  Checkbox,
  Drawer,
  List,
  ListItem,
  ListItemButton,
  ListItemText,
  SxProps,
  Typography,
} from "@mui/material";
import React, { useEffect, useState } from "react";
import { ColorResult } from "react-color";
import { convert2UiData, uiData2Data, uiVector2Vector } from "~/utils/adapter";
import { debounceCallback } from "~/utils/time";

import CollapseContainer from "../common/collapseContainer";
import ColorPickInput from "../common/colorPickInput";
import Range from "../common/range";
import CommonSelect from "../common/select";
import { HStack } from "../common/stack";
import Title from "../common/title";
import TitleInput from "../common/titleInput";
import VectorPanel from "../common/vector3Panel";
import BuildIcon from "@mui/icons-material/Build";
import GradientItem from "./gradient";
import AddIcon from "@mui/icons-material/Add";

interface ParticleSystemPropertyProps {
  pcs: ParticleSystem;
  sx?: SxProps;
  isSub?: boolean;
}

export type ParticleKey = {
  [P in keyof ParticleSystem]?: any;
};

let isApply = false;
let idIndex = 0;

const ParticleSystemProperty: React.FC<ParticleSystemPropertyProps> = ({
  pcs,
  sx,
  isSub = false,
}) => {
  const [json, setJSON] = useState<ParticleKey>({});
  const [collkeys, setCollkeys] = useState(new Set(["basic"]));
  const [openSub, setOpenSub] = useState(false);
  const [sub, setSub] = useState<SubEmitter>(null);

  const toggleDrawer = () => {
    setOpenSub(!openSub);
  };

  const confirmSubEmiter = () => {
    if (!isApply) {
      applySubEmiter();
    } else {
      isApply = false;
    }
    toggleDrawer();
  };
  const applySubEmiter = () => {
    isApply = true;
    if (
      pcs.SubParticleSystems.every((s) =>
        json.SubParticleSystems.some((s1) => s1 === s)
      )
    ) {
      pcs.start();
    } else pcs.SubParticleSystems = [...json.SubParticleSystems];
  };

  const handleChangeRange = (key: string, value: string, name: string) => {
    const newJson = { ...json, [name]: { ...json[name], [key]: value } };

    setJSON(newJson);
    if (value)
      debounceCallback(() => {
        if (name === "AngularSpeed") {
          pcs[name] = uiData2Data(newJson[name], Utils.deg2rad) as any;
        } else pcs[name] = uiData2Data(newJson[name]);
      });
  };

  const handleChangeVector = (key: string, value: string, name?: string) => {
    setJSON({ ...json, [name]: { ...json[name], [key]: value } });
  };
  const handleApplyVector = (evt: React.FocusEvent<HTMLInputElement>) => {
    const name = evt.target.name;
    pcs[name] = uiVector2Vector(json[name]);
  };
  const handleChange = (evt: React.ChangeEvent<HTMLInputElement>) => {
    const keys = evt.target.name.split(".");
    if (keys.length === 1) {
      setJSON({ ...json, [keys[0]]: evt.target.value });
    } else if (keys.length === 2) {
      const k1 = keys[0];
      const k2 = keys[1];
      setJSON({ ...json, [k1]: { ...json[k1], [k2]: evt.target.value } });
    }
  };
  const handleChangeSelect = (evt: React.ChangeEvent<HTMLInputElement>) => {
    const key = evt.target.name;
    const value = evt.target.value;
    pcs[key] = value;
    if (key === "EmiterShape") {
      setJSON({
        ...json,
        [key]: value,
        Emiter: pcs.Emiter ? pcs.Emiter.toJSON() : {},
      });
    } else {
      setJSON({ ...json, [key]: value });
    }
  };

  const handleChangeColor = (c: ColorResult, name: string) => {
    let value = c.hex;
    if (c.rgb) {
      const color = Utils.Color4.FromArray([
        c.rgb.r / 255,
        c.rgb.g / 255,
        c.rgb.b / 255,
        c.rgb.a,
      ]);
      value = color.toHexString();
    }

    setJSON({ ...json, [name]: value });

    debounceCallback(() => {
      pcs[name] = value;
    });
  };

  const handleBlur = (evt: React.FocusEvent<HTMLInputElement>) => {
    const keys = evt.target.name.split(".");
    const key = keys[0];
    const key2 = keys[1];
    let value = evt.target.value as any;

    let originValue = pcs[key];

    if (key2) {
      originValue = originValue[key2];
    }

    if (key in pcs) {
      if (typeof originValue === "number") {
        if (!value) return;
        value = parseFloat(value) as number;
      }
    }
    if (key2) {
      if (key === "Emiter") {
        pcs.Emiter[key2] = value;
      } else {
        pcs[key] = { ...pcs[key], [key2]: value };
      }
    } else pcs[key] = value;
  };

  const handleChecked = (evt: React.ChangeEvent<HTMLInputElement>) => {
    const keys = evt.target.name.split(".");
    const checked = evt.target.checked;
    if (keys.length === 1) {
      setJSON({ ...json, [keys[0]]: checked });
      pcs[keys[0]] = checked;
    } else if (keys.length === 2) {
      setJSON({ ...json, [keys[0]]: { ...json[keys[0]], [keys[1]]: checked } });
      pcs[keys[0]] = uiData2Data({ ...json[keys[0]], [keys[1]]: checked });
    }
  };

  const handleExpand = (key: string) => {
    if (collkeys.has(key)) {
      collkeys.delete(key);
    } else {
      collkeys.add(key);
    }
    setCollkeys(new Set(collkeys));
  };

  const getRgba = (c: string) => {
    const color = Utils.Color4.FromHexString(c);
    return { r: color.r * 255, g: color.g * 255, b: color.b * 255, a: color.a };
  };

  const handleAddSubEmiter = () => {
    const pcs = new ParticleSystem({
      image: "textures/flare.png",
      capacity: 1e2,
      manualEmitCount: 1e2,
    });
    pcs.Name = "子粒子特效";
    const sub = new SubEmitter(pcs);
    sub.ObjectId = new ObjectId(idIndex++);
    setSub(sub);
    setJSON({ ...json, SubParticleSystems: [...json.SubParticleSystems, sub] });
  };
  const handleDeleteSubEmiter = () => {
    if (!sub) return;
    const index = json.SubParticleSystems.indexOf(sub);
    json.SubParticleSystems.splice(index, 1);
    sub.destory();
    setSub(json.SubParticleSystems[0]);
    setJSON({ ...json, SubParticleSystems: [...json.SubParticleSystems] });
  };

  const handAddGradient = () => {
    const gs = [
      ...json.Gradients,
      {
        key: "Size",
        period: json.Gradients.length === 0 ? 0 : 1,
        value: 1,
        otherValue: 1,
        id: idIndex++,
      },
    ];
    setJSON({
      ...json,
      Gradients: gs,
    });
    pcs.Gradients = handleGradientData(gs);
  };

  const handleGradientData = (gs) => {
    return gs.map((g) => ({
      key: g.key,
      value: Number(g.value) || 0,
      otherValue: Number(g.otherValue) || undefined,
      period: Number(g.period) || 0,
    }));
  };
  const handleChangeGradient = (g: IGradient, index: number) => {
    const gs = [...json.Gradients];
    gs[index] = g;
    setJSON({ ...json, Gradients: gs });
    debounceCallback(() => {
      pcs.Gradients = handleGradientData(gs);
    });
  };

  const handleDeleteGradient = (id: number) => {
    const gs = [...json.Gradients];
    const index = gs.findIndex((g) => g.id === id);
    gs.splice(index, 1);
    setJSON({ ...json, Gradients: gs });
    pcs.removeGradient(index);
  };

  const handleAddColorGradient = () => {
    const gs = [
      ...json.Gradients,
      {
        key: "Color",
        period: json.Gradients.length === 0 ? 0 : 1,
        value: "#FFFFFF",
        otherValue: "#000000",
        id: idIndex++,
      },
    ];
    setJSON({
      ...json,
      Gradients: gs,
    });
    pcs.Gradients = gs;
  };

  const handleChangeColorGradient = (g: IGradient, index: number) => {
    const gs = [...json.Gradients];
    gs[index] = g;
    setJSON({ ...json, Gradients: gs });
    debounceCallback(() => {
      pcs.Gradients = gs;
    });
  };

  const handleDeleteColorGradient = (id: number) => {
    const gs = [...json.Gradients];
    const index = gs.findIndex((g) => g.id === id);
    gs.splice(index, 1);
    setJSON({ ...json, Gradients: gs });
    pcs.removeGradient(index);
  };

  const init = (pcs: ParticleSystem) => {
    if (pcs) {
      setJSON({
        Name: pcs.Name,
        Capacity: pcs.Capacity,
        Rate: pcs.Rate,
        Color1: pcs.Color1,
        Color2: pcs.Color2,
        ColorDead: pcs.ColorDead,
        Image: pcs.Image,
        EmiterPosition: pcs.EmiterPosition,
        Pivot: pcs.Pivot,
        EmiterType: pcs.EmiterType,
        EmiterShape: pcs.EmiterShape,
        Size: pcs.Size,
        Scale: pcs.Scale,
        LifeTime: pcs.LifeTime,
        TargetStopDuration: pcs.TargetStopDuration,
        SpriteOption: pcs.SpriteOption,
        AngularSpeed: convert2UiData(pcs.AngularSpeed, (n) =>
          Utils.rad2Deg(n).toString()
        ),
        MinEmitBox: pcs.MinEmitBox,
        MaxEmitBox: pcs.MaxEmitBox,
        Direction1: pcs.Direction1,
        Direction2: pcs.Direction2,
        Gravity: pcs.Gravity,
        EmitPower: pcs.EmitPower,
        BlendMode: pcs.BlendMode,
        UpdateSpeed: pcs.UpdateSpeed,
        SubParticleSystems: pcs.SubParticleSystems,
        ManualEmitCount: pcs.ManualEmitCount,
        Emiter: pcs.Emiter ? pcs.Emiter.toJSON() : {},
        Gradients: pcs.Gradients.map((g) => ({ ...g, id: idIndex++ })),
      });
    }
  };

  useEffect(() => {
    init(pcs);
  }, [pcs]);

  return (
    <Box sx={sx}>
      {Object.keys(json).length !== 0 && (
        <List sx={{ height: "100%", overflow: "auto" }}>
          <CollapseContainer
            title="基础"
            open={collkeys.has("basic")}
            onExpand={handleExpand}
            target="basic"
          >
            <List component="div" disablePadding>
              <ListItem>
                <TitleInput
                  title="名称"
                  value={json.Name}
                  name="Name"
                  onChange={handleChange}
                  onBlur={handleBlur}
                />
              </ListItem>
              <ListItem>
                <TitleInput
                  title="粒子图片"
                  value={json.Image}
                  name="Image"
                  onChange={handleChange}
                  onBlur={handleBlur}
                />
              </ListItem>
              <ListItem>
                <TitleInput
                  title="粒子数量"
                  value={json.Capacity}
                  name="Capacity"
                  onChange={handleChange}
                  onBlur={handleBlur}
                />
              </ListItem>
              <ListItem>
                <TitleInput
                  title="更新速度"
                  value={json.UpdateSpeed}
                  name="UpdateSpeed"
                  onChange={handleChange}
                  onBlur={handleBlur}
                />
              </ListItem>
              <ListItem>
                <Title title="混合模式" />
                <CommonSelect
                  value={json.BlendMode}
                  options={[
                    {
                      label: "默认",
                      value: 0,
                    },
                    {
                      label: "STANDARD",
                      value: 1,
                    },
                    {
                      label: "ADD",
                      value: 2,
                    },
                    {
                      label: "MULTIPLY",
                      value: 3,
                    },
                    {
                      label: "MULTIPLYADD",
                      value: 4,
                    },
                  ]}
                  name="BlendMode"
                  onChange={handleChangeSelect}
                />
              </ListItem>
              <ListItem>
                <Title title="重力" />
                <VectorPanel
                  onChange={handleChangeVector}
                  onBlur={handleApplyVector}
                  vector={json.Gravity}
                  name="Gravity"
                />
              </ListItem>
              <ListItem>
                <Title title="粒子锚点" />
                <VectorPanel
                  onChange={handleChangeVector}
                  onBlur={handleApplyVector}
                  vector={json.Pivot}
                  name="Pivot"
                />
              </ListItem>
              <ListItem>
                <TitleInput
                  title="单次粒子数"
                  value={json.ManualEmitCount}
                  name="ManualEmitCount"
                  onChange={handleChange}
                  onBlur={handleBlur}
                />
              </ListItem>
            </List>
          </CollapseContainer>
          <CollapseContainer
            title="粒子颜色属性"
            open={collkeys.has("color")}
            onExpand={handleExpand}
            target="color"
          >
            <List component="div" disablePadding>
              {!json.Gradients.find((g) => g.key === "Color") && (
                <>
                  <ListItem>
                    <Title title="粒子颜色1" />
                    <ColorPickInput
                      value={json.Color1}
                      color={getRgba(json.Color1)}
                      onChange={(c) => handleChangeColor(c, "Color1")}
                    />
                  </ListItem>
                  <ListItem>
                    <Title title="粒子颜色2" />
                    <ColorPickInput
                      value={json.Color2}
                      color={getRgba(json.Color2)}
                      onChange={(c) => handleChangeColor(c, "Color2")}
                    />
                  </ListItem>
                  <ListItem>
                    <Title title="粒子消失颜色" />
                    <ColorPickInput
                      value={json.ColorDead}
                      color={getRgba(json.ColorDead)}
                      onChange={(c) => handleChangeColor(c, "ColorDead")}
                    />
                  </ListItem>
                </>
              )}
              {/* <ListItemButton onClick={handleAddColorGradient}>
                <ListItemText primary="添加颜色渐变" />
                <AddIcon />
              </ListItemButton> */}
              {json.Gradients.map(
                (g, index) =>
                  g.key === "Color" && (
                    <GradientItem
                      onChange={(json) =>
                        handleChangeColorGradient(json, index)
                      }
                      gradient={g}
                      key={g.id}
                      onDelete={() => handleDeleteColorGradient(g.id)}
                    />
                  )
              )}
            </List>
          </CollapseContainer>

          <CollapseContainer
            title="发射器属性"
            open={collkeys.has("emiter")}
            onExpand={handleExpand}
            target="emiter"
          >
            <List component="div" disablePadding>
              <ListItem>
                <Title title="发射位置类型" />
                <CommonSelect
                  value={json.EmiterType}
                  options={[
                    {
                      label: "点",
                      value: EmiterType.Point,
                    },
                    {
                      label: "实体",
                      value: EmiterType.Entity,
                    },
                    {
                      label: "相机",
                      value: EmiterType.Camera,
                    },
                  ]}
                  name="EmiterType"
                  onChange={handleChangeSelect}
                />
              </ListItem>

              {json.EmiterType === EmiterType.Point && (
                <ListItem>
                  <Title title="发射位置" />
                  <VectorPanel
                    onChange={handleChangeVector}
                    onBlur={handleApplyVector}
                    vector={json.EmiterPosition}
                    name="EmiterPosition"
                  />
                </ListItem>
              )}

              <ListItem>
                <Title title="发射形状" />
                <CommonSelect
                  value={json.EmiterShape}
                  options={[
                    {
                      label: "点",
                      value: EmiterShape.Point,
                    },
                    {
                      label: "盒子",
                      value: EmiterShape.Box,
                    },
                    {
                      label: "球",
                      value: EmiterShape.Sphere,
                    },
                    {
                      label: "半球",
                      value: EmiterShape.Hemisphere,
                    },
                    {
                      label: "锥形",
                      value: EmiterShape.Cone,
                    },
                    {
                      label: "圆柱",
                      value: EmiterShape.Cylinder,
                    },
                    {
                      label: "方向圆柱",
                      value: EmiterShape.DirctedCylinder,
                    },
                  ]}
                  name="EmiterShape"
                  onChange={handleChangeSelect}
                />
              </ListItem>
              {json.EmiterShape === EmiterShape.Box && (
                <>
                  <ListItem>
                    <Title title="发射器最小点" />
                    <VectorPanel
                      onChange={handleChangeVector}
                      onBlur={handleApplyVector}
                      vector={json.MinEmitBox}
                      name="MinEmitBox"
                    />
                  </ListItem>
                  <ListItem>
                    <Title title="发射器最大点" />
                    <VectorPanel
                      onChange={handleChangeVector}
                      onBlur={handleApplyVector}
                      vector={json.MaxEmitBox}
                      name="MaxEmitBox"
                    />
                  </ListItem>
                </>
              )}
              {(json.EmiterShape === EmiterShape.Box ||
                json.EmiterShape === EmiterShape.Point) && (
                <>
                  <ListItem>
                    <Title title="发射方向1" />
                    <VectorPanel
                      onChange={handleChangeVector}
                      onBlur={handleApplyVector}
                      vector={json.Direction1}
                      name="Direction1"
                    />
                  </ListItem>
                  <ListItem>
                    <Title title="发射方向2" />
                    <VectorPanel
                      onChange={handleChangeVector}
                      onBlur={handleApplyVector}
                      vector={json.Direction2}
                      name="Direction2"
                    />
                  </ListItem>
                </>
              )}
              {json.EmiterShape === EmiterShape.Sphere && (
                <>
                  <ListItem>
                    <TitleInput
                      title="发射半径"
                      value={json.Emiter.Radius}
                      name="Emiter.Radius"
                      onChange={handleChange}
                      onBlur={handleBlur}
                    />
                  </ListItem>
                  <ListItem>
                    <TitleInput
                      title="发射球范围"
                      value={json.Emiter.RadiusRange}
                      name="Emiter.RadiusRange"
                      onChange={handleChange}
                      onBlur={handleBlur}
                    />
                  </ListItem>
                </>
              )}
            </List>
          </CollapseContainer>
          <CollapseContainer
            title="发射属性"
            open={collkeys.has("emission")}
            onExpand={handleExpand}
            target="emission"
          >
            <List component="div" disablePadding>
              <ListItem>
                <TitleInput
                  title="粒子发射数量"
                  value={json.Rate}
                  name="Rate"
                  onChange={handleChange}
                  onBlur={handleBlur}
                />
              </ListItem>
              <ListItem>
                <Title title="发射速度范围" />
                <Range
                  range={json.EmitPower}
                  onChange={handleChangeRange}
                  name="EmitPower"
                />
              </ListItem>
            </List>
          </CollapseContainer>

          <CollapseContainer
            title="粒子尺寸"
            open={collkeys.has("size")}
            onExpand={handleExpand}
            target="size"
          >
            <List component="div" disablePadding>
              {!json.Gradients.find((g) => g.key === "Size") && (
                <ListItem>
                  <Title title="粒子尺寸范围" />
                  <Range
                    range={json.Size}
                    onChange={handleChangeRange}
                    name="Size"
                  />
                </ListItem>
              )}
              <ListItemButton onClick={handAddGradient}>
                <ListItemText primary="添加尺寸渐变" />
                <AddIcon />
              </ListItemButton>
              {json.Gradients.map(
                (g, index) =>
                  g.key === "Size" && (
                    <GradientItem
                      onChange={(json) => handleChangeGradient(json, index)}
                      gradient={g}
                      key={g.id}
                      onDelete={() => handleDeleteGradient(g.id)}
                    />
                  )
              )}
            </List>
          </CollapseContainer>

          <CollapseContainer
            title="粒子生命周期"
            open={collkeys.has("lifetime")}
            onExpand={handleExpand}
            target="lifetime"
          >
            <List component="div" disablePadding>
              <ListItem>
                <Title title="粒子生命周期" />
                <Range
                  range={json.LifeTime}
                  onChange={handleChangeRange}
                  name="LifeTime"
                />
              </ListItem>
              <ListItem>
                <TitleInput
                  title="持续时间"
                  name="TargetStopDuration"
                  value={json.TargetStopDuration}
                  onChange={handleChange}
                />
              </ListItem>
            </List>
          </CollapseContainer>

          <CollapseContainer
            title="粒子旋转属性"
            open={collkeys.has("rotation")}
            onExpand={handleExpand}
            target="rotation"
          >
            <List component="div" disablePadding>
              <ListItem>
                <Title title="角速度" />
                <Range
                  range={json.AngularSpeed}
                  onChange={handleChangeRange}
                  name="AngularSpeed"
                />
              </ListItem>
            </List>
          </CollapseContainer>

          <CollapseContainer
            title="精灵图属性"
            open={collkeys.has("sprite")}
            onExpand={handleExpand}
            target="sprite"
          >
            <List component="div" disablePadding>
              <ListItem>
                <Title title="使用精灵图" />
                <Checkbox
                  name="SpriteOption.isAnimationSheetEnabled"
                  checked={json.SpriteOption?.isAnimationSheetEnabled}
                  onChange={handleChecked}
                />
              </ListItem>
              {json.SpriteOption?.isAnimationSheetEnabled && (
                <>
                  <ListItem>
                    <TitleInput
                      name="SpriteOption.spriteCellWidth"
                      title="精灵宽"
                      value={json.SpriteOption?.spriteCellWidth}
                      onChange={handleChange}
                    />
                  </ListItem>
                  <ListItem>
                    <TitleInput
                      title="精灵高"
                      name="SpriteOption.spriteCellHeight"
                      value={json.SpriteOption?.spriteCellHeight}
                      onChange={handleChange}
                    />
                  </ListItem>
                  <ListItem>
                    <Title title="随机精灵" />
                    <Checkbox
                      name="SpriteOption.spriteRandomStartCell"
                      checked={json.SpriteOption?.spriteRandomStartCell}
                      onChange={handleChecked}
                    />
                  </ListItem>
                  <ListItem>
                    <TitleInput
                      title="起始精灵索引"
                      name="SpriteOption.startSpriteCellID"
                      value={json.SpriteOption?.startSpriteCellID}
                      onChange={handleChange}
                    />
                  </ListItem>
                  <ListItem>
                    <TitleInput
                      title="结束精灵索引"
                      name="SpriteOption.endSpriteCellID"
                      value={json.SpriteOption?.endSpriteCellID}
                      onChange={handleChange}
                    />
                  </ListItem>
                  <ListItem>
                    <TitleInput
                      title="精灵切换速度"
                      name="SpriteOption.spriteCellChangeSpeed"
                      value={json.SpriteOption?.spriteCellChangeSpeed}
                      onChange={handleChange}
                    />
                  </ListItem>
                </>
              )}
            </List>
          </CollapseContainer>
          {!isSub && (
            <>
              <ListItemButton
                onClick={toggleDrawer}
                sx={{ background: "#ccc", outline: "1px solid #444" }}
              >
                <ListItemText
                  primary={`子粒子触发器(${json.SubParticleSystems.length})`}
                />
                <BuildIcon />
              </ListItemButton>
              <Drawer
                anchor="left"
                open={openSub}
                onClose={toggleDrawer}
                variant="persistent"
                PaperProps={{ sx: { width: "40%" } }}
              >
                <Typography sx={{ textAlign: "center" }}>
                  子粒子触发器
                </Typography>
                <HStack>
                  <Button onClick={handleAddSubEmiter}>添加</Button>
                  <Button onClick={handleDeleteSubEmiter}>删除</Button>
                  <Button onClick={applySubEmiter}>应用</Button>
                  <Button onClick={confirmSubEmiter}>确定</Button>
                  <Button onClick={toggleDrawer}>取消</Button>
                </HStack>
                <HStack flexWrap="wrap">
                  {json.SubParticleSystems.map((s, index) => (
                    <Button
                      color={sub === s ? "secondary" : "primary"}
                      variant="outlined"
                      key={s.Id ?? index}
                      onClick={() => setSub(s)}
                    >
                      {s.Name}
                    </Button>
                  ))}
                </HStack>
                {sub && (
                  <ParticleSystemProperty isSub pcs={sub.particleSystem} />
                )}
              </Drawer>
            </>
          )}
        </List>
      )}
    </Box>
  );
};
export default ParticleSystemProperty;
