import { IVariableOption, ObjectId } from "@hc/engine";
import {
  Autocomplete,
  AutocompleteChangeReason,
  Box,
  MenuItem,
  TextField,
} from "@mui/material";
import { Observer } from "mobx-react-lite";
import React from "react";
import { App } from "~/intance";
import { interactionStore } from "~/stores/InteractionStore";

interface CommonSelectEntityProps {
  value: { objectId?: ObjectId; value?: ObjectId } | ObjectId;
  onChange?: (evt, opt, reason) => void;
  objKey?: string;
  openSelect?: boolean;
  hasPage?: boolean;
  label?: string;
}

export const CommonSelectEntity: React.FC<CommonSelectEntityProps> = ({
  value,
  onChange,
  objKey = "objectId",
  label,
  hasPage,
  openSelect,
  ...rest
}) => {
  const options = App.intance.Database.ModelSpace.Entitys.filter(
    (e) => !e.IsErase
  ).map((e) => ({
    label: e.Name,
    value: e.Id,
  }));

  if (hasPage) {
    options.unshift({ label: "页面", value: 0 });
  }

  const handleSelect = (
    evt: React.SyntheticEvent<Element, Event>,
    opt: any,
    reason: AutocompleteChangeReason
  ) => {
    if (reason === "selectOption") {
      if (onChange) {
        onChange?.(evt, opt, reason);
      } else
        interactionStore.updateState(() => {
          value[objKey] = App.intance.Database.getObjectId(opt.value);
        });
    }
  };

  return (
    <Observer>
      {() => (
        <Autocomplete
          id="select-entity-list"
          sx={{ width: 300 }}
          options={options}
          autoHighlight
          size="small"
          isOptionEqualToValue={(o, v) => {
            if (v === -1) return true;
            return o.value === v;
          }}
          getOptionLabel={(v: any) => {
            if (v === -1) return "请选择";
            return (
              options.find((opt) => opt.value === v)?.label ?? v?.label ?? v
            );
          }}
          onChange={handleSelect}
          value={
            value instanceof ObjectId
              ? value.Index
              : value?.[objKey]?.Index ?? -1
          }
          renderOption={(props, option) => (
            <Box component="li" {...props} key={option.value}>
              {option.label}
            </Box>
          )}
          renderInput={(params) => (
            <TextField
              {...params}
              label={label ?? "选择目标"}
              inputProps={{
                ...params.inputProps,
              }}
              {...rest}
            />
          )}
        />
      )}
    </Observer>
  );
};
export const CommonSelectParam: React.FC<{
  variables: IVariableOption[];
  value?: string;
  onChange?: (evt) => void;
  objKey?: string;
  openSelect?: boolean;
}> = ({ value, onChange, variables }) => {
  return (
    <TextField
      size="small"
      fullWidth
      select
      value={value}
      label="变量"
      sx={{ my: 1 }}
      onChange={onChange}
    >
      {variables.map((d) => (
        <MenuItem key={d.key} value={d.key}>
          {d.key}
        </MenuItem>
      ))}
    </TextField>
  );
};
