import { Typography } from "@mui/material";
import { when } from "mobx";
import React, { useEffect, useState } from "react";
import { App } from "~/intance";

const FPS: React.FC = () => {
  const [fps, setFPS] = useState("60");
  useEffect(() => {
    let timer;
    when(
      () => !!App.intance,
      () => {
        timer = setInterval(
          () => setFPS(App.intance.Viewer.FPS.toFixed(0)),
          1000
        );
      }
    );
    return () => {
      timer && clearInterval(timer);
    };
  }, [App.intance]);

  return <Typography>{fps} fps</Typography>;
};

export default FPS;
