import * as React from "react";
import { createRoot } from "react-dom/client";

import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
} from "@mui/material";

interface ConfirmProp {
  title?: string;
  content?: string;
  onOk?: () => void;
  onClose?: () => void;
}

const Confirm: React.FC<ConfirmProp> = ({ title, content, onOk, onClose }) => {
  const [open, setOpen] = React.useState(true);

  const closePane = () => {
    setOpen(false);
  };

  const handleOk = () => {
    closePane();
    onOk && onOk();
  };

  const handleClose = () => {
    closePane();
    onClose && onClose();
  };

  return (
    <div>
      <Dialog
        open={open}
        onClose={closePane}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
        maxWidth="lg"
      >
        <DialogTitle id="alert-dialog-title" sx={{ fontSize: "16px" }}>
          {title || "提示"}
        </DialogTitle>
        <DialogContent sx={{ width: 400 }}>
          <DialogContentText
            sx={{ fontSize: "13px", color: "#333" }}
            id="alert-dialog-description"
          >
            {content}
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose}>取消</Button>
          <Button onClick={handleOk}>确定</Button>
        </DialogActions>
      </Dialog>
    </div>
  );
};

const confirm = ({ title, content, onOk, onClose }: ConfirmProp) => {
  // 创建一个dom
  const dom = document.createElement("div");
  // 渲染DOM
  createRoot(dom).render(
    <Confirm
      title={title}
      content={content}
      onOk={onOk}
      onClose={onClose}
    ></Confirm>
  );

  // 置入到body节点下
  document.body.appendChild(dom);
};
export { confirm };
