import {
  BaseGui,
  GenerateTexture,
  CanvasControl,
  AppStore,
  HCFiler,
  Factory,
  Utils,
  Video,
  MultiLine2,
} from "@hc/engine";
import { getGraphView, getHTNode, getHTData, IHC2D, IDataBindings } from "./ht";

@Factory
export class HC2DGui extends BaseGui implements GenerateTexture, IHC2D {
  static type = "HC2DGui";
  private _source: string;
  private graphView;
  private hcNode;
  private _dataBindings: IDataBindings[];
  private _setAttr: Record<string, any> = {};
  private timer: NodeJS.Timeout;
  constructor(option?: { url: string }) {
    super();
    if (option?.url) {
      this._source = option.url;
    }
  }
  get DataBindings() {
    return this._dataBindings ?? [];
  }
  get Attr() {
    return this._setAttr;
  }
  /**
   * 设置绑定数据值
   * @param k
   * @param value
   */
  setProp(k: string, value: any) {
    this.updateAttr({ [k]: value });
  }
  updateAttr(obj: Record<string, any>) {
    if (Object.keys(obj).length === 0 || !this.graphView || !this.hcNode)
      return;

    const dom = this.graphView.getView();
    document.body.append(dom);
    this.timer && clearTimeout(this.timer);
    this.timer = setTimeout(() => {
      dom.remove();
    }, 2000);
    Object.assign(this._setAttr, obj);

    this.hcNode.a(obj);
  }

  initDrawObject() {
    const graphView = getGraphView();
    const root = Utils.getGuiContainer(this.UniqueName);
    root.horizontalAlignment = 0;
    root.verticalAlignment = 0;

    graphView.addViewListener((event) => {
      if (event?.kind === "validate") this.updateDrawObject(root);
    });
    this.graphView = graphView;
    getHTNode(graphView.dm(), this._source).then((node: any) => {
      if (!node) return;
      this.hcNode = node;
      const data = getHTData(node.getImage());
      this.load(data);
      this.generateLinkLine(root, 100);
    });

    return root;
  }
  updateDrawObject(obj: Utils.Container): void {
    super.updateDrawObject(obj);
    if (this.linkEntity) {
      obj.linkWithMesh(this.linkEntity.Object.DrawObject);
    } else {
      obj.linkWithMesh(null);
    }
    this.graphView.fitContent(false, 0);
    let canvas: HTMLCanvasElement;
    let video: HTMLVideoElement;
    const dom = this.graphView.__htmlOrderList?.[0] as HTMLElement;
    if (dom && dom instanceof HTMLElement) {
      canvas =
        this.graphView.__htmlOrderList[0].getElementsByTagName("canvas")[0];

      if (dom.nodeName === "VIDEO") {
        video = dom as HTMLVideoElement;
      }
    } else {
      canvas = this.graphView.getCanvas();
    }

    if (!canvas) canvas = this.graphView.getCanvas();

    if (obj.children.length === 0) {
      if (video) {
        this.DataBindings.find;
        const gui = new Video(
          this.UniqueName + "_video",
          video.src,
          this._width,
          this._height,
          AppStore.MainScene
        );
        obj.addControl(gui);
        gui.width = this._width;
        gui.height = this._height;
      } else {
        const gui = new CanvasControl(
          this.UniqueName,
          canvas,
          AppStore.MainScene
        );
        obj.addControl(gui);
        gui.width = this._width;
        gui.height = this._height;
      }
    } else {
      const control = obj.children[0];
      if (video) {
        (control as Video).source = video.src;
      } else {
        (control as CanvasControl).CanvasDom = canvas;
      }
    }

    obj.width = this._width;
    obj.height = this._height;
    this.updateAttr(this._setAttr);
  }
  getTexture() {
    return null;
  }
  get IsSelect() {
    return this._isSelect;
  }
  set IsSelect(v: boolean) {
    this._isSelect = v;
    if (!this._drawObject) return;
    const root = this._drawObject as Utils.Container;
    if (v) {
      root.background = "#9595d8";
    } else {
      root.background = "";
    }
  }
  writeFile(filer: HCFiler): void {
    super.writeFile(filer);
    filer.write(1);
    filer.write(this._source);
    filer.write(JSON.stringify(this._setAttr));
  }
  readFile(filer: HCFiler): void {
    super.readFile(filer);
    filer.read();
    this._source = filer.read();
    this._setAttr = JSON.parse(filer.read());
    this.update();
  }
  private load = (data) => {
    if (!data) return;
    const width =
      typeof data.width === "object" ? data.width.value : data.width;
    const height =
      typeof data.height === "object" ? data.height.value : data.height;

    this._dataBindings = data.dataBindings;

    const dom = this.graphView.getView() as HTMLElement;
    this._width = width + "px";
    this._height = height + "px";

    dom.style.cssText = `position:fixed;left:0;top:0;width:${width}px;height:${height}px;visibility:hidden`;
    this.graphView.getCanvas().width = width;
    this.graphView.getCanvas().height = height;
    document.body.append(dom);
    setTimeout(() => {
      dom.remove();
    }, 2000);
  };
}
