import { Box } from "@mui/material";
import { observer } from "mobx-react-lite";
import React from "react";
import { CommonSelectParam } from "~/components/common/commonInteraction";
import { useInteractionStoresContext } from "~/contexts/interactionStoreContext";
import { useRightStores } from "~/contexts/rightStoreContext";
import ValueTypeSelect from "./valueTypeSelect";

interface SetParamDetailProps {}

const SetVariableDetail: React.FC<SetParamDetailProps> = (props) => {
  const store = useInteractionStoresContext();
  const { globalVarStore } = useRightStores();

  const detail = store.CurrentActionDetail;

  const handleChangeValueType = (evt) => {
    store.updateState(() => {
      detail.value.type = evt.target.value;
    });
  };
  const handleChangeValue = (evt) => {
    store.updateState(() => {
      detail.value.value = evt.target.value;
    });
  };

  return (
    <Box>
      <CommonSelectParam
        value={detail.key}
        variables={globalVarStore.variables}
      />
      <ValueTypeSelect
        widgetValue={detail.value}
        onChange={handleChangeValueType}
        variables={globalVarStore.variables}
        selectProps={{ fullWidth: true, label: "设置为", sx: { my: 1 } }}
        onChangeValue={handleChangeValue}
      />
    </Box>
  );
};
export default observer(SetVariableDetail);
