import React from "react";
import { Typography } from "@mui/material";
interface TitleProps {
  title: string;
  mt?: number;
  tip?: string;
}

const Title: React.FC<TitleProps> = ({ title, mt, tip }) => {
  return (
    <Typography
      sx={{
        mr: 2,
        mt: mt ? mt : 0,
        color: "#424B54",
        width: 60,
        minWidth: 60,
        fontSize: 14,
        textAlign: "left",
      }}
      title={tip}
    >
      {title}
    </Typography>
  );
};
export default Title;
