import {
  Button,
  DialogContent,
  DialogTitle,
  FormControlLabel,
  Switch,
  TextField,
  Typography,
} from "@mui/material";
import Dialog from "@mui/material/Dialog";
import React, { useEffect, useState } from "react";
import { AppApi } from "~/apis";
import { postReq, ResponseStatus } from "~/utils/axios";
import { message } from "~/utils/message";
import { copyText } from "~/utils/utils";

import { HStack } from "../common/stack";

interface ShareProps {
  onClose: () => void;
  open: boolean;
  target: number;
}

const SharePanel: React.FC<ShareProps> = ({ onClose, open, target }) => {
  const [isSecret, setIsSecret] = useState(false);
  const [secret, setSecret] = useState("");
  const [key, setKey] = useState("");

  const share = async () => {
    const result = await postReq(AppApi.share, {
      id: target,
      secret: isSecret ? secret : "",
    });
    if (result.code === ResponseStatus.Ok) {
      const key = result.data.key;
      setKey(key);
    }
  };

  const copy = () => {
    copyText(getUrl(key)).then((r) => {
      message.success("已复制分享链接!");
    });
    onClose();
  };

  const getUrl = (k = key) => {
    let url = location.origin + "/share.html";
    if (k) {
      url += `?k=${k}`;
    }
    return url;
  };

  useEffect(() => {
    share();
  }, []);

  return (
    <Dialog open onClose={onClose}>
      <DialogTitle>分享</DialogTitle>
      <DialogContent>
        <HStack alignItems="center" sx={{ mb: 2 }}>
          <Typography sx={{ mr: 2 }}>链接</Typography>
          <TextField value={getUrl()} size="small" sx={{ flex: 1 }} />
        </HStack>
        <HStack alignItems="center">
          <FormControlLabel
            label="加密"
            checked={isSecret}
            control={<Switch />}
            onChange={(evt, c) => setIsSecret(c)}
          />
          <HStack alignItems="center">
            <Typography sx={{ mr: 2 }}>设置密码</Typography>
            <TextField
              value={secret}
              disabled={!isSecret}
              size="small"
              onChange={(evt) => setSecret(evt.target.value)}
              onBlur={share}
            />
          </HStack>
          <Button variant="contained" onClick={copy} sx={{ ml: 2 }}>
            复制链接
          </Button>
        </HStack>
      </DialogContent>
    </Dialog>
  );
};
export default SharePanel;
