import {
  Box,
  CircularProgress,
  CircularProgressProps,
  LinearProgress,
  LinearProgressProps,
  Typography,
} from "@mui/material";
import * as React from "react";

export const CircularProgressWithLabel: React.FC<
  CircularProgressProps & { value: number; label: string }
> = (props) => {
  return (
    <Box sx={{ position: "relative", display: "inline-flex" }}>
      <CircularProgress variant="determinate" {...props} />
      <Box
        sx={{
          top: 0,
          left: 0,
          bottom: 0,
          right: 0,
          position: "absolute",
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
        }}
      >
        <Typography variant="caption" component="div" color="text.secondary">
          {props.label}
          {`${Math.round(props.value)}%`}
        </Typography>
      </Box>
    </Box>
  );
};

export function LinearProgressWithLabel(
  props: LinearProgressProps & { value: number; label: string }
) {
  return (
    <Box sx={{ width: 200, height: 30, position: "relative" }}>
      <LinearProgress variant="determinate" {...props} />
      <Typography
        sx={{ position: "absolute", top: 3, left: "10%", color: "#FCFCFC" }}
      >
        {props.label}
      </Typography>
    </Box>
  );
}

export function LinearProgressWithLabel2(
  props: LinearProgressProps & { value: number; label?: string }
) {
  return (
    <Box sx={{ display: "flex", alignItems: "center" }}>
      <Box sx={{ width: "20%", whiteSpace: "no-wrap" }}>
        {props.label || "正在加载资源："}
      </Box>
      <Box sx={{ width: "70%", mr: 1 }}>
        <LinearProgress variant="determinate" {...props} />
      </Box>
      <Box sx={{ width: "10%" }}>
        <Typography variant="body2" color="#fff">{`${Math.round(
          props.value
        )}%`}</Typography>
      </Box>
    </Box>
  );
}
