import { Typography, Box, SvgIconProps } from "@mui/material";
import React, { useState } from "react";
import IconAli from "../common/iconAli";
import { SxProps } from "@mui/system";
import ButtonBase from "@mui/material/ButtonBase";
interface UpDownButtonProps {
  iconName: string | React.FC<SvgIconProps>;
  title: string;
  sx?: SxProps;
  onClick: (e) => void;
  color?: string;
  disabled?: boolean;
}

const UpDownButton: React.FC<UpDownButtonProps> = ({
  iconName: IconName,
  title,
  sx,
  onClick,
  color,
  disabled,
}) => {
  const [fontSize, setFontSize] = useState(12); //#075FB8
  if (disabled === undefined) disabled = false;
  let iconColor = disabled ? "rgba(0, 0, 0, 0.26);" : color ?? "#000";
  return (
    <Box
      onClick={disabled ? () => {} : onClick}
      sx={{
        ...sx,
        display: "flex",
        flexDirection: "column",
        justifyContent: "center",
        alignItems: "center",
        pr: 1,
        pl: 1,
        cursor: disabled ? "not-allowed" : "pointer",
        "&:hover": {
          background: "rgba(7, 177, 77, 0.1)",
        },
        color: iconColor,
        borderRadius: "10%",
        outline: color ? "1px solid " + color : undefined,
      }}
    >
      {typeof IconName === "string" ? (
        <IconAli name={IconName} color={iconColor} />
      ) : (
        <IconName sx={{ fontSize: 20 }} />
      )}
      <Typography
        sx={{
          m: 0,
          p: 0,

          fontSize: fontSize,
        }}
      >
        {title}
      </Typography>
    </Box>
  );
};
export default UpDownButton;
