import { CMD, end } from "@hc/engine";
import { Box } from "@mui/material";
import React, { useEffect, useState } from "react";
import { debounceCallback } from "~/utils/time";

interface CmdHistoryProps {}

const CmdHistory: React.FC<CmdHistoryProps> = () => {
  const [cmdName, setCmdName] = useState("");
  useEffect(() => {
    const clears = [
      end(CMD.commandManager, CMD.commandManager.CommandStart, (cmdName) => {
        setCmdName("执行:" + cmdName);
      }),
      end(CMD.commandManager, CMD.commandManager.CommandEnd, () => {
        debounceCallback(() => {
          setCmdName("");
        }, 1000);
      }),
    ];
    return () => {
      clears.forEach((c) => c());
    };
  }, []);

  return <Box>{cmdName}</Box>;
};
export default CmdHistory;
