import React from "react";

import { Icon } from "@mui/material";
import { styled } from "@mui/material/styles";

const SelfIcon = styled(Icon)(({ theme }) => ({
  fontSize: "20px",
  color: "#333",
}));

const SelfI = styled("i")(({ theme }) => ({
  cursor: "pointer",
  fontSize: "18px",
  color: "#333",
}));

interface SIconProps {
  type: string;
  sx?: object;
  onClick?: Function;
}
const SIcon: React.FC<SIconProps> = ({ type, sx, onClick }) => {
  const renderIcon = () => {
    if (!type) {
      return null;
    }
    let style = sx || {};
    if (type.indexOf("icon-") === -1) {
      // 图标名称驼峰换成下划线
      return (
        <SelfIcon sx={style} onClick={(e) => onClick && onClick(e)}>
          {type}
        </SelfIcon>
      );
    } else {
      return (
        <SelfI
          sx={style}
          className={"iconfont " + type}
          onClick={(e) => onClick && onClick(e)}
        ></SelfI>
      );
    }
  };

  return renderIcon();
};

export default SIcon;
