import { MenuItem, TextField, TextFieldProps } from "@mui/material";
import React from "react";

interface SelectProps {
  options: ({ label: string; value: number | string } | string | number)[];
}

const CommonSelect: React.FC<SelectProps & TextFieldProps> = ({
  options,
  value,
  onChange,
  ...props
}) => {
  return (
    <TextField select size="small" onChange={onChange} value={value} {...props}>
      {options.map((opt) => {
        let label = typeof opt === "object" ? opt.label : opt;
        let value = typeof opt === "object" ? opt.value : opt;
        return (
          <MenuItem key={value} value={value}>
            {label}
          </MenuItem>
        );
      })}
    </TextField>
  );
};
export default CommonSelect;
