import { Utils } from "@hc/engine";
import AddIcon from "@mui/icons-material/Add";
import FolderIcon from "@mui/icons-material/Folder";
import { ListItemButton, TextField, Typography } from "@mui/material";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import ListItemText from "@mui/material/ListItemText";
import * as React from "react";
import { DirSourceType } from "~/apis";
import { createFileDir, getFileList } from "~/apis/fileReq";
import { IFileReturnData } from "~/apis/types";
import { ResponseStatus } from "~/utils/axios";
import { Bus } from "~/utils/bus";
import { message } from "~/utils/message";

interface SelectProjectListProps {
  onClick: (id) => void;
  projectId: number;
  disableId?: number;
  selectId?: number;
  sourceType: DirSourceType;
}

let isConfirm = false;

const SelectDirList: React.FC<SelectProjectListProps> = ({
  onClick,
  disableId,
  selectId,
  sourceType,
  projectId,
}) => {
  const [list, setList] = React.useState<
    (IFileReturnData & { editor?: boolean })[]
  >([]);
  const [page, setPage] = React.useState({
    page: 1,
    limit: 20,
    project_type: "3d",
  });

  const update = async () => {
    const list = await getFileList(sourceType);
    setList((list ?? []).filter((d) => d.node_type === "dir"));
  };

  const addDir = () => {
    setList([...list, { ...list[0], id: -1, name: "新建目录", editor: true }]);
  };

  const handleChangeName = (
    e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>,
    d: IFileReturnData
  ) => {
    d.name = e.target.value;
    setList([...list]);
  };

  const handleConfirm = async (e: React.KeyboardEvent | React.ChangeEvent) => {
    if ("code" in e && e.code !== Utils.KeyCode.Enter) {
      return;
    }

    if (isConfirm) {
      isConfirm = false;
      return;
    }

    isConfirm = true;

    const v = (e.target as HTMLInputElement).value;

    if (!v) {
      message.error("目录不能为空");
      list.pop();
      setList([...list]);
      return;
    }

    const res = await createFileDir({
      name: v,
      sourceType: sourceType,
    });

    if (res.code === ResponseStatus.Ok) {
      if (sourceType === DirSourceType.Model) {
        Bus.trigger({ type: "ApplyModel", target: null });
      }
      update();
    }
  };

  React.useEffect(() => {
    update();
  }, [page]);

  return (
    <List
      sx={{
        pt: 0,
        width: 500,
        maxHeight: 300,
        overflow: "auto",
        outline: "1px solid #ccc",
        p: "14px",
      }}
      subheader={<Typography>文件目录列表</Typography>}
    >
      {list.map((d) => (
        <ListItem
          disabled={disableId === d.id}
          autoFocus
          button
          key={d.id}
          onClick={() => onClick(d.id)}
          sx={{
            "&:hover": { backgroundColor: "#ccc" },
            backgroundColor: selectId === d.id ? "#ccc" : undefined,
          }}
        >
          <FolderIcon sx={{ mr: 2, color: "#df9c26" }} />
          {d.editor ? (
            <TextField
              size="small"
              value={d.name}
              onChange={(e) => handleChangeName(e, d)}
              onFocus={(e) => e.target.select()}
              onBlur={handleConfirm}
              onKeyDown={handleConfirm}
            />
          ) : (
            <ListItemText primary={d.name} />
          )}
        </ListItem>
      ))}
      <ListItemButton onClick={addDir}>
        <ListItemText primary="新建目录" />
        <AddIcon />
      </ListItemButton>
    </List>
  );
};
export default SelectDirList;
