import { Box, TextField, Typography } from "@mui/material";
import { observer } from "mobx-react-lite";
import React from "react";
import ColorPickInput from "~/components/common/colorPickInput";
import { CommonSelectEntity } from "~/components/common/commonInteraction";
import { useInteractionStoresContext } from "~/contexts/interactionStoreContext";

interface HighlightDetailProps {}

const HighlightDetail: React.FC<HighlightDetailProps> = () => {
  const store = useInteractionStoresContext();

  const detail = store.CurrentActionDetail;

  const handleChange = (c) => {
    store.updateState(() => {
      detail.color = c.hex;
    });
  };

  return (
    <Box>
      <CommonSelectEntity value={detail} />
      <Typography>颜色</Typography>
      <ColorPickInput color={detail.color} onChange={handleChange} />
    </Box>
  );
};
export default observer(HighlightDetail);
