import {
  Box,
  Button,
  IconButton,
  List,
  ListItem,
  Menu,
  MenuItem,
  Typography,
} from "@mui/material";
import React, { useMemo } from "react";
import CommonSelect from "~/components/common/select";
import { useInteractionStoresContext } from "~/contexts/interactionStoreContext";
import { App } from "~/intance";
import { EntityEventList, EventList } from "../dataList";
import { getEntityOptions } from "../utils";
import DeleteIcon from "@mui/icons-material/Delete";
import { observer } from "mobx-react-lite";

const ITEM_HEIGHT = 100;

let idIndex = 0;

const EnEventList = [];
for (const data of EntityEventList) {
  for (const d of data.list) {
    EnEventList.push({ label: d.label, value: d.type });
  }
}

const PageEventList = [];
for (const data of EventList) {
  for (const d of data.list) {
    PageEventList.push({ label: d.label, value: d.type });
  }
}

const DispatchDetail: React.FC = () => {
  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };
  const handleSelect = (evt) => {
    const type = evt.target.getAttribute("data-value");
    const label = evt.target.getAttribute("data-label");
    store.updateState(() => {
      detail.triggerNames.push({ type, label, id: idIndex++ });
    });
    handleClose();
  };
  const handleDelete = (index: number) => {
    store.updateState(() => {
      detail.triggerNames.splice(index, 1);
    });
  };
  const store = useInteractionStoresContext();

  const options = useMemo(() => {
    return getEntityOptions(true);
  }, []);

  const detail = store.CurrentActionDetail;

  const handleChange = (evt) => [
    store.updateState(() => {
      const value = evt.target.value;
      detail.objectId = App.intance.Database.getObjectId(Number(value));
    }),
  ];

  return (
    <Box>
      <CommonSelect
        options={options}
        onChange={handleChange}
        value={detail.objectId?.Index ?? "0"}
      />
      <List>
        {detail.triggerNames.map((t, index) => (
          <ListItem key={t.id}>
            <Typography>{t.label}</Typography>
            <IconButton onClick={() => handleDelete(index)}>
              <DeleteIcon />
            </IconButton>
          </ListItem>
        ))}
      </List>
      <Button onClick={handleClick}>添加事件</Button>
      <Menu
        id="event-list"
        MenuListProps={{
          "aria-labelledby": "long-button",
        }}
        anchorEl={anchorEl}
        open={open}
        onClose={handleClose}
        PaperProps={{
          style: {
            maxHeight: ITEM_HEIGHT * 4.5,
            width: "20ch",
          },
        }}
      >
        {(detail.objectId ? EnEventList : PageEventList).map((option) => (
          <MenuItem
            key={option.value}
            onClick={handleSelect}
            data-value={option.value}
            data-label={option.label}
          >
            {option.label}
          </MenuItem>
        ))}
      </Menu>
    </Box>
  );
};

export default observer(DispatchDetail);
