import {
  HCFiler,
  ParticleSystemGroup,
  ParticleSystem,
  HCFactory,
} from "@hc/engine";

export function parseParticle(data: { isGroup?: boolean; data }) {
  const fileData = data.data;
  if (!fileData) return;
  const filer = new HCFiler(fileData);
  const isGroup = data.isGroup;
  if (isGroup) {
    const group = new ParticleSystemGroup();
    group.readFile(filer);
    return group;
  } else {
    const pcs = new ParticleSystem();
    pcs.readFile(filer);
    return pcs;
  }
}

export function parseEngineObect(data: Record<string, any>) {
  if (!data) return null;
  if (data?.key === "particle") {
    const fileData = JSON.parse(data.json);
    const particle = parseParticle(fileData);
    particle.Intance.start();
    return particle;
  }

  if (data.key === "2D") {
    return null;
  }

  return HCFactory.CreateObject(data.key, data);
}
