import CloseIcon from "@mui/icons-material/Close";
import CropDinIcon from "@mui/icons-material/CropDin";
import DisplaySettingsIcon from "@mui/icons-material/DisplaySettings";
import FavoriteBorderIcon from "@mui/icons-material/FavoriteBorder";
import FilterNoneIcon from "@mui/icons-material/FilterNone";
import ShoppingCartIcon from "@mui/icons-material/ShoppingCart";
import WebAssetIcon from "@mui/icons-material/WebAsset";
import { Button, DialogContent, Drawer } from "@mui/material";
import AppBar from "@mui/material/AppBar";
import Dialog from "@mui/material/Dialog";
import IconButton from "@mui/material/IconButton";
import Slide from "@mui/material/Slide";
import Toolbar from "@mui/material/Toolbar";
import { TransitionProps } from "@mui/material/transitions";
import Typography from "@mui/material/Typography";
import * as React from "react";
import { useEffect, useState } from "react";
import { useLocalStorage } from "~/hooks/useLocalStrorage";
import { debounceCallback } from "~/utils/time";

import SearchInput from "../common/searchInput";
import { HStack } from "../common/stack";
import ResourceContent from "./content";
import { EMarketType } from "./types";
import { Tooltip } from "@mui/material";
interface ResourceManagerProps {
  onClose: () => void;
  open: boolean;
}

const Types: { label: string; value: 0 | 1 | 2; icon: JSX.Element }[] = [
  {
    label: "已收藏",
    value: EMarketType.Coll,
    icon: <FavoriteBorderIcon />,
  },
  {
    label: "已购买",
    value: EMarketType.Buyed,
    icon: <ShoppingCartIcon />,
  },
  {
    label: "所有素材",
    value: EMarketType.Shop,
    icon: <WebAssetIcon />,
  },
];

const Transition = React.forwardRef(function Transition(
  props: TransitionProps & {
    children: React.ReactElement;
  },
  ref: React.Ref<unknown>
) {
  return <Slide direction="up" ref={ref} {...props} />;
});

const ResourceManager: React.FC<ResourceManagerProps> = ({ onClose, open }) => {
  const [fullScreen, setFullScreen] = useState(false);
  const [isDrawer, setIsDrawer] = useState(true);

  const [type, setType] = useState<EMarketType>(EMarketType.Shop);
  const [query, setQuery] = useState("");

  const toggleDrawer = () => {
    setIsDrawer(!isDrawer);
  };

  const handleSearch = (evt: React.ChangeEvent<HTMLInputElement>) => {
    setQuery(evt.target.value);
  };

  const handleMaxScene = () => {
    setFullScreen(true);
  };
  const handleResetScene = () => {
    setFullScreen(false);
  };

  const handleSwitch = (t: EMarketType) => {
    setType(t);
  };

  if (isDrawer) {
    return (
      <Drawer
        anchor="right"
        open={open}
        onClose={onClose}
        ModalProps={{ hideBackdrop: true }}
        PaperProps={{ sx: { width: 320 } }}
        sx={{ width: 0 }}
        disableAutoFocus
        disableEnforceFocus
      >
        <AppBar sx={{ position: "relative", width: "100%" }}>
          <Toolbar>
            <Typography sx={{ ml: 2, flex: 1 }} variant="h6" component="div">
              市场
            </Typography>
            <IconButton
              edge="start"
              color="inherit"
              onClick={toggleDrawer}
              aria-label="close"
              sx={{ ml: "5px" }}
            >
              <DisplaySettingsIcon />
            </IconButton>
            <IconButton
              edge="start"
              color="inherit"
              onClick={onClose}
              aria-label="close"
              sx={{ ml: "5px" }}
            >
              <CloseIcon />
            </IconButton>
          </Toolbar>
        </AppBar>
        <HStack>
          <SearchInput value={query} onChange={handleSearch} sx={{ ml: 1 }} />
          <HStack>
            {Types.map((t) => (
              <Tooltip key={t.value} title={t.label} placement="bottom">
                <IconButton
                  color={type === t.value ? "primary" : "inherit"}
                  onClick={() => handleSwitch(t.value)}
                  sx={{ mr: 1 }}
                >
                  {t.icon}
                </IconButton>
              </Tooltip>
            ))}
          </HStack>
        </HStack>
        <ResourceContent
          query={query}
          type={type}
          contentType={2}
          sx={{ height: "calc(100% - 110px)" }}
        />
      </Drawer>
    );
  }

  return (
    <Dialog
      fullScreen={fullScreen}
      open={open}
      onClose={onClose}
      maxWidth="lg"
      fullWidth
      PaperProps={{ sx: { height: "100%" } }}
      TransitionComponent={Transition}
    >
      <AppBar sx={{ position: "relative" }}>
        <Toolbar>
          <Typography sx={{ ml: 2, flex: 1 }} variant="h6" component="div">
            市场
          </Typography>
          {!fullScreen ? (
            <IconButton
              edge="start"
              color="inherit"
              onClick={handleMaxScene}
              aria-label="close"
            >
              <CropDinIcon />
            </IconButton>
          ) : (
            <IconButton
              edge="start"
              color="inherit"
              onClick={handleResetScene}
              aria-label="close"
            >
              <FilterNoneIcon />
            </IconButton>
          )}

          <IconButton
            edge="start"
            color="inherit"
            onClick={toggleDrawer}
            aria-label="close"
            sx={{ ml: "5px" }}
          >
            <DisplaySettingsIcon />
          </IconButton>
          <IconButton
            edge="start"
            color="inherit"
            onClick={onClose}
            aria-label="close"
            sx={{ ml: "5px" }}
          >
            <CloseIcon />
          </IconButton>
        </Toolbar>
      </AppBar>
      <DialogContent sx={{ p: 2 }}>
        <HStack>
          <HStack>
            {Types.map((t) => (
              <Button
                key={t.value}
                variant="contained"
                size="small"
                startIcon={t.icon}
                color={type === t.value ? "primary" : "inherit"}
                onClick={() => handleSwitch(t.value)}
                sx={{
                  mr: 2,
                  backgroundColor: type !== t.value ? "#fff" : undefined,
                }}
              >
                {t.label}
              </Button>
            ))}
          </HStack>
          <SearchInput value={query} onChange={handleSearch} sx={{ flex: 1 }} />
        </HStack>
        <ResourceContent
          query={query}
          type={type}
          contentType={0}
          sx={{ height: "calc(100% - 36px)" }}
        />
      </DialogContent>
    </Dialog>
  );
};
export default ResourceManager;
