import React from "react";
import { TextField, TextFieldProps } from "@mui/material";
import Title from "../common/title";
import { Observer } from "mobx-react-lite";
interface TitleInputProps {
  title?: string;
  defaultValue?: string;
  onBlur?: (e) => void;
  id?: string;
}

const TitleInput: React.FC<TitleInputProps & TextFieldProps> = ({
  title,
  defaultValue,
  onBlur,
  id,
  ...rest
}) => {
  return (
    <Observer>
      {() => (
        <>
          <Title title={title} />

          <TextField
            id={id || "filled-hidden-label-small"}
            size="small"
            variant="filled"
            defaultValue={defaultValue}
            onBlur={onBlur}
            sx={{ width: 165, border: 0 }}
            autoComplete="off"
            InputProps={{
              sx: {
                height: 24,
                borderRadius: 1,
                background: "#F4F4F4",
                border: 0,
              },
            }}
            inputProps={{
              sx: {
                pt: 1,
                height: 24,
              },
            }}
            {...rest}
          />
        </>
      )}
    </Observer>
  );
};
export default TitleInput;
