import { App } from "./../../intance";
import { WidgetValue, ObjectId } from "@hc/engine";

export const ValueOptions = [
  { label: "值", value: "value" },
  { label: "变量", value: "param" },
  { label: "组件文本", value: "entity" },
  // {
  //   label: "选中状态",
  //   value: "entity-IsSelect",
  //   bool: true,
  // },
];

export const getSelectKey = (value: WidgetValue) => {
  const temps: string[] = [value.type];
  if (value.entityKey) {
    temps.push(value.entityKey);
  }
  return temps.join("-");
};

export function getEntityOptions(hasPage = false): {
  label: string;
  value: string;
  objectId?: ObjectId;
}[] {
  const options = [];
  if (hasPage) {
    options.push({ label: "页面", value: "0" });
  }
  for (const en of App.intance.Database.ModelSpace.Entitys) {
    if (!en.IsErase)
      options.push({
        label: en.Name,
        value: en.Id.toString(),
        objectId: en.ObjectId,
      });
  }
  return options;
}
