import { CMD } from "@hc/engine";
import { App } from "~/intance";

export class SelectAllCommand implements CMD.Command {
  exec() {
    App.intance.Editor.selectControl.SelectEntitys = [
      ...App.intance.Database.ModelSpace.Entitys.filter(
        (e) => e.IsPickable && e.Visiable
      ),
    ];
  }
}
