import { makeAutoObservable } from "mobx";
import { SkyboxMode } from "@hc/engine";
import { ProjectApi } from "~/apis";
import { getReq, ResponseStatus } from "~/utils/axios";
import { rightStore } from "./rightStore";

export class RootStore {
  private _projectId = 0;
  particleDisplay = true;
  private bgType = SkyboxMode.None;
  private _appIndexId: number;
  private _projectName = "";
  constructor() {
    makeAutoObservable(this);
  }
  get projectId() {
    return this._projectId;
  }
  set projectId(v: number) {
    this._projectId = v;
  }
  get BgType() {
    return this.bgType;
  }
  set BgType(n: SkyboxMode) {
    this.bgType = n;
  }
  get appIndexId() {
    return this._appIndexId;
  }
  set appIndexId(v: number) {
    this._appIndexId = v;
  }
  get projectName() {
    return this._projectName;
  }
  set projectName(v: string) {
    this._projectName = v;
  }
  async loadProject(projectId: number) {
    this.projectId = projectId;
    if (!projectId) return;
    const pdetail = await getReq(ProjectApi.detail + projectId);
    if (pdetail.code === ResponseStatus.Ok && pdetail.data?.info) {
      this.appIndexId = pdetail.data.info.preview_app_id;
      this.projectName = pdetail.data.info.name;
      const variable = pdetail.data.info.variable;
      if (variable) {
        rightStore.globalVarStore.setVariable(JSON.parse(variable));
      }
    }
  }
}

export const rootStore = new RootStore();
