import { MenuItem, Paper, Stack } from "@mui/material";
import { styled } from "@mui/material/styles";
import { observer } from "mobx-react-lite";
import React, { useState, useEffect } from "react";
import { useStores } from "~/contexts/store-context";
import useContextMenu from "~/hooks/useContextMenu";
import { message } from "~/utils/message";
import { updateQueryStringParameter } from "~/utils/url";
import DataList from "../dataList";
import FileModal from "./fileModal";
import MoveAppPanel from "./moveAppPanel";
import SharePanel from "./share";
import PublishModal from "~/components/publish/publishModal";
import { ECategory } from "~/components/resourceManager/types";
import SearchInput from "~/components/common/searchInput";
import { debounceCallback } from "~/utils/time";
const Item = styled(Paper)(({ theme }) => ({
  ...theme.typography.body2,
  height: 100,
  textAlign: "center",
  width: "100%",
  color: theme.palette.text.secondary,
  cursor: "pointer",
}));

const SceneTab: React.FC = () => {
  const [query, setQuery] = useState("");
  const { fileStore } = useStores();
  const [move, setMove] = useState(false);
  const [share, setShare] = useState(false);
  const [publish, setPublish] = useState(false);
  const [openMenu, closeMenu, MenuContainer] = useContextMenu((e) => {
    const clickData = getClickFile(e.target as HTMLInputElement);
    if (clickData)
      fileStore.SelectFile = { id: clickData[0], name: clickData[1] };
    else {
      fileStore.SelectFile = { id: 0, name: "" };
    }
  });

  const handleOpen = () => {
    fileStore.SelectFile = { id: 0, name: "" };
    handleRename();
  };
  const handleRename = () => {
    fileStore.openModal();
    closeMenu();
  };

  const handleChangeQuery = (evt) => {
    setQuery(evt.target.value);
  };
  useEffect(() => {
    debounceCallback(() => {
      fileStore.loadFileList(query);
    }, 500);
  }, [query]);
  const loadFile = (id: number) => {
    if (id === fileStore.currentFileId) return;
    fileStore.setLoadProcess(false);
    fileStore.load(id);
    const newUrl = updateQueryStringParameter(
      location.href,
      "id",
      id.toString()
    );
    history.replaceState(
      {
        path: newUrl,
      },
      "",
      newUrl
    );
    message.success("加载成功", { preventDuplicate: true });
  };

  const handleDelete = () => {
    if (fileStore.selectFile.id) {
      fileStore.deleteFile(fileStore.selectFile.id);
    }
    closeMenu();
  };

  const getSearchUrl = () => {
    const search = new URLSearchParams(location.search);
    search.set("id", fileStore.selectFile.id.toString());
    return search.toString();
  };

  const handleCopyUrl = () => {
    closeMenu();
    handleStartShare();
  };
  const handlePreview = () => {
    closeMenu();
    const searchUrl = getSearchUrl();
    window.open("/preview.html?" + searchUrl, "_blank");
  };

  const getClickFile = (el: HTMLElement): [number, string] | null => {
    let dataId = el.getAttribute("data-id");
    let dataName = el.getAttribute("data-name");
    if (dataId) {
      return [Number(dataId), dataName];
    }

    while (el && !el.hasAttribute("data-id")) {
      el = el.parentElement;
      if (el.id === "leftPanel") return null;
    }

    dataId = el.getAttribute("data-id");
    dataName = el.getAttribute("data-name");

    if (dataId) {
      return [Number(dataId), dataName];
    }
  };

  const handleCopy = async () => {
    closeMenu();
    const status = await fileStore.copy(fileStore.selectFile.id);

    if (status) {
      fileStore.loadFileList();
    }
  };
  const handleMove = () => {
    handleStartMove();
    closeMenu();
  };

  const handleStartMove = () => {
    setMove(true);
  };
  const handleEndMove = () => {
    setMove(false);
  };
  const handleStartShare = () => {
    setShare(true);
  };
  const handleEndShare = () => {
    setShare(false);
  };
  const handleOpenPublish = () => {
    closeMenu();
    setPublish(true);
  };
  const handleClosePublish = () => {
    setPublish(false);
  };

  return (
    <>
      <div>
        <Stack flexDirection="row" sx={{ height: "28px" }}>
          <SearchInput
            value={query}
            onChange={handleChangeQuery}
            placeholder="搜索场景"
            sx={{ flex: 1 }}
          />
        </Stack>
        <DataList
          list={fileStore.fileList}
          component={Item}
          onContextMenu={openMenu}
          onAddData={handleOpen}
          onDoubleClick={(f) => loadFile(f.id)}
          selectId={fileStore.currentFileId}
          componentProps={(f) => ({
            elevation: fileStore.getIsSelect(f.id) ? 5 : 0,
            "data-id": f.id,
            "data-name": f.title,
          })}
          loading={fileStore.loading}
          xs={12}
          addTitle="新建场景"
        />
      </div>
      {!!fileStore.selectFile.id && (
        <MenuContainer>
          <MenuItem onClick={handleRename}>重命名</MenuItem>
          <MenuItem onClick={handlePreview}>预览</MenuItem>
          <MenuItem onClick={handleCopy}>拷贝</MenuItem>
          <MenuItem onClick={handleMove}>移动</MenuItem>
          <MenuItem onClick={handleCopyUrl}>分享</MenuItem>
          <MenuItem onClick={handleOpenPublish}>发布</MenuItem>
          <MenuItem onClick={handleDelete}>删除</MenuItem>
        </MenuContainer>
      )}
      {move && (
        <MoveAppPanel
          target={fileStore.selectFile.id}
          open={move}
          onClose={handleEndMove}
        />
      )}
      {share && (
        <SharePanel
          target={fileStore.selectFile.id}
          open={share}
          onClose={handleEndShare}
        />
      )}
      {publish && (
        <PublishModal
          target={fileStore.selectFile.id}
          onClose={handleClosePublish}
          type="app"
          category={ECategory.File}
          targetObj={fileStore.fileList.find(
            (item) => item.id === fileStore.selectFile.id
          )}
        />
      )}
    </>
  );
};
export default observer(SceneTab);
