import { parse, ParseResult, parseExpression } from "@babel/parser";
import {
  File,
  Node,
  Program,
  objectExpression,
  objectProperty,
  identifier,
  stringLiteral,
  newExpression,
  memberExpression,
} from "@babel/types";
import { HCFiler, Viewer } from "@hc/engine";
import CloseIcon from "@mui/icons-material/Close";
import {
  Box,
  Dialog,
  DialogContent,
  DialogTitle,
  IconButton,
  Typography,
} from "@mui/material";
import React, { useRef, useState } from "react";
import { getComponent } from "~/utils/component";
import { RNDComponent } from "../common/rnd";

import { demoCode } from "../commonTab/defaultCode";
import CodeEditor from "./codeEditor";
import ComponentDataPanel from "./componentData";
import { IComponentData, IComponentProperty, TEditor } from "./types";
import generate from "@babel/generator";
import { useLeftStores } from "~/contexts/leftStoreContext";
import { message } from "~/utils/message";

interface EditorComponentModalProps {
  onClose: (evt, reasion?: string) => void;
  current: IComponentData;
}

interface IComponentProps {
  name: string;
  type: string;
  value: string;
  prop: string;
  remark?: string;
}

let needUpdataAst = true;
let ast: ParseResult<File>;
let component = null;

const EditorComponentModal: React.FC<EditorComponentModalProps> = ({
  onClose,
  current,
}) => {
  const editorRef = useRef<TEditor>(null);
  const [propertys, setPropertys] = useState<IComponentProps[]>([]);
  const errorRef = useRef<string[]>([]);
  const viewRef = useRef<Viewer>(null);
  const { commonStore } = useLeftStores();

  const getAst = () => {
    try {
      if (needUpdataAst) {
        needUpdataAst = false;
        const code = editorRef.current.getValue();
        ast = parse(code);
      }
      return ast;
    } catch (err) {
      console.error("err: ", err);
      return null;
    }
  };

  const updateProperyty = (props?: IComponentProps[], camera?: any[]) => {
    try {
      const code = editorRef.current.getValue();
      const ast = parse(code);
      const result = getComponentDataAst(ast.program);
      if (props) {
        for (const prop of result) {
          const oldProp = props.find((p) => p.prop === prop.prop);
          if (oldProp) {
            prop.name = oldProp.name;
          }
        }
      }
      setPropertys(result);
      //TODO:代码没问题在渲染
      const Component = getComponent(code);
      if (component) {
        component.destory();
      }
      component = new Component();
      if (!component.entitys?.length) return;

      //这个get 会自动添加到场景
      component.RenderObject;
      component.watchLoad().then(() => {
        if (camera) {
          const filer = new HCFiler(camera);
          viewRef.current.cameraManager.ArcRotateControl.readFile(filer);
        } else if (current.camera) {
          const filer = new HCFiler(current.camera);
          viewRef.current.cameraManager.ArcRotateControl.readFile(filer);
        } else {
          viewRef.current.zoomAll();
          viewRef.current.cameraManager.ArcRotateControl.Alpha = Math.PI / 4;
        }
      });
    } catch (err) {
      message.error("代码存在错误");
    }
  };

  const getComponentDataAst = (program: Program) => {
    try {
      const rows: IComponentProps[] = [];
      //@ts-ignore
      const methods = program.body?.[0].body.body;
      for (const b of methods) {
        //@ts-ignore
        if (b.type === "ClassMethod" && b.key.name === "getData") {
          //@ts-ignore
          const ObjExp: Node = b.body.body[0];
          if (ObjExp?.type === "ReturnStatement") {
            const argument = ObjExp.argument;
            //@ts-ignore
            for (const n of argument.properties) {
              rows.push({
                name: n.key.name ?? n.key.value,
                prop: n.key.name ?? n.key.value,
                value: n.value.value,
                type: typeof n.value.value,
              });
            }
          }
          break;
        }
      }
      return rows;
    } catch (err) {
      return [];
    }
  };

  const generateDemo = () => {
    editorRef.current.setValue(demoCode);
    updateProperyty();
  };

  const handleChangeCode = (errors: string[]) => {
    needUpdataAst = true;
    errorRef.current = errors;
  };

  const handleEditorMounted = () => {
    if (current && editorRef.current) {
      editorRef.current?.setValue(current.code);
      updateProperyty(current.propertys);
    }
  };

  const handleFormat = () => {
    editorRef.current.getAction("editor.action.formatDocument").run();
  };
  const handleApply = (camera: any[]) => {
    editorRef.current.getAction("editor.action.formatDocument").run();
    updateProperyty(propertys, camera);
  };

  const hasErrror = () => {
    return errorRef.current.length > 0;
  };
  const handleUpdatePropertyName = (data: IComponentProperty) => {
    setPropertys((ps) => {
      for (const p of ps) {
        if (p.prop === data.prop) {
          Object.assign(p, data);
        }
      }
      return [...ps];
    });
  };

  const getView = (v) => {
    viewRef.current = v;
  };

  const onDrop = (event) => {
    try {
      const json = event.dataTransfer.getData("text/plain");
      const data = JSON.parse(json);
      const code = editorRef.current.getValue();
      const ast = parse(code);
      // console.log("ast: ", ast);

      commonStore.assetMap.set(data.url, data.modelId);

      //@ts-ignore
      const methods = ast.program.body?.[0].body.body;

      for (const b of methods) {
        //@ts-ignore
        if (b.type === "ClassMethod" && b.key.name === "initComponents") {
          const ArrExp = b.body.body.find((n) => n.type === "ReturnStatement");
          if (ArrExp?.argument?.type === "ArrayExpression") {
            const argument = ArrExp.argument;
            argument.elements.push(
              newExpression(
                memberExpression(identifier("HC"), identifier("ImportEntity")),
                [
                  objectExpression([
                    objectProperty(identifier("url"), stringLiteral(data.url)),
                  ]),
                ]
              )
            );
            console.log("ast: ", ast);
            const output = generate(ast, {});
            editorRef.current.setValue(output.code);
          }
          break;
        }
      }
    } catch (err) {
      console.log("err: ", err);
    }
  };

  return (
    <Dialog
      fullWidth
      maxWidth="xl"
      onClose={onClose}
      open={true}
      PaperProps={{ sx: { height: "90vh" } }}
      // hideBackdrop
      // PaperComponent={RNDComponent}
      onContextMenu={(e) => e.stopPropagation()}
    >
      <DialogTitle
        className="draggable-dialog-title"
        sx={{
          display: "flex",
          justifyContent: "space-between",
          cursor: "move",
        }}
      >
        <Typography>模型封装</Typography>
        <IconButton aria-label="close" size="small" onClick={onClose}>
          <CloseIcon fontSize="small" />
        </IconButton>
      </DialogTitle>
      <DialogContent sx={{ display: "flex", overflow: "hidden" }}>
        <Box
          sx={{ width: "60%" }}
          onDrop={onDrop}
          onDragOver={(event) => event.preventDefault()}
          onDragEnd={(event) => event.preventDefault()}
        >
          <CodeEditor
            updateProperyty={updateProperyty}
            onChange={handleChangeCode}
            getEditor={(e) => (editorRef.current = e)}
            onMount={handleEditorMounted}
          />
        </Box>
        <Box sx={{ flex: 1, padding: 2 }}>
          <ComponentDataPanel
            getAst={getAst}
            getCode={() => editorRef.current?.getValue()}
            generateDemo={generateDemo}
            propertys={propertys}
            onClose={onClose}
            current={current}
            format={handleFormat}
            hasErrror={hasErrror}
            updatePropName={handleUpdatePropertyName}
            getView={getView}
            apply={handleApply}
          />
        </Box>
      </DialogContent>
    </Dialog>
  );
};
export default EditorComponentModal;
