import { CMD, HCFiler } from "@hc/engine";
import { App } from "~/intance";
import { copyText } from "~/utils/utils";

export class CopyCommand implements CMD.Command {
  exec() {
    const objects = App.intance.SelectObjects;
    if (objects.length > 0) {
      const filer = new HCFiler();
      filer.write(objects.length);
      for (const object of objects) {
        filer.writeObject(object);
      }
      const pt = App.intance.Editor.mouseCtrl.WorldPoint;
      copyText(
        JSON.stringify({
          pt: { x: pt?.x, y: pt?.y, z: pt?.z },
          data: filer.Data,
        })
      );
    } else {
      copyText("");
    }
  }
}
