import {
  SxProps,
  Stack,
  TextField,
  Typography,
  InputBaseComponentProps,
} from "@mui/material";
import React from "react";

interface LabelInputProps {
  value?: string | number;
  defaultValue?: string | number;
  label: string;
  onChange?: (e: React.ChangeEvent<HTMLInputElement>) => void;
  onBlur?: (e: React.ChangeEvent) => void;
  placeholder?: string;
  sx?: SxProps;
  endElement?: JSX.Element;
  name?: string;
}

const LabelInput: React.FC<LabelInputProps> = ({
  label,
  onChange,
  placeholder,
  value,
  defaultValue,
  sx = { marginBottom: 0.1 },
  onBlur,
  endElement,
  name,
}) => {
  return (
    <Stack direction="row" sx={sx} spacing={2} alignItems="center">
      <Typography sx={{ marginRight: 0.1, minWidth: "45px", fontSize: 14 }}>
        {label}
      </Typography>
      <TextField
        defaultValue={defaultValue}
        value={value}
        placeholder={placeholder}
        hiddenLabel
        variant="outlined"
        size="small"
        onChange={onChange}
        onBlur={onBlur}
        sx={{ padding: "2px" }}
        name={name}
      />
      {endElement}
    </Stack>
  );
};
export default LabelInput;
