import { FilePreviewer, Utils } from "@hc/engine";
import { LoadingButton } from "@mui/lab";
import IconAli from "~/components/common/iconAli";
import {
  Backdrop,
  Box,
  Button,
  CircularProgress,
  Dialog,
  DialogActions,
  DialogContent,
  Divider,
  MenuItem,
  Stack,
  IconButton,
} from "@mui/material";
import React, { useEffect, useState } from "react";
import { DirSourceType, FileApi, FileUploadApi } from "~/apis";
import {
  createFileDir,
  editorFile,
  getFileList,
  updateFile,
  updateFilePreview,
} from "~/apis/fileReq";
import { IFileReturnData, IUploadFile, IUploadReturnData } from "~/apis/types";
import PublishModal from "~/components/publish/publishModal";
import { useEditorRootContext } from "~/contexts/editorRootContext";
import { useLeftStores } from "~/contexts/leftStoreContext";
import { useStores } from "~/contexts/store-context";
import useContextMenu from "~/hooks/useContextMenu";
import { App } from "~/intance";
import { rootStore } from "~/stores/RootStore";
import {
  deleteReq,
  IResponseResult,
  postReq,
  putReq,
  ResponseStatus,
} from "~/utils/axios";
import { Bus } from "~/utils/bus";
import { message } from "~/utils/message";
import { debounceCallback } from "~/utils/time";
import { getFileUrl } from "~/utils/url";

import { LinearProgressWithLabel } from "../common/circularProgressWithLabel";
import CollapseContainer from "../common/collapseContainer";
import { NoneInput } from "../common/custom";
import DialogHeader from "../common/dialogHeader";
import DragCard from "../common/dragCard";
import SearchInput from "../common/searchInput";
import SelectDirList from "../common/selectDirList";
import { getDefaultCode } from "../commonTab/defaultCode";
import { IDataItemProps } from "../dataList/types";
import DataList from "./../dataList";
import { ECategory } from "~/components/resourceManager/types";
import { ELeftTab } from "../leftPanel/types";

export let filePreview: FilePreviewer;
let canvas: HTMLCanvasElement;
let files: File[];

//TODO:后端不能提供批量删除的接口。批量上传无法精确返回想要的结果

const FILES3D = ["obj", "gltf", "glb", "STL"];

//上传模型最大20M
const MAX_MODE_SIZE = 30 * 1024 * 1024;

const MAX_UPLOAD_COUNT = 50;

let ModelFiles: File[];
let AssetsFiles: File[];
let LoadedFiles: File[];
let ErrorFiles: File[];

const getNodeID = (el: HTMLElement) => {
  do {
    const id = el.getAttribute("data-nodeid");
    const type = el.getAttribute("data-nodetype");
    if (id) {
      return [id, type];
    }
    el = el.parentElement;
  } while (el);
  return [];
};

const getFileType = (name: string) => {
  return Utils.arrayLast(name.split("."));
};

let waitRes: (v: unknown) => void;

const wait = () => {
  return new Promise((res) => {
    waitRes = res;
  });
};

let isZip = false;
let currentFileId: number;

const apiCache = new Map<number, IFileReturnData[]>();

const ResourceTab: React.FC<{
  readonly?: boolean;
  toEditComponent?: () => void;
}> = ({ readonly = false, toEditComponent }) => {
  const [assets, setAssets] = useState<IDataItemProps[]>([]);
  const [selectId, setSelectId] = useState<number>(null);
  const [dirlist, setDirList] = useState<IFileReturnData[]>([]);
  const [loading, setLoading] = useState(false);
  const [renameModel, setRenamModel] = useState("");
  const [uploading, setUploading] = useState<{
    loading: boolean;
    value?: number;
    label?: string;
  }>({
    loading: false,
  });
  const [collkeys, setCollkeys] = useState<Set<number>>(new Set());
  const { fileStore } = useStores();
  const [query, setQuery] = useState("");
  const [publish, setPublish] = useState(false);
  const [openMenu, closeMenu, MenuContainer] = useContextMenu();
  const { changeLeftTab } = useEditorRootContext();
  const { commonStore } = useLeftStores();
  const [dirId, setDirId] = useState(null);
  const [startSelectDir, setStartSelectDir] = useState(false);

  const handleExpand = async (key: number, evt: React.MouseEvent) => {
    evt.stopPropagation();
    if (evt.button === 0) {
      if (collkeys.has(key)) {
        collkeys.delete(key);
      } else {
        collkeys.add(key);
        handleUpdateTree(Array.from(collkeys), false);
      }
      setCollkeys(new Set(collkeys));
    }
    fileStore.SelectedModels = [];
    setSelectId(key);
  };

  const updateDirList = async (
    key: number | number[],
    list: IFileReturnData[] = dirlist
  ) => {
    if (!Array.isArray(key)) {
      key = [key];
    }

    const rootDirs: IFileReturnData[] = [];
    const childrenDirs: IFileReturnData[] = [];
    for (const d of list) {
      if (d.pid === 0) {
        rootDirs.push(d);
      } else {
        childrenDirs.push(d);
      }
    }

    if (!query) {
      const kMap = new Map<number, IFileReturnData[]>();
      for (const k of key) {
        let list: IFileReturnData[];
        if (apiCache.has(k)) {
          list = apiCache.get(k);
        } else {
          list = await getFileList(DirSourceType.Model, k, query);
          if (list) apiCache.set(k, list);
        }
        if (!list) list = [];
        kMap.set(
          k,
          list.filter((l) => l.pid !== 0)
        );
      }

      for (const d of list) {
        if (kMap.has(d.id)) {
          d.children = kMap.get(d.id).map((l) => ({
            title: l.name.split(".")[0] ?? l.file_key,
            data: {
              key: "ImportEntity",
              url: getFileUrl(l),
              name: l.name.split(".")[0] ?? l.file_key,
              modelId: l.uuid,
            },
            preview: getFileUrl(l, true, false),
            id: l.id,
          }));
        }
      }

      setDirList([...list]);
    } else {
      for (const node of childrenDirs) {
        const pd = rootDirs.find((r) => r.id === node.pid);
        if (pd) {
          if (!pd.children) pd.children = [];

          pd.children.push({
            title: node.name.split(".")[0] ?? node.file_key,
            data: {
              key: "ImportEntity",
              url: getFileUrl(node),
              name: node.name.split(".")[0] ?? node.file_key,
              modelId: node.uuid,
            },
            preview: getFileUrl(node, true),
            id: node.id,
          });
        }
      }
      setDirList(rootDirs);
    }
  };

  const handleZipUpload = async () => {
    isZip = true;
    for (const file of files) {
      const formData = new FormData();
      formData.append("pid", selectId.toString());
      formData.append("pic_key", "icon.png");
      formData.append("source_type", DirSourceType.Model);
      formData.append("node_type", "file");
      formData.append("status", "1");
      formData.append("files", file);
      formData.append("name", file.name);
      formData.append("project_type", "3d");
      setUploading({
        loading: true,
        value: 0,
        label: `开始上传模型资源${file.name}`,
      });
      const result = await postReq<IFileReturnData[]>(
        FileUploadApi.upload,
        formData,
        {
          headers: {
            "Content-Type": "multipart/form-data",
          },
          transformRequest: [
            function (data) {
              return data;
            },
          ],
          onUploadProgress: (event) => {
            const v = (event.loaded / event.total) * 100;
            if (v === 100) {
              setUploading((d) => ({
                ...d,
                value: v,
                label: `${file.name}上传成功`,
              }));
            } else setUploading((d) => ({ ...d, value: v }));
          },
        }
      );

      if (result.code === ResponseStatus.Ok && result.data) {
        for (const d of result.data) {
          setUploading({
            loading: true,
            value: 0,
            label: `开始生成${d.file_key}预览`,
          });
          await filePreview.loadFile(App.prefix + getFileUrl(d));
          currentFileId = d.id;
          //更新预览图
          await updateFilePreview(d.id, async () =>
            filePreview.getPreviewUrl(200)
          );
          setUploading({
            loading: true,
            value: 100,
            label: `${d.file_key}预览已更新`,
          });
        }
      }
    }
    setUploading({
      loading: false,
    });
    apiCache.delete(selectId);
    handleUpdateTree();

    isZip = false;
  };

  const onFilePicked = async (evt: React.ChangeEvent<HTMLInputElement>) => {
    files = Array.from(evt.target.files);

    if (files.some((f) => f.name.endsWith(".zip"))) {
      await handleZipUpload();
      evt.target.value = null;
      return;
    }

    ModelFiles = [];
    AssetsFiles = [];
    for (const file of files) {
      if (file.size > MAX_MODE_SIZE) {
        message.error(
          file.name + "文件大小超过20M，目前单个上传文件体积不超过20M"
        );
        return;
      }
      if (FILES3D.some((t) => file.name.endsWith(t))) {
        ModelFiles.push(file);
      } else {
        AssetsFiles.push(file);
      }
    }

    if (ModelFiles.length > 0) {
      if (filePreview) {
        LoadedFiles = [];
        ErrorFiles = [];
        //先加载全部资源文件和单个模型
        filePreview.loadLocalFile({
          target: { files: [ModelFiles[0], ...AssetsFiles] },
        });
        setUploading({
          loading: true,
          value: 0,
          label: "预处理" + ModelFiles[0].name,
        });
        await wait();
        //然后依次加载剩余模型文件
        for (let i = 1; i < ModelFiles.length; i++) {
          filePreview.loadLocalFile({ target: { files: [ModelFiles[i]] } });
          setUploading({
            loading: true,
            value: 0,
            label: "预处理" + ModelFiles[i].name,
          });
          await wait();
        }
      }
    } else {
      message.warn(
        "请上传正确的模型文件和其依赖文件，目前支持OBJ,GLTF,GLB 3种",
        {
          autoHideDuration: 2000,
        }
      );
    }

    evt.target.value = null;
  };

  const parseAssets = (files: IUploadFile[]) => {
    const getPreviewUrl = (name: string) => {
      for (const file of files) {
        if (file.tag === "png" && file.name === name + ".png") {
          return file;
        }
      }
    };
    const promise = [];
    for (const file of files) {
      if (FILES3D.includes(file.tag)) {
        const preview = getPreviewUrl(file.name);
        promise.push(
          postReq(FileApi.create, {
            name: file.name,
            pId: selectId || 0,
            source_type: DirSourceType.Model,
            node_type: "file",
            fileId: file.ID,
            picId: preview.ID,
          })
        );
      }
    }

    Promise.all(promise)
      .then((res) => {
        message.success(
          `成功上传${LoadedFiles.length}个模型,失败${ErrorFiles.length}个`,
          { autoHideDuration: 5000 }
        );
      })
      .then((res) => {
        handleUpdateTree();
      })
      .finally(() => {
        setUploading({ loading: false });
      });
  };

  const handleAddDir = () => {
    closeMenu();
    setDirList((list) => {
      const newData = {
        name: "新建目录",
        node_type: "dir",
        id: -1,
        editor: true,
      };
      return [...list, newData];
    });
  };

  const isSelected = (fid: number) => {
    return !!fileStore.SelectedModels.find((ele) => ele.id === fid);
  };

  const handleUpdateTree = async (keys?: number[], isRefesh = true) => {
    if (isRefesh) setLoading(true);

    let list = isRefesh
      ? await getFileList(DirSourceType.Model, 0, query)
      : dirlist;

    list && updateDirList(keys ?? Array.from(collkeys), list);
    setDirList(list ?? []);
    setLoading(false);
  };

  const handleRenameNode = () => {
    closeMenu();
    if (!selectId) return;
    const node = dirlist.find((d) => d.id === selectId);
    if (node) {
      node.editor = true;
    }
    setDirList([...dirlist]);
  };

  const handleDeleteNode = async () => {
    closeMenu();
    if (!selectId) return;
    const result = await deleteReq(FileApi.delete, {
      data: { id: Number(selectId) },
    });

    if (result.code === ResponseStatus.Ok) {
      setSelectId(0);
      handleUpdateTree();
    }
  };
  const promiseArr = [];
  const deleteModel = () => {
    apiCache.clear();
    fileStore.SelectedModels.forEach((selectedModel) => {
      promiseArr.push(
        deleteReq(FileApi.delete, {
          data: { id: selectedModel.id },
        }).then((res) => {
          return res;
        })
      );
    });
    return Promise.all(promiseArr);
  };
  const handleDeleteModeNode = () => {
    closeMenu();
    // if (!selectModel) return;
    if (fileStore.SelectedModels.length < 1) return;
    deleteModel().then((res) => {
      handleUpdateTree();
    });
  };

  const handleEditorModel = () => {
    closeMenu();
    if (fileStore.SelectedModels.length > 0) {
      setStartSelectDir(true);
    }
  };

  const handleRenameModel = () => {
    closeMenu();
    setRenamModel(String(fileStore.SelectedModels[0].id));
  };

  const handleChangeModelName = (dirId) => {
    return async (evt: React.FocusEvent<HTMLInputElement>) => {
      const modeId = Number(renameModel);
      setRenamModel("");
      // if (!modeId || !evt.target.value || !selectId) return;
      if (!modeId || !evt.target.value) return;
      const result = await updateFile({
        id: modeId,
        name: evt.target.value,
        pid: dirId, //selectId,
      });
      if (result.code === ResponseStatus.Ok) {
        apiCache.delete(dirId);
        // apiCache.delete(selectId);
        handleUpdateTree();
      }
    };
  };

  const upload = async () => {
    //当模型文件都加载完毕后,一次性上传所有文件
    if (LoadedFiles.length + ErrorFiles.length === ModelFiles.length) {
      if (LoadedFiles.length === 0) {
        message.warn("模型上传失败，检查模型是否存在问题", {
          autoHideDuration: 2000,
        });
        setUploading({ loading: false });
        return;
      }

      let formData = new FormData();

      let count = 0;
      const timestap = Date.now();
      const returnfiles: IUploadFile[] = [];
      const allFiles = [...LoadedFiles, ...files];

      let sliceLen = Math.ceil(allFiles.length / MAX_UPLOAD_COUNT);

      for (let i = 0; i < allFiles.length; i++) {
        const f = allFiles[i];
        if (ErrorFiles.includes(f)) {
          continue;
        }
        formData.append(f.name, f);
        count++;
        if (count >= MAX_UPLOAD_COUNT || i === allFiles.length - 1) {
          setUploading({
            loading: true,
            value: 0,
            label: `开始上传模型资源${
              sliceLen > 1 ? "(" + sliceLen + ")" : ""
            }`,
          });

          const result = await postReq<IUploadReturnData>(
            FileUploadApi.uploadMulti + "?p=" + timestap,
            formData,
            {
              headers: {
                "Content-Type": "multipart/form-data",
              },
              transformRequest: [
                function (data) {
                  return data;
                },
              ],
              onUploadProgress: (event) => {
                const v = (event.loaded / event.total) * 100;
                if (v === 100) {
                  setUploading((d) => ({
                    ...d,
                    value: v,
                    label: "上传结束，等待结果",
                  }));
                } else setUploading((d) => ({ ...d, value: v }));
              },
            }
          );

          if (result.code === ResponseStatus.Ok) {
            returnfiles.push(...result.data.files);
          }

          formData = new FormData();
          count = 0;
          sliceLen--;
        }
      }

      parseAssets(returnfiles);
    }
  };

  const handleEndSelect = () => {
    setStartSelectDir(false);
  };

  useEffect(() => {
    if (!canvas) {
      canvas = document.createElement("canvas") as HTMLCanvasElement;
      canvas.width = 400;
      canvas.height = 400;
      // document.body.append(canvas);
      // canvas.style.position = "fixed";
      // canvas.style.right = "0";
      // canvas.style.top = "0";
      window["filePreview"] = filePreview = new FilePreviewer(canvas, true);
    }
  }, []);

  useEffect(() => {
    const dispose = filePreview.onProgress((e) => {
      const v = (e.loaded / e.total) * 100;
      setUploading((d) => ({ ...d, value: v }));
    });
    return () => {
      dispose();
    };
  }, []);
  useEffect(() => {
    return () => {
      fileStore.SelectedModels = [];
    };
  }, []);
  useEffect(() => {
    const disposes: (() => void)[] = [];

    disposes.push(
      filePreview.onLoaded(async (file: File) => {
        if (!file && isZip && currentFileId) {
          return;
        }

        const blob = await filePreview.getBlob();

        const pwFile = new File([blob], file.name + ".png");

        LoadedFiles.push(pwFile);

        if (waitRes) {
          waitRes(true);
          waitRes = null;
        }
        //当模型文件都加载完毕后,一次性上传所有文件
        await upload();
      }),
      filePreview.onError(({ file, message }) => {
        ErrorFiles.push(file);
        if (waitRes) {
          waitRes(true);
          waitRes = null;
        }

        //当模型文件都加载完毕后,一次性上传所有文件
        upload();
      })
    );

    return () => {
      disposes.forEach((d) => d());
    };
  }, [selectId]);

  const handleKeyDown = (e: KeyboardEvent) => {
    if (e.ctrlKey && e.code === "KeyA") {
      e.preventDefault();
      e.stopPropagation();
      fileStore.SelectedModels = [...assets];
    }
  };

  const handleChangeQuery = (evt) => {
    setQuery(evt.target.value);
  };

  // useEffect(() => {
  //   handleUpdateTree();
  // }, []);
  useEffect(() => {
    debounceCallback(() => {
      handleUpdateTree();
    }, 500);
  }, [query]);

  useEffect(() => {
    const clean = Bus.on("ApplyModel", () => {
      handleUpdateTree();
    });
    return () => {
      clean();
    };
  }, []);

  useEffect(() => {
    document.addEventListener("keydown", handleKeyDown);
    return () => {
      document.removeEventListener("keydown", handleKeyDown);
    };
  });

  const handleDir = async (data: IFileReturnData) => {
    let result: IResponseResult<any>;

    if (data.id === -1) {
      result = await createFileDir({
        name: data.name,
        sourceType: DirSourceType.Model,
      });
    } else {
      result = await editorFile(data);
    }

    if (result.code === ResponseStatus.Ok) {
      handleUpdateTree();
    }
  };

  const handleMouseDown = (a: IDataItemProps, evt: React.MouseEvent) => {
    evt.stopPropagation();
    if (!isSelected(a.id)) {
      if (evt.ctrlKey)
        fileStore.SelectedModels = [...fileStore.SelectedModels, a];
      else fileStore.SelectedModels = [a];
    }
  };

  const clear = () => {
    setSelectId(0);
    fileStore.SelectedModels = [];
  };
  const handleOpenPublish = () => {
    closeMenu();
    setPublish(true);
  };
  const handleClosePublish = () => {
    setPublish(false);
  };

  const selectDir = (did: number) => {
    setDirId(did);
  };
  const handleEditModel = () => {
    handleEndSelect();
    const urls = fileStore.SelectedModels.map((data) => {
      commonStore.assetMap.set(data.data.url, data.data.modelId);
      return `new HC.ImportEntity({url:"${data.data.url}"})`;
    });
    if (toEditComponent) {
      toEditComponent();
    } else changeLeftTab(ELeftTab.Component);
    commonStore.startEditor(dirId, getDefaultCode(urls.join(",\r")));
  };

  return (
    <>
      <Stack
        sx={{
          width: "100%",
          height: "100%",
        }}
      >
        <Stack flexDirection="row" sx={{ height: "28px" }}>
          <SearchInput
            value={query}
            onChange={handleChangeQuery}
            placeholder="搜索模型"
            sx={{ flex: 1 }}
          />
          <IconButton
            onClick={handleAddDir}
            aria-label="delete"
            color="primary"
            sx={{ width: 28, ml: 1 }}
          >
            <IconAli name="icon-3Da-tianjia3x" fontSize={28} />
          </IconButton>

          {/* <Button onClick={handleAddDir}>新建目录</Button> */}
        </Stack>
        <Box
          sx={{ height: "90%", overflow: "auto" }}
          onContextMenu={openMenu}
          onMouseDown={clear}
        >
          {dirlist.map(
            (dir) =>
              dir.node_type === "dir" && (
                <CollapseContainer
                  title={dir.name}
                  open={collkeys.has(dir.id)}
                  onExpand={handleExpand}
                  target={dir.id}
                  key={dir.id}
                  editor={dir.editor}
                  onBlur={(evt) => {
                    if (evt.target.value) dir.name = evt.target.value;
                    handleDir(dir);
                  }}
                  select={dir.id === selectId}
                >
                  <DataList
                    onContextMenu={openMenu}
                    list={dir.children ?? []}
                    component={DragCard}
                    componentProps={(f) => ({
                      elevation: isSelected(f.id) ? 5 : 1,
                      "data-nodeid": f.id,
                      "data-nodetype": "file",
                    })}
                    editorId={Number(renameModel)}
                    onChangeEnd={handleChangeModelName(dir.id)}
                    canPreview
                    onMouseDown={handleMouseDown}
                  />
                </CollapseContainer>
              )
          )}
        </Box>
        {/* <Box sx={{ height: "40%" }} onContextMenu={openMenu}>
          <ModelDirList
            selectId={selectId}
            onSelect={handleSelect}
            dirlist={dirlist}
            onUpdateTree={handleUpdateTree}
          />
        </Box> */}
        {/* <Box
          sx={{ height: "50%", overflow: "auto" }}
          onClick={(e) => {
            fileStore.SelectedModels = [];
          }}
        >
          <Divider sx={{ mb: 1 }} />
          {assets.length === 0 ? (
            <Typography>该目录下没有模型资源</Typography>
          ) : (
            <DataList
              onContextMenu={openMenu}
              list={assets}
              component={DragCard}
              componentProps={(f) => ({
                elevation: isSelected(f.id) ? 5 : 1,
                "data-nodeid": f.id,
                "data-nodetype": "file",
              })}
              editorId={Number(renameModel)}
              onChangeEnd={handleChangeModelName}
              onClick={onClick}
              canPreview
            />
          )}
        </Box> */}
        {!readonly && (
          <Box>
            <label htmlFor="contained-button-file">
              <NoneInput
                id="contained-button-file"
                multiple
                type="file"
                accept=".zip"
                onChange={(evt) => onFilePicked(evt)}
                disabled={!selectId}
              />
              <LoadingButton
                disabled={!selectId}
                variant="contained"
                fullWidth
                component="span"
                loading={uploading.loading}
                loadingIndicator={
                  <LinearProgressWithLabel
                    value={uploading.value}
                    label={uploading.label}
                    sx={{ height: "100%" }}
                  />
                }
              >
                模型上传
              </LoadingButton>
            </label>
            <Divider sx={{ mt: 1 }} />
          </Box>
        )}

        <Backdrop
          sx={{ color: "#fff", zIndex: (theme) => theme.zIndex.drawer + 1 }}
          open={loading}
          onClick={() => setLoading(false)}
        >
          <CircularProgress color="inherit" />
        </Backdrop>
      </Stack>
      {!readonly && (
        <MenuContainer>
          {fileStore.SelectedModels.length < 1 && (
            <MenuItem key="newDir" onClick={handleAddDir}>
              新建目录
            </MenuItem>
          )}
          {fileStore.SelectedModels.length < 1 &&
            selectId !== undefined &&
            selectId !== null &&
            selectId !== 0 && (
              <MenuItem key="rename" onClick={handleRenameNode}>
                重命名
              </MenuItem>
            )}
          {fileStore.SelectedModels.length < 1 &&
            selectId !== undefined &&
            selectId !== null &&
            selectId !== 0 && (
              <MenuItem key="delete" onClick={handleDeleteNode}>
                删除目录
              </MenuItem>
            )}
          {fileStore.SelectedModels.length === 1 && (
            <MenuItem key="renameModel" onClick={handleRenameModel}>
              重命名模型
            </MenuItem>
          )}
          {fileStore.SelectedModels.length > 0 && (
            <MenuItem key="deleteModel" onClick={handleDeleteModeNode}>
              删除模型
            </MenuItem>
          )}
          {fileStore.SelectedModels.length > 0 && (
            <MenuItem key="copyUrl" onClick={handleEditorModel}>
              封装模型
            </MenuItem>
          )}
          {fileStore.SelectedModels.length > 0 && (
            <MenuItem key="publish" onClick={handleOpenPublish}>
              发布
            </MenuItem>
          )}
        </MenuContainer>
      )}
      {!readonly && publish && (
        <PublishModal
          target={fileStore.SelectedModels[0].id}
          onClose={handleClosePublish}
          type="file"
          category={ECategory.Model}
          targetObj={fileStore.SelectedModels[0]}
        />
      )}
      {startSelectDir && (
        <Dialog
          open={startSelectDir}
          onClose={handleEndSelect}
          maxWidth="md"
          fullWidth
        >
          <DialogHeader onClose={handleEndSelect} title="选择物模型目录" />
          <DialogContent sx={{ overflow: "initial" }}>
            <SelectDirList
              sourceType={DirSourceType.Component}
              projectId={rootStore.projectId}
              onClick={selectDir}
              selectId={dirId}
            />
          </DialogContent>
          <DialogActions>
            <Button disabled={!dirId} onClick={handleEditModel}>
              前往封装
            </Button>
            <Button onClick={handleEndSelect}>取消</Button>
          </DialogActions>
        </Dialog>
      )}
    </>
  );
};
export default ResourceTab;
