import React, { useState } from "react";
import { Link, useNavigate } from "react-router-dom";

import { styled } from "@mui/material/styles";
import { Box, Grid, Button, TextField, Typography } from "@mui/material";

import Header from "~/components/header/project-header";
import SMenu from "~/components/SMenu";

const pathnameArr = window.location.pathname.split("/");
const defaultSelected = pathnameArr[pathnameArr.length - 1];
const arr = [
  {
    path: "/project",
    name: "project",
    title: "项目",
    icon: "icon-3Dicon_yingyongguanli",
  },
  {
    path: "/model",
    name: "model",
    title: "模型",
    icon: "icon-3Ddici",
  },
  {
    path: "/component",
    name: "component",
    title: "组件",
    icon: "icon-3Dwumoxing",
  },
  {
    path: "/collect",
    name: "collect",
    title: "收藏列表",
    icon: "icon-3Dshoucang",
  },
  {
    path: "/works",
    name: "works",
    title: "作品",
    icon: "icon-3Dicon_24_wodezuopin",
  },
  {
    path: "/purchased",
    name: "purchased",
    title: "已购买",
    icon: "icon-3Dgouwuche",
  },
  {
    path: "/deploy",
    name: "deploy",
    title: "项目部署",
    icon: "icon-3Dweb1",
  },
  {
    path: "/examine",
    name: "examine",
    title: "部署审核",
    role: "A000001",
    icon: "icon-3Dshenhetongguo",
  },
  // {
  //   path: "/recycle",
  //   name: "recycle",
  //   title: "回收站",
  //   icon: "icon-huishou",
  // },
];
const footerHeight = 280;

const SelfGrid = styled(Grid)(({ theme }) => ({
  display: "flex",
  justifyContent: "center",
  alignItems: "center",
  height: "70%",
  "& .logo": {
    width: "140px",
  },
  "& .pane-list": {
    width: "30%",
    display: "flex",
    padding: "10px 20px",
    flexDirection: "column",
    "& p": {
      width: "66px",
      height: "16px",
      fontSize: "15px",
      fontFamily: "Source Han Sans CN",
      fontWeight: "bold",
      color: "#424b54",
    },
    "& a": {
      fontSize: "12px",
      fontFamily: "Source Han Sans CN",
      fontWeight: "400",
      color: "#424b54",
      textDecoration: "none",
      marginTop: "10px",
    },
  },
  "& .code-pane": {
    width: "34%",
    display: "grid",
    gridTemplateColumns: "repeat(auto-fill, calc(100% / 2))",
    "& span": {
      fontSize: "12px",
      fontFamily: "Source Han Sans CN",
      color: "#424854",
      margin: "8px 0",
    },
    "& img": {
      width: "100px",
      height: "100px",
    },
  },
}));

const SelfButton = styled(Button)(({ theme }) => ({
  height: "30px",
  background: "#1890ff",
  fontSize: "14px",
}));

const List: React.FC = (props) => {
  const navigate = useNavigate();
  const [selected, setSelected] = useState(defaultSelected);

  const menuClick = (item) => {
    // console.log(item);
    const { path, name } = item;
    setSelected(name);
    navigate(`/list${path}`);
  };

  return (
    <Box sx={{ flexGrow: 1 }}>
      <Grid item xs={12}>
        <Header></Header>
      </Grid>
      <Grid
        columns={11}
        container
        spacing={0}
        sx={{ height: `calc(100vh - 48px)`, overflowY: "auto" }}
      >
        <Grid
          item
          xs={1}
          sx={{ borderRight: "1px solid #eee", height: "100%" }}
        >
          <Typography
            sx={{
              fontSize: "15px",
              fontWeight: "bold",
              padding: "10px 15px",
            }}
          >
            开发中心
          </Typography>
          <SMenu
            sx={{ height: "auto" }}
            dataList={arr}
            selected={selected}
            handleClick={menuClick}
          ></SMenu>
        </Grid>
        <Grid item xs={10} sx={{ height: "100%" }}>
          {props["children"]}
        </Grid>
      </Grid>
      <Grid
        container
        spacing={0}
        sx={{
          height: footerHeight,
          boxShadow: "0px 0px 18px 0px rgba(0, 0, 0, 0.3)",
        }}
      >
        <SelfGrid item xs={4} direction="column" container>
          {/* <Box
            sx={{
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
              margin: "15px 30px",
              flexDirection: "column",
            }}
          > */}
          <img className="logo" src="/static/images/logo.png" />
          <SelfButton
            sx={{ margin: "20px 0 15px" }}
            variant="contained"
            startIcon={<i className="iconfont icon-3Dkefu"></i>}
          >
            点我交流
          </SelfButton>
          <SelfButton
            variant="contained"
            startIcon={<i className="iconfont icon-3Dkefuxiaoxi"></i>}
          >
            我要留言
          </SelfButton>
          {/* </Box> */}
        </SelfGrid>
        <SelfGrid item xs={4} sx={{ justifyContent: "center" }}>
          <div className="pane-list">
            <p>关于我们</p>
            <a href="/">关于我们</a>
            <a href="/">用户协议</a>
            <a href="/">会员中心</a>
            <a href="/">联系我们</a>
          </div>
          <div className="pane-list">
            <p>常见问题</p>
            <a href="/">购买/支付</a>
            <a href="/">使用问题</a>
            <a href="/">关于账号</a>
            <a href="/">帮助中心</a>
          </div>
          <div className="pane-list">
            <p>友情链接</p>
            <a href="/">海创微联</a>
            <a href="/">采集接入</a>
            <a href="/">云平台</a>
            <a href="/">云组态</a>
          </div>
        </SelfGrid>
        <SelfGrid direction="column" item xs={4} container>
          <p>微信扫码关注我们</p>
          <div className="code-pane">
            <div>
              <span>公众号-惊喜不断</span>
              <img src="/static/images/img-web.jpg" />
            </div>
            <div>
              <span>服务号-商务合作</span>
              <img src="/static/images/img-job.jpg" />
            </div>
          </div>
        </SelfGrid>
        <SelfGrid
          container
          direction="column"
          sx={{ height: "30%", justifyContent: "center", fontSize: "12px" }}
          item
          xs={12}
        >
          <p style={{ margin: 0 }}>
            版权所有©2021 中海创科技（福建）集团有限公司 Copyright© 2021 Fujian
            HISTRON Technology Co., Ltd. All rights reserved
          </p>
          <p style={{ margin: 0 }}>
            地址：福建省福州市海西高新技术创业园二期18号楼
          </p>
        </SelfGrid>
      </Grid>
    </Box>
  );
};

export default List;
