import * as React from "react";
import Box from "@mui/material/Box";
import ResourceTab from "../resourceTab";
import CommonTab from "../commonTab";
import HcTabs from "~/components/common/hcTabs";
const AllResourceTab = () => {
  const [value, setValue] = React.useState(0);

  const handleChange = (event: React.SyntheticEvent, newValue: number) => {
    setValue(newValue);
  };
  const tabs = [
    {
      children: <ResourceTab toEditComponent={() => setValue(1)} />,
      index: 0,
      value: 0,
      label: "模型",
    },
    {
      children: <CommonTab />,
      index: 1,
      value: 1,
      label: "组件",
    },
  ];
  return (
    <>
      <Box sx={{ bgcolor: "background.paper", width: "100%", height: "100%" }}>
        <HcTabs
          tabs={tabs}
          value={value}
          onChange={handleChange}
          variant="fullWidth"
        ></HcTabs>
      </Box>

      {/* <Box sx={{ bgcolor: "background.paper", width: "100%", height: "100%" }}>
        <Tabs
          value={value}
          onChange={handleChange}
          indicatorColor="secondary"
          textColor="inherit"
          variant="fullWidth"
          aria-label="full width tabs example"
        >
          <Tab label="模型" />
          <Tab label="组件" />
        </Tabs>

        <TabPanel
          style={{ height: "calc(100% - 50px)" }}
          currentValue={value}
          value={0}
        >
          <ResourceTab toEditComponent={() => setValue(1)} />
        </TabPanel>
        <TabPanel
          style={{ height: "calc(100% - 50px)" }}
          currentValue={value}
          value={1}
        >
          <CommonTab />
        </TabPanel>
      </Box> */}
    </>
  );
};

export default AllResourceTab;
