import React from "react";

interface MaskingProps {
  onClick: () => void;
  zIndex?: number;
}

const Masking: React.FC<MaskingProps> = ({ onClick, zIndex }) => {
  return (
    <div
      onClick={onClick}
      style={{
        position: "fixed",
        left: 0,
        right: 0,
        bottom: 0,
        top: 0,
        margin: "auto",
        width: "100%",
        height: "100%",
        zIndex,
      }}
    ></div>
  );
};
export default Masking;
