import { IGradient, Utils } from "@hc/engine";
import { Typography, Slider, IconButton } from "@mui/material";
import React from "react";
import Range from "../common/range";
import { HStack } from "../common/stack";
import DeleteIcon from "@mui/icons-material/Delete";
import ColorPickInput from "../common/colorPickInput";
import { getRgba } from "~/utils/utils";
import { ColorResult } from "react-color";

interface GradientItemProps {
  gradient: IGradient;
  onChange: (gradient: IGradient) => void;
  onDelete: () => void;
}

const GradientItem: React.FC<GradientItemProps> = ({
  gradient,
  onChange,
  onDelete,
}) => {
  const handleChange = (k: string, v: string) => {
    const json = { ...gradient };
    if (k === "min") {
      json.value = v;
    } else {
      json.otherValue = v;
    }
    onChange(json);
  };
  const handleChangeSilder = (evt, v: number) => {
    onChange({ ...gradient, period: v });
  };

  const handleChangeColor = (c: ColorResult, key: string) => {
    const json = { ...gradient };
    let value = c.hex;
    if (c.rgb) {
      const color = Utils.Color4.FromArray([
        c.rgb.r / 255,
        c.rgb.g / 255,
        c.rgb.b / 255,
        c.rgb.a,
      ]);
      value = color.toHexString();
    }
    json[key] = value;
    onChange(json);
  };

  const renderRange = () => {
    switch (gradient.key) {
      case "Size":
        return (
          <Range
            range={{ min: gradient.value, max: gradient.otherValue }}
            onChange={handleChange}
          />
        );
      case "Color":
        return (
          <HStack>
            <ColorPickInput
              value={gradient.value}
              color={getRgba(gradient.value)}
              onChange={(c) => handleChangeColor(c, "value")}
            />
            <ColorPickInput
              value={gradient.otherValue}
              color={getRgba(gradient.otherValue)}
              onChange={(c) => handleChangeColor(c, "otherValue")}
            />
          </HStack>
        );
    }
  };

  return (
    <HStack alignItems="center">
      <Typography sx={{ mr: "2%" }}>渐变范围:</Typography>
      {renderRange()}
      <HStack alignItems="center" sx={{ width: "50%", ml: "2%" }}>
        <Typography sx={{ mr: "5%" }}>生命周期:</Typography>
        <Typography>0</Typography>
        <Slider
          size="small"
          aria-label="Small"
          min={0}
          max={1}
          step={0.01}
          sx={{ width: "50%", mx: "2%" }}
          valueLabelDisplay="auto"
          value={gradient.period}
          onChange={handleChangeSilder}
        />
        <Typography>1</Typography>
        <IconButton onClick={onDelete}>
          <DeleteIcon />
        </IconButton>
      </HStack>
    </HStack>
  );
};

export default GradientItem;
