import { Box, FormControlLabel, Radio, RadioGroup } from "@mui/material";
import { observer } from "mobx-react-lite";
import React from "react";
import { CommonSelectEntity } from "~/components/common/commonInteraction";
import { useInteractionStoresContext } from "~/contexts/interactionStoreContext";

const DisplayDetail: React.FC = () => {
  const store = useInteractionStoresContext();

  const detail = store.CurrentActionDetail;

  const handleChange = (evt) => {
    store.updateState(() => {
      detail.type = evt.target.value;
    });
  };

  return (
    <Box>
      <CommonSelectEntity value={detail} />
      <RadioGroup row onChange={handleChange} value={detail.type}>
        <FormControlLabel value="Show" control={<Radio />} label="显示" />
        <FormControlLabel value="Hidden" control={<Radio />} label="隐藏" />
        <FormControlLabel value="Toggle" control={<Radio />} label="切换" />
      </RadioGroup>
    </Box>
  );
};
export default observer(DisplayDetail);
