import { Box, Stack, Tab, Tabs, Typography } from "@mui/material";
import React, { useEffect, useState } from "react";
import { a11yProps } from "~/components/common/tabPanel";
import { FullContainer } from "~/components/fullContainer";
import Header from "~/components/header";
import tabs from "~/components/leftPanel/tabs";
import { EditorRootContext } from "~/contexts/editorRootContext";
import { ELeftTab } from "~/components/leftPanel/types";

const EditorLayout: React.FC<{ children: React.ReactNode }> = (props) => {
  const [leftTab, setLeftTab] = useState(ELeftTab.Scene);

  const handleChangeLeftTab = (e, v: number) => {
    setLeftTab(v);
  };

  return (
    <EditorRootContext.Provider
      value={{ leftTabIndex: leftTab, changeLeftTab: (v) => setLeftTab(v) }}
    >
      <Stack flexDirection="column" sx={{ height: "100%" }}>
        <Header />
        <Stack flexDirection="row" sx={{ height: "calc(100% - 48px)" }}>
          <Box
            sx={{
              display: "flex",
              flexDirection: "column",
              height: "100%",
              backgroundColor: "#FCFCFC",
              alignItems: "center",
            }}
          >
            <Tabs
              orientation="vertical"
              variant="scrollable"
              value={leftTab}
              onChange={handleChangeLeftTab}
              aria-label="basic tabs example"
              sx={{
                width: 50,
                background: "#FCFCFC",
              }}
              TabIndicatorProps={{
                sx: {
                  color: "red",
                },
              }}
            >
              {tabs.map(
                (model) =>
                  !model.isHide && (
                    <Tab
                      value={model.id}
                      key={model.id}
                      icon={
                        <model.icon
                          name={model.iconName}
                          fontSize={21}
                          color={leftTab === model.id ? "#0084FF" : "#424B54"}
                        />
                      }
                      label={model.title}
                      {...a11yProps(model.id)}
                      sx={{
                        padding: "12px 0",
                        minWidth: "50px",
                        maxWidth: "initial",
                      }}
                    />
                  )
              )}
            </Tabs>
          </Box>

          <Box
            flex={1}
            sx={{ display: "flex", flexDirection: "column", height: "100%" }}
            onContextMenu={(evt) => evt.preventDefault()}
          >
            {props.children}
          </Box>
        </Stack>
      </Stack>
    </EditorRootContext.Provider>
  );
};
export default EditorLayout;
