import { Entity } from "@hc/engine";
import {
  Box,
  InputAdornment,
  MenuItem,
  Stack,
  TextField,
  Typography,
} from "@mui/material";
import { observer } from "mobx-react-lite";
import React from "react";
import { CommonSelectEntity } from "~/components/common/commonInteraction";
import CommonSelect from "~/components/common/select";
import { HStack } from "~/components/common/stack";
import Vector3Panel from "~/components/common/vector3Panel";
import { useInteractionStoresContext } from "~/contexts/interactionStoreContext";
import { TypeOptions } from "./options";

const RotateActionDetail: React.FC = observer(() => {
  const store = useInteractionStoresContext();

  if (!store.CurrentActionDetail) return null;

  const detail = store.CurrentActionDetail;

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    store.updateState(() => {
      detail[e.target.name] = e.target.value;
    });
  };

  const handleSelect = (evt: React.ChangeEvent<HTMLInputElement>) => {
    store.updateState(() => {
      detail[evt.target.name] = evt.target.value;
    });
  };

  const handleChangePivot = (k: string, v: string) => {
    store.updateState(() => {
      detail.pivot[k] = v;
    });
  };
  const handleChangeAxis = (k: string, v: string) => {
    store.updateState(() => {
      detail.axis[k] = v;
    });
  };

  return (
    <Box>
      <CommonSelectEntity value={detail} />
      <HStack sx={{ my: 2 }}>
        <CommonSelect
          options={[
            { label: "逆时针", value: 1 },
            { label: "顺时针", value: 0 },
          ]}
          value={detail.dir}
          sx={{ flex: 1 }}
          name="dir"
          onChange={handleSelect}
        />
        <CommonSelect
          options={TypeOptions}
          value={detail.type}
          name="type"
          sx={{ flex: 1 }}
          onChange={handleSelect}
        />
        <TextField
          hiddenLabel
          variant="outlined"
          size="small"
          value={detail.angle}
          sx={{ flex: 1 }}
          name="angle"
          onChange={handleChange}
          InputProps={{
            endAdornment: <InputAdornment position="start">度</InputAdornment>,
          }}
        />
      </HStack>
      {/* <TextField
        size="small"
        fullWidth
        select
        value="无"
        label="动画"
        sx={{ my: 1 }}
      >
        {["无", "线性"].map((c) => (
          <MenuItem key={c} value={c}>
            {c}
          </MenuItem>
        ))}
      </TextField> */}
      <TextField
        value={detail.time}
        hiddenLabel
        variant="outlined"
        size="small"
        label="时间"
        name="time"
        onChange={handleChange}
        InputProps={{
          startAdornment: (
            <InputAdornment position="start">时间</InputAdornment>
          ),
          endAdornment: <InputAdornment position="start">毫秒</InputAdornment>,
        }}
      />
      <Typography variant="subtitle1">锚点</Typography>
      <Vector3Panel
        sx={{ flex: 1 }}
        vector={detail.pivot}
        onChange={handleChangePivot}
      />
      <Typography variant="subtitle1">旋转轴</Typography>
      <Vector3Panel
        sx={{ flex: 1 }}
        vector={detail.axis}
        onChange={handleChangeAxis}
      />
    </Box>
  );
});

export default RotateActionDetail;
