import { Entity } from "@hc/engine";
import {
  Box,
  FormControlLabel,
  InputAdornment,
  Radio,
  RadioGroup,
  TextField,
  Typography,
} from "@mui/material";
import { observer } from "mobx-react-lite";
import React from "react";
import { CommonSelectEntity } from "~/components/common/commonInteraction";
import VectorPanel from "~/components/common/vector3Panel";
import { useInteractionStoresContext } from "~/contexts/interactionStoreContext";
import { getScaleData } from "../config";

const ScaleDetail: React.FC = observer(() => {
  const store = useInteractionStoresContext();

  const detail = store.CurrentActionDetail;

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    store.updateState(() => {
      detail[e.target.name] = e.target.value;
    });
  };
  const handleChangeRadios = (e: React.ChangeEvent<HTMLInputElement>) => {
    store.updateState(() => {
      detail[e.target.name] = e.target.value === "1";
      const en = detail.objectId?.Object as Entity;
      if (!en) return;
      Object.assign(
        detail.scale,
        getScaleData(detail.objectId, e.target.value === "1")
      );
    });
  };
  const handleChangeVector = (k: string, v: string) => {
    store.updateState(() => {
      detail.scale[k] = v;
    });
  };
  const handleChangePivot = (k: string, v: string) => {
    store.updateState(() => {
      detail.pivot[k] = v;
    });
  };

  return (
    <Box>
      <CommonSelectEntity value={detail} />
      <RadioGroup
        row
        aria-labelledby="demo-controlled-radio-buttons-group"
        name="bySize"
        value={detail.bySize ? "1" : "2"}
        onChange={handleChangeRadios}
      >
        <FormControlLabel value="1" control={<Radio />} label="按尺寸" />
        <FormControlLabel value="2" control={<Radio />} label="按比例" />
      </RadioGroup>
      <VectorPanel
        sx={{ flex: 1 }}
        vector={detail.scale}
        onChange={handleChangeVector}
      />
      <Typography variant="subtitle1">锚点</Typography>
      <VectorPanel
        sx={{ flex: 1 }}
        vector={detail.pivot}
        onChange={handleChangePivot}
      />
      <TextField
        value={detail.time}
        hiddenLabel
        variant="outlined"
        size="small"
        name="time"
        onChange={handleChange}
        InputProps={{
          startAdornment: (
            <InputAdornment position="start">时间</InputAdornment>
          ),
          endAdornment: <InputAdornment position="start">毫秒</InputAdornment>,
        }}
      />
    </Box>
  );
});

export default ScaleDetail;
