import { CMD, GroupRecord } from "@hc/engine";
import { App } from "~/intance";
import { message } from "~/utils/message";

let index = 0;

export class Group implements CMD.Command {
  exec() {
    const entitys = App.intance.SelectEntitys;

    if (entitys.length === 0) {
      message.warn("未选择要编组的实体");
      return;
    }
    if (entitys.length === 1) {
      message.warn("编组至少需要2个实体");
      return;
    }

    const group = new GroupRecord();
    group.Name = "组" + index++;
    App.intance.Database.GroupTable.append(group);

    for (const en of entitys) group.entitys.push(en.ObjectId);
  }
}
