import { DataBindStore } from "./dataBind";
import { GlobalVarStore } from "./globalVarStore";
import { CloudServicesStore } from "./cloudServicesStore";
import { makeAutoObservable } from "mobx";

export class RightStore {
  globalVarStore = new GlobalVarStore();
  dataBindStore = new DataBindStore();
  cloudServicesStore = new CloudServicesStore();

  constructor() {
    makeAutoObservable(this);
  }
}

export const rightStore = new RightStore();
