import { makeAutoObservable, action } from "mobx";
import { removeToken } from "../utils/auth";
import { getAction } from "~/apis/manage";
import userManager from "../utils/oidc/oidc";
class UserStore {
  // private token = getToken();
  private userName = "";
  userCode = "";
  private userId = "";
  private userRole = [];
  // private role = "";
  constructor() {
    makeAutoObservable(this, { userCode: false });
  }
  // get Token() {
  //   return this.token;
  // }
  // set Token(token: string) {
  //   this.token = token;
  // }
  get UserName() {
    return this.userName;
  }
  set UserName(userName: string) {
    this.userName = userName;
  }
  get UserId() {
    return this.userId;
  }
  set UserId(userId: string) {
    this.userId = userId;
  }
  get UserRole() {
    return this.userRole;
  }
  set UserRole(userRole: Array<string | number>) {
    this.userRole = userRole;
  }
  // get Role() {
  //   return this.role;
  // }
  // set Role(role: string) {
  //   this.role = role;
  // }
  logOut() {
    removeToken();
    userManager.signoutRedirect();
  }
  @action
  getUserInfo() {
    let infoUrl = "editor/user/detail";
    getAction(infoUrl, {}).then((res) => {
      if (res && res.data && res.data.info) {
        this.userId = res.data.info.user_code;
        this.userName = res.data.info.user_name;
        this.userCode = res.data.info.user_code;
        let arr = [];
        if (res.data.info.Role && res.data.info.Role.length > 0) {
          for (let item of res.data.info.Role) {
            arr.push(item.role_code);
          }
        }
        this.userRole = arr;
      }
    });
  }
}

export const userStore = new UserStore();
