import { CMD, TransformMode } from "@hc/engine";
import {
  ListItemIcon,
  Menu,
  MenuItem,
  Toolbar,
  Typography,
} from "@mui/material";
import React, { useEffect, useState } from "react";
import { HeaderContext } from "~/contexts/header-context";
import { App } from "~/intance";
import { message } from "~/utils/message";

import UpDownButton from "../common/upDownButton";
import objectList from "../designTab/objectList";
import ResourceManager from "../resourceManager";
import ArrayPanel from "./arrayPanel";
import MarkPanel from "./markModal";

import ZoomInIcon from "@mui/icons-material/ZoomIn";
import AddShoppingCartIcon from "@mui/icons-material/AddShoppingCart";
interface TopToolBarProps {}

const Options = [
  {
    label: "不可操作",
    value: TransformMode.None,
    icon: "icon-3Dbukecaozuo",
  },
  {
    label: "移动",
    value: TransformMode.Translate,
    icon: "icon-3Dyidong",
  },
  {
    label: "旋转",
    value: TransformMode.Rotate,
    icon: "icon-3Dxuanzhuan",
  },
  {
    label: "缩放",
    value: TransformMode.Scale,
    icon: "icon-3Dsuofang",
  },
  {
    label: "转换",
    value: TransformMode.All,
    icon: "icon-3Dzhuanhuan",
  },
];

const TopToolBar: React.FC<TopToolBarProps> = (props) => {
  const [mode, setMode] = useState(TransformMode.Translate);
  const [openMark, setOpenMark] = useState(false);
  const [openRes, setOpenRes] = useState(false);
  const [openArray, setOpenArray] = useState(false);
  const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);

  const handleStartAdd = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget);
  };
  const handleEndAdd = () => {
    setAnchorEl(null);
  };

  const handleAddMark = () => {
    setOpenMark(true);
  };

  const handleClose = () => {
    setOpenMark(false);
  };
  const handleOpenRes = () => {
    setOpenRes(true);
  };

  const handleCloseRes = () => {
    setOpenRes(false);
  };

  const setTransfromMode = (mode) => {
    App.intance.Editor.transformControl.Mode = mode;
    setMode(mode);
  };

  const handleOpenShops = () => {
    handleOpenRes();
  };

  const handelAddEntity = (evt) => {
    const key = evt.currentTarget.getAttribute("data-key");
    if (key) {
      CMD.CommandWrap(() => {
        const en = App.intance.create({ type: key });
        App.intance.Editor.selectControl.SelectEntitys = [en];
      }, "创建物体");
    }

    handleEndAdd();
  };

  const openAdd = Boolean(anchorEl);

  return (
    <HeaderContext.Provider value={{ closeMarket: handleCloseRes }}>
      <Toolbar disableGutters variant="dense">
        {Options.map((o) => (
          <UpDownButton
            key={o.value}
            iconName={o.icon}
            title={o.label}
            color={mode === o.value ? "#075FB8" : undefined}
            onClick={() => setTransfromMode(o.value)}
            sx={{ ml: 1, mr: 1 }}
          />
        ))}
        <UpDownButton
          iconName="icon-3Dquanxuan1"
          title="全选"
          onClick={() => CMD.commandManager.ExecCommand("SELECTALL")}
          sx={{ ml: 1, mr: 1 }}
        />
        <UpDownButton
          iconName="icon-3Dicon_yingyongguanli"
          title="阵列"
          onClick={() => {
            if (!App.intance.SelectEntitys.length) {
              message.warn("请先选择要阵列的实体");
              return;
            }
            setOpenArray(true);
          }}
          sx={{ ml: 1, mr: 1 }}
        />
        <UpDownButton
          iconName="icon-3Dbiaozhu"
          title="标注"
          onClick={handleAddMark}
          sx={{ ml: 1, mr: 1 }}
        />
        <UpDownButton
          iconName="icon-3Dsheji"
          title="物体"
          onClick={handleStartAdd}
          sx={{ ml: 1, mr: 1 }}
        />
        <UpDownButton
          iconName="icon-3Dsucai"
          title="市场"
          onClick={handleOpenShops}
          sx={{ ml: 1, mr: 1 }}
        />
        {openMark && <MarkPanel onClose={handleClose} open={openMark} />}
        <ResourceManager onClose={handleCloseRes} open={openRes} />
        {openAdd && (
          <Menu
            id="long-menu"
            MenuListProps={{
              "aria-labelledby": "long-button",
            }}
            anchorEl={anchorEl}
            open={openAdd}
            onClose={handleEndAdd}
          >
            {objectList[0].children.map((option) => (
              <MenuItem
                key={option.key}
                onClick={handelAddEntity}
                data-key={option.key}
              >
                <ListItemIcon>
                  <option.icon name={option.iconName} />
                </ListItemIcon>
                <Typography variant="inherit" noWrap>
                  {option.title}
                </Typography>
              </MenuItem>
            ))}
          </Menu>
        )}
        <ArrayPanel open={openArray} onClose={() => setOpenArray(false)} />
      </Toolbar>
    </HeaderContext.Provider>
  );
};
export default TopToolBar;
