type UiData<T> = { [P in keyof T]: T[P] extends number ? string : T[P] };

export function convert2UiData<T extends Record<string, any>>(
  params: T,
  callback?: (n: number) => string
): UiData<T> {
  const newData: any = {};

  for (const k in params) {
    if (params.hasOwnProperty(k)) {
      if (typeof params[k] === "number") {
        newData[k] = callback ? callback(params[k]) : params[k].toString();
      } else {
        newData[k] = params[k];
      }
    }
  }
  return newData;
}

export function uiVector2Vector(
  {
    x,
    y,
    z,
  }: {
    x: string;
    y?: string;
    z?: string;
  },
  callback?: (n) => number
) {
  if (!callback) callback = (n) => n;
  return {
    x: callback(parseFloat(x) || 0),
    y: y === undefined ? undefined : callback(parseFloat(y) || 0),
    z: z === undefined ? undefined : callback(parseFloat(z) || 0),
  };
}

export function uiData2Data(
  data: Record<string, any>,
  callback?: (n) => number
) {
  if (!callback) callback = (n) => n;
  const newData: Record<string, number> = {};
  for (const k in data) {
    if (typeof data[k] === "string") newData[k] = callback(parseFloat(data[k]));
    else {
      newData[k] = data[k];
    }
  }
  return newData;
}
