import * as React from "react";
import Button from "@mui/material/Button";
import { styled } from "@mui/material/styles";
import Dialog from "@mui/material/Dialog";
import DialogTitle from "@mui/material/DialogTitle";
import DialogContent from "@mui/material/DialogContent";
import DialogActions from "@mui/material/DialogActions";
import IconButton from "@mui/material/IconButton";
import CloseIcon from "@mui/icons-material/Close";
import { Grid, Stack } from "@mui/material";
import Box from "@mui/material/Box";
import Paper from "@mui/material/Paper";
const BootstrapDialog = styled(Dialog)(({ theme }) => ({
  "& .MuiDialogContent-root": {
    padding: theme.spacing(2),
  },
  "& .MuiDialogActions-root": {
    padding: theme.spacing(1),
  },
}));

export interface DialogTitleProps {
  id: string;
  children?: React.ReactNode;
  onClose: () => void;
}

const BootstrapDialogTitle = (props: DialogTitleProps) => {
  const { children, onClose, ...other } = props;

  return (
    <DialogTitle sx={{ m: 0, p: 2, width: "850px" }} {...other}>
      {children}
      {onClose ? (
        <IconButton
          aria-label="close"
          onClick={onClose}
          sx={{
            position: "absolute",
            right: 8,
            top: 8,
            color: (theme) => theme.palette.grey[500],
          }}
        >
          <CloseIcon />
        </IconButton>
      ) : null}
    </DialogTitle>
  );
};
const Item = styled(Paper)(({ theme }) => ({
  // backgroundColor: theme.palette.mode === "dark" ? "#1A2027" : "#fff",
  ...theme.typography.body2,
  padding: theme.spacing(2),
  margin: theme.spacing(1),
  border: "1px solid rgba(0,0,0,0.05)",
  // color: theme.palette.text.secondary,
}));
interface CustomizedDialogsProps {
  open: boolean;
  title: string;
  footerName: string;
  handleClose: () => void;
}
const CustomizedDialogs: React.FC<CustomizedDialogsProps> = ({
  open,
  title,
  footerName,
  handleClose,
}) => {
  return (
    <div>
      <BootstrapDialog
        onClose={handleClose}
        aria-labelledby="customized-dialog-title"
        open={open}
        fullWidth={false}
        maxWidth={"md"}
      >
        <BootstrapDialogTitle
          id="customized-dialog-title"
          onClose={handleClose}
        >
          {title}
        </BootstrapDialogTitle>
        <DialogContent dividers sx={{ height: "450px" }}>
          <Box sx={{ height: "350px" }}>
            <Stack mb={1}>
              鼠标
              <Item elevation={0}>
                <Stack
                  direction="row"
                  justifyContent="space-between"
                  spacing={2}
                >
                  <div>点击左键拖拽：框选场景节点</div>
                  <div>按住中间键移动：旋转场景</div>
                  <div>滚动中间键：缩放场景</div>
                </Stack>
              </Item>
            </Stack>
            <Stack mb={1}>
              键盘
              <Item elevation={0}>
                漫游视角:
                <Item elevation={0}>
                  <Stack
                    direction="column"
                    justifyContent="space-between"
                    spacing={2}
                  >
                    <Stack direction="row" spacing={2}>
                      <div style={{ width: "200px" }}>↑：向上移动</div>
                      <div>↓：向下移动</div>
                    </Stack>
                    <Stack
                      direction="row"
                      justifyContent="space-between"
                      spacing={2}
                    >
                      <div>W：向前移动</div>
                      <div>S：向后移动</div>
                      <div>A：向左移动</div>
                      <div>D：向右移动</div>
                    </Stack>
                  </Stack>
                </Item>
                <Stack
                  direction="row"
                  justifyContent="space-between"
                  spacing={2}
                >
                  <div>Delete：删除</div>
                  <div>Ctrl+Z：撤销</div>
                  <div>Ctrl+Y：恢复</div>
                  <div>Ctrl+A：全选</div>
                  <div>F11：全屏缩放</div>
                </Stack>
              </Item>
            </Stack>
            <Stack mb={1}>
              鼠标 + 键盘
              <Item elevation={0}>
                <Stack
                  direction="row"
                  justifyContent="space-between"
                  spacing={2}
                >
                  <div>Alt+鼠标中间键：移动场景</div>
                </Stack>
              </Item>
            </Stack>
          </Box>
        </DialogContent>
        <DialogActions>
          <Button autoFocus onClick={handleClose}>
            {footerName}
          </Button>
        </DialogActions>
      </BootstrapDialog>
    </div>
  );
};
export default CustomizedDialogs;
