import * as React from "react";
import { Tabs, TabsProps, Tab, Box } from "@mui/material";
import { styled } from "@mui/material/styles";

interface TabPanelProps {
  children?: React.ReactNode;
  index: number;
  value: number;
}

function TabPanel(props: TabPanelProps) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      style={{ height: "calc(100% - 50px)" }}
      {...other}
    >
      {value === index && (
        <Box
          sx={{
            width: "100%",
            display: "flex",
            flexWrap: "wrap",
            height: "100%",
            padding: 1,
          }}
        >
          {children}
        </Box>
      )}
    </div>
  );
}

function a11yProps(index: number) {
  return {
    id: `simple-tab-${index}`,
    "aria-controls": `simple-tabpanel-${index}`,
  };
}
interface TabProps {
  children?: React.ReactNode;
  index: number;
  value: number;
  label: string;
}
interface HcTabsProps {
  tabs: TabProps[];
  value: number;
  bgColor?: string;
  variant?: string;
}
const HcTabs: React.FC<HcTabsProps & TabsProps> = ({
  tabs,
  value,
  bgColor,
  variant,
  ...rest
}) => {
  const CustomizedTab = styled(Tab)`
    background: #ffffff;
    min-height: 34px;
  `;
  const CustomizedTabs = styled(Tabs)`
    min-height: 34px;
    & .MuiTabs-indicator {
      background: #0084ff;
    }
    & .MuiTabs-flexContainer {
      background: ${bgColor || "#f5f5f5"};
      height: 34px;
    }
  `;
  return (
    <>
      <CustomizedTabs
        value={value}
        indicatorColor="secondary"
        textColor="inherit"
        variant={variant || "standard"}
        {...rest}
      >
        {tabs.map((tab) => (
          <CustomizedTab
            key={tab.index}
            label={tab.label}
            {...a11yProps(tab.index)}
          ></CustomizedTab>
        ))}
      </CustomizedTabs>
      {tabs.map((tab) => (
        <TabPanel key={tab.index} value={value} index={tab.index}>
          {tab.children}
        </TabPanel>
      ))}
    </>
  );
};
export default HcTabs;
