import { createContext, useContext } from "react";
import { ELeftTab } from "~/components/leftPanel/types";

export type IEditorRootState = {
  leftTabIndex: ELeftTab;
  changeLeftTab: (v: ELeftTab) => void;
};

export const EditorRootContext = createContext<IEditorRootState>(null);

export const useEditorRootContext = () => {
  return useContext(EditorRootContext);
};
