import { getToken, setToken } from "~/utils/auth";
import { userStore } from "~/stores/UserStore";
import { commonStore } from "~/stores/commonStore";
import userManager from "./oidc";
/**
 * 单点登录
 */
const init = (callback) => {
  console.log("-------单点登录开始-------");
  let token = getToken();
  let ast = getUrlParam("access_token");
  if (token) {
    loginSuccess(callback);
  } else {
    if (ast) {
      setToken(ast);
      setHref();
      loginSuccess(callback);
    } else {
      userManager.signinRedirect();
    }
  }
  console.log("-------单点登录结束-------");
};
const OIDC = {
  init: init,
};
function setHref() {
  let url = document.location.origin + "/list/project";
  document.location.href = url;
}
function getUrlParam(paraName) {
  let url = document.location.toString();
  let arrObj = url.split("#");
  if (arrObj.length > 1) {
    let arrPara = arrObj[1].split("&");
    let arr;

    for (let i = 0; i < arrPara.length; i++) {
      arr = arrPara[i].split("=");

      if (arr !== null && arr[0] === paraName) {
        return arr[1];
      }
    }
    return "";
  } else {
    return "";
  }
}

function loginSuccess(callback) {
  const str = window.location.href,
    index = str.indexOf("#"); //获取其地址
  let url = "";
  if (index !== -1) {
    url = str.substring(0, index);
  }
  if (window.location.pathname === "/") {
    window.location.href = url + "list/project";
  }
  userStore.getUserInfo();
  commonStore.getPrefix();
  callback();
}
export default OIDC;
