import React, { useRef, useEffect, useState } from "react";
import {
  Application,
  BaseDom,
  BaseGui,
  CameraType,
  CMD,
  IVector3,
  ParticleSystem,
  Utils,
} from "@hc/engine";
import { useEditorContext } from "./../../contexts/editor-context";
import {
  CLEAR,
  SELECT_DOM,
  SELECT_ENTITY,
  SELECT_GUI,
  UPDATE_DOM,
  UPDATE_GUI,
} from "~/reducers/editorReducers";
import { Box } from "@mui/material";
import { setAppIntance } from "~/intance";
import BottomPanel from "./bottom";
import { useStores } from "~/contexts/store-context";
import { Entity, AppConfig, SkyboxMode } from "@hc/engine";
import { UPDATE_ENTITY } from "./../../reducers/editorReducers";
import { ReportError } from "~/utils/errorMonitoring";
import { toJS } from "mobx";
import { HC2DGui } from "~/extension/Hc2DGui";
import "~/extension/HC2DDom";
import { HC2DTexture } from "~/extension/HC2DTexture";
import { message, ToasterKeys } from "~/utils/message";
import { HC2DDom } from "~/extension/HC2DDom";
import { parseParticle } from "~/utils/parse";
import { rootStore } from "~/stores/RootStore";
import { Bus } from "~/utils/bus";
import { MarkList } from "~/apis/types";
import { MarketApi } from "~/apis";
import { postReq, ResponseStatus } from "~/utils/axios";
import { ECategory } from "../resourceManager/types";
import {
  loadOnlineComponent,
  loadOnlineModel,
  updateUrl,
} from "~/utils/loadFile";
import ConfirmModal from "../common/confirm";
import BuyItemPanel from "../resourceManager/buyItem";
import { CommandEnum } from "~/commands/commandEnum";
import { isDebugBBL } from "~/utils/utils";

if (process.env.NODE_ENV === "development" || isDebugBBL())
  import("@babylonjs/inspector");

export let app: Application;

let position: IVector3;

const Editor: React.FC = () => {
  const canvasRef = useRef<HTMLCanvasElement>(null);
  const { fileStore } = useStores();
  const [worldPt, setWorldPt] = useState({ x: 0, y: 0, z: 0 });
  const [startBuy, setStartBuy] = useState({ open: false, shop: null });

  const { dispatch, editorState } = useEditorContext();

  const pullShop = (shop: MarkList) => {
    const type = shop.real_type as ECategory;
    if (type === ECategory.Project) {
      message.warn("项目素材不支持直接应用到场景");
      return;
    }
    postReq(
      MarketApi.pull,
      type === ECategory.File
        ? {
            project_id: rootStore.projectId,
            name: shop.product_name + Date.now(),
            id: shop.real_code,
          }
        : {
            pid: -1,
            name: shop.product_name + Date.now(),
            id: shop.real_code,
          }
    ).then((res) => {
      if (res.code === ResponseStatus.Ok) {
        loadOnline(type, res?.data?.id);
      }
    });
  };

  const loadOnline = (type: ECategory, id: number) => {
    if (!id) return;
    if (type === ECategory.Model) {
      if (id) loadOnlineModel(id, app, { position: position });
      message.success("应用模型成功");
    } else if (type === ECategory.Component) {
      if (id) loadOnlineComponent(id, app, { position: position });
      message.success("应用模型成功");
    } else if (type === ECategory.File) {
      fileStore.loadFileList().then((res) => {
        if (id) {
          fileStore.load(id);
          updateUrl(id);
          message.success("拉取应用成功");
        }
      });
    }
  };

  const addModel = (worldPt, object) => {
    if (object) {
      position = worldPt;
      if (object.key === "shop") {
        const shop = object.shop as MarkList;
        console.log("shop: ", shop);

        if (shop.isBuy) {
          //直接应用
          pullShop(shop);
        } else {
          //先购买
          setStartBuy({ open: true, shop });
        }
        return;
      }

      const option = {
        type: object.key,
        url: object.url,
        position: worldPt,
        loading: undefined,
        complete: undefined,
        name: object.name,
      };

      if (object.key === "particle") {
        const data = object.json;
        const particle = parseParticle(data);
        app.Database.ParticleSystemSpace.append(particle);
        !rootStore.particleDisplay && particle.stop();
        return null;
      }
      if (object.key === "background") {
        app.Viewer.sceneControl.Skybox = object.url;
        rootStore.BgType = app.Viewer.sceneControl.Mode = SkyboxMode.Image;
        fileStore.setLoadProcess(true);
        let per = 0;
        let step = 10;
        let timer = setInterval(() => {
          per += step;
          if (per >= 90) {
            step = 1;
          }
          if (per >= 98) {
            clearInterval(timer);
            return;
          }
          fileStore.setLoadProcess(true, per);
        }, 1000);
        app.Viewer.sceneControl.LoadObserable.addOnce((e) => {
          clearInterval(timer);
          fileStore.setLoadProcess(false);
        });
        return null;
      }

      if (object.url && object.key === "ImportEntity") {
        fileStore.setLoadProcess(true);
      }
      object.modelId && fileStore.addUseModel(object.modelId);

      if (object?.component) {
        fileStore.componentIds.add(object.id);
      }

      if (object.key === "2D") {
        return render2D(option);
      } else return app.create(option);
    }
  };
  const onDrop = (event: React.DragEvent<HTMLDivElement>) => {
    const { offsetX, offsetY } = event.nativeEvent;
    const worldPt = app.Viewer.screen2World({ x: offsetX, y: offsetY });
    const selectedModels = toJS(fileStore.SelectedModels);
    let lastEn: Entity | BaseGui | BaseDom;
    CMD.CommandWrap(() => {
      if (selectedModels.length < 1) {
        const json = event.dataTransfer.getData("text/plain");
        lastEn = addModel(worldPt, JSON.parse(json));
      } else {
        selectedModels.forEach((selectedModel) => {
          lastEn = addModel(worldPt, selectedModel.data);
        });
      }
      if (lastEn instanceof Entity)
        app.Editor.selectControl.SelectEntitys = [lastEn];
    }, "创建");
    fileStore.SelectedModels = [];
  };

  const render2D = (option) => {
    let type = "";
    switch (editorState.render2DType) {
      case "dom": {
        type = "HC2DDom";
        const gui = new HC2DDom(option);
        if (option.position) {
          const pos = app.Viewer.world2screen(option.position);
          gui.Position = pos;
        }
        gui.Name = option.name;
        app.Database.DomSpace.append(gui);
        return gui;
      }
      case "gui": {
        type = "HC2DGui";
        const gui = new HC2DGui(option);
        if (option.position) {
          const pos = app.Viewer.world2screen(option.position);
          pos.x = pos.x - app.Viewer.Width / 2;
          pos.y = pos.y - app.Viewer.Height / 2;
          gui.Position = pos;
        }
        gui.Name = option.name;
        if (app.SelectEntitys[0]) {
          gui.attachEntity(app.SelectEntitys[0].ObjectId);
        }
        app.Database.GuiSpace.append(gui);
        return gui;
      }
      case "texture": {
        type = "HC2DTexture";
        if (app.SelectEntitys.length === 0) {
          message.warn("请先选择要贴的实体");
          return null;
        }
        const texture = new HC2DTexture(option.url);
        app.Database.TextureTable.append(texture);
        texture.initTexture().then(() => {
          if (app.SelectEntitys[0]) {
            app.SelectEntitys[0].DiffuseTexture = texture.ObjectId as any;
          }
        });

        return null;
      }
    }
    if (!type) return null;
    option.type = type;
    return app.create(option);
  };

  const handleBuy = () => {
    postReq(MarketApi.buy, { productCode: startBuy.shop.product_code }).then(
      (res) => {
        if (res.code === ResponseStatus.Ok) {
          message.success("商品购买成功");
          Bus.trigger({ type: "BuySuccess", target: null });
          //直接应用
          pullShop(startBuy.shop);
        }
      }
    );
    handleEndBuy();
  };
  const handleEndBuy = () => {
    setStartBuy({ open: false, shop: null });
  };

  useEffect(() => {
    if (canvasRef.current) {
      app = new Application({
        canvas: canvasRef.current,
        editor: true,
        debug: isDebugBBL(),
        cameraRadius: 100,
      });
      setAppIntance(app);
      window["app"] = app;
    }
    return () => {
      app.destory();
      fileStore.reset();
    };
  }, []);

  //监听鼠标事件
  useEffect(() => {
    if (!app) return;
    const cleaners = [
      app.Editor.selectControl.on((event) => {
        if (event.type === "select") {
          if (event.entitys?.[0] instanceof BaseGui) {
            dispatch({
              type: SELECT_GUI,
              gui: event.entitys?.[0] as BaseGui,
            });
          } else if (event.entitys?.[0] instanceof BaseDom) {
            dispatch({
              type: SELECT_DOM,
              dom: event.entitys?.[0] as BaseDom,
            });
          } else if (event.entitys?.[0] instanceof Entity) {
            dispatch({
              type: SELECT_ENTITY,
              entity: event.entitys?.[0] as Entity,
            });
          } else {
            dispatch({
              type: CLEAR,
            });
          }
        }
      }),
      app.Editor.mouseCtrl.on("pointerMove", (event) => {
        if (event.worldPoint) setWorldPt(event.worldPoint);
      }),
      app.Editor.mouseCtrl.on("pointerDown", (event) => {
        if (ToasterKeys.has("freeCamera")) {
          app.Viewer.cameraManager.Type = CameraType.FreeCamera;
          const worldPoint = event.worldPoint;
          if (worldPoint) {
            app.Viewer.cameraManager.Control.Position = worldPoint;
            app.Viewer.cameraManager.Control.Target = {
              ...worldPoint,
              z: worldPoint.z - 10,
            };
          }

          message.close("freeCamera");
        }
      }),
      app.Editor.mouseCtrl.on("pointerDoubleTap", (event) => {
        app.Viewer.cameraManager.zoomAll();
      }),
    ];
    return () => {
      cleaners.forEach((c) => c());
    };
  }, [dispatch]);

  //监听更新和加载
  useEffect(() => {
    if (!app) return;
    let timer: NodeJS.Timeout;
    const cleaners = [
      app.on("update", (event) => {
        if (event.target) {
          timer && clearTimeout(timer);
          timer = setTimeout(() => {
            if (event.target instanceof BaseDom) {
              dispatch({ type: UPDATE_DOM });
            } else if (event.target instanceof BaseGui) {
              dispatch({ type: UPDATE_GUI });
            } else {
              if (!(event.target as Entity)?.isLight) {
                dispatch({ type: UPDATE_ENTITY });
              }
            }
          }, 100);
        }
      }),
      app.on("loading", (event) => {
        fileStore.setLoadProcess(true, event.percentage);
      }),
      app.on("load", (event) => {
        if (event?.error) {
          message.error(event?.target?.["Name"] + "加载出错", {
            preventDuplicate: true,
          });
        }
        fileStore.setLoadProcess(false);
      }),
      app.on("error", (event) => {
        ReportError(event.error?.stack, "发生错误");
      }),
      app.on("loadFile", (event) => {
        dispatch({ type: CLEAR });
      }),
      app.on("newFile", (event) => {
        dispatch({ type: CLEAR });
      }),
      Bus.on("Delete-File", (evnet) => {
        dispatch({ type: CLEAR });
      }),
    ];
    return () => {
      cleaners.forEach((c) => c());
    };
  }, [dispatch]);

  const handleGlobalKeyDown = (e: KeyboardEvent) => {
    switch (e.code) {
      case Utils.KeyCode.Delete: {
        CMD.commandManager.ExecCommand(CommandEnum.Delete);
        break;
      }
      case Utils.KeyCode.KeyA: {
        if (e.ctrlKey || e.metaKey) {
          e.stopPropagation();
          e.preventDefault();
          CMD.commandManager.ExecCommand(CommandEnum.SelectAll);
        }
        break;
      }
    }
  };
  const handleCanvasKeyDown = (e: KeyboardEvent) => {
    switch (e.code) {
      case Utils.KeyCode.KeyZ: {
        if (e.ctrlKey || e.metaKey) {
          e.stopPropagation();
          e.preventDefault();
          CMD.commandManager.ExecCommand("UNDO");
        }
        break;
      }
      case Utils.KeyCode.KeyY: {
        if (e.ctrlKey || e.metaKey) {
          e.stopPropagation();
          e.preventDefault();
          CMD.commandManager.ExecCommand("REDO");
        }
        break;
      }
      case Utils.KeyCode.KeyC: {
        if (e.ctrlKey || e.metaKey) {
          e.stopPropagation();
          CMD.commandManager.ExecCommand(CommandEnum.Copy);
        }
        break;
      }
      case Utils.KeyCode.KeyV: {
        if (e.ctrlKey || e.metaKey) {
          e.stopPropagation();
          CMD.commandManager.ExecCommand(CommandEnum.Paste);
        }
        break;
      }
      case Utils.KeyCode.KeyS: {
        if (e.ctrlKey || e.metaKey) {
          e.stopPropagation();
          e.preventDefault();
          fileStore.save().then((r) => r && message.success("保存成功"));
        }
        break;
      }
    }
  };

  useEffect(() => {
    if (canvasRef.current) {
      canvasRef.current.addEventListener("keydown", handleCanvasKeyDown);
    }
    return () => {
      if (canvasRef.current)
        canvasRef.current.removeEventListener("keydown", handleCanvasKeyDown);
    };
  }, []);

  //监听键盘事件
  useEffect(() => {
    if (!app) return;
    document.addEventListener("keydown", handleGlobalKeyDown);
    return () => {
      document.removeEventListener("keydown", handleGlobalKeyDown);
    };
  }, []);

  return (
    <Box
      sx={{
        flex: 1,
        position: "relative",
        display: "flex",
        flexDirection: "column",
      }}
    >
      <div
        onDrop={onDrop}
        onDragOver={(event) => event.preventDefault()}
        onDragEnd={(event) => event.preventDefault()}
        style={{
          flex: 1,
          minHeight: 0,
          position: "relative",
          overflow: "hidden",
        }}
      >
        <canvas
          style={{ outline: "none", width: "100%", height: "100%" }}
          ref={canvasRef}
        ></canvas>
        <div id="2d"></div>
        {app && app.Viewer.cameraManager.Type !== CameraType.ArcRotate && (
          <Box
            id="roamClose"
            style={{
              right: "20px",
              bottom: "0px",
              position: "absolute",
              pointerEvents: "none",
              textAlign: "center",
            }}
          >
            <Box
              style={{ color: "#FCFCFC", opacity: 0.8, marginBottom: "5px" }}
            >
              退出漫游
            </Box>
            <Box
              style={{ cursor: "pointer", pointerEvents: "all" }}
              onClick={() => {
                app.Viewer.cameraManager.Type = CameraType.ArcRotate;
                document.getElementById("roamClose").style.display = "none";
              }}
            >
              <img src="/static/images/close.png" width={50} />
            </Box>
          </Box>
        )}
      </div>
      <BottomPanel process={fileStore.loadingEntity} vector={worldPt} />
      {startBuy.open && (
        <ConfirmModal
          open={startBuy.open}
          onOk={handleBuy}
          onCancel={handleEndBuy}
          content={<BuyItemPanel shop={startBuy.shop} />}
          title="您即将购买"
        />
      )}
    </Box>
  );
};

export default Editor;
