import { CMD, Utils } from "@hc/engine";
import { App } from "~/intance";

export class Model2Zero implements CMD.Command {
  exec() {
    const models = App.intance.SelectEntitys;

    if (models.length > 0) {
      const box = new Utils.Box3();
      for (const m of models) {
        box.union(m.BoundingBox);
      }
      const center = box.centerWorld.negate();
      center.y = 0;
      for (const m of models) {
        m.translate(center);
      }
    }
  }
}
