import React, { useState } from "react";
import { TextField, TextFieldProps, InputAdornment } from "@mui/material";
import { styled } from "@mui/material/styles";

interface HcInputProps {
  title?: string;
  defaultValue?: string;
  onBlur?: (e) => void;
  id?: string;
  height?: number;
  startAdornment?: string;
  placeholder?: string;
}
const CustomizedTextField = styled(TextField)`
  .MuiInputLabel-shrink {
    top: 0px;
  }
`;
const HcInput: React.FC<HcInputProps & TextFieldProps> = ({
  title,
  defaultValue,
  onBlur,
  id,
  height,
  startAdornment,
  placeholder,
  ...rest
}) => {
  const onFocusInput = (e) => {
    e.target.select();
    window.addEventListener("keydown", onKeyDown); // 添加全局事件
  };
  const onBlurInput = (e) => {
    onBlur?.(e);
    window.removeEventListener("keydown", onKeyDown); // 销毁
  };
  // 键盘事件
  const onKeyDown = (e) => {
    switch (e.keyCode) {
      case 13: // enter事件
        document.getElementById(id ? id : "filled-hidden-label-small").blur();
        break;
    }
  };
  return (
    <>
      <CustomizedTextField
        defaultValue={defaultValue}
        label={title}
        id={id || "filled-hidden-label-small"}
        variant={"outlined"}
        placeholder={placeholder}
        onBlur={onBlurInput}
        onFocus={onFocusInput}
        fullWidth
        autoComplete="off"
        InputLabelProps={{
          sx: {
            top: "-10px",
          },
        }}
        InputProps={{
          startAdornment: startAdornment && (
            <InputAdornment position="start">{startAdornment}</InputAdornment>
          ),
          sx: {
            height: height || 35,
            border: 0,
          },
        }}
        inputProps={{
          height: 0,
        }}
        {...rest}
      />
    </>
  );
};
export default HcInput;
