import React, { useEffect, useState } from "react";
import UpDownButton from "../common/upDownButton";
import Menu from "@mui/material/Menu";
import MenuItem from "@mui/material/MenuItem";
import CustomizedDialogs from "./hotKey";
import LogPanel from "./log";
interface HelpProps {}
const Help: React.FC<HelpProps> = (HelpProps) => {
  const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);
  const [openLog, setOpenLog] = useState(false);
  const [openHotkey, setOpenHotkey] = useState(false);
  const open = Boolean(anchorEl);
  const handleHelp = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };
  const hotkey = () => {
    setOpenHotkey(true);
    handleClose();
  };
  const closeHotkey = () => {
    setOpenHotkey(false);
    handleClose();
  };
  return (
    <>
      <UpDownButton
        iconName="icon-3Dbangzhu"
        title="帮助"
        onClick={handleHelp}
      />
      <Menu
        id="basic-menu"
        anchorEl={anchorEl}
        open={open}
        onClose={handleClose}
        MenuListProps={{
          "aria-labelledby": "basic-button",
        }}
      >
        <MenuItem onClick={hotkey} sx={{ fontSize: "12px" }}>
          快捷键说明
        </MenuItem>
        {!location.host.includes("localhost") && (
          <MenuItem
            onClick={() => {
              setOpenLog(true);
              handleClose();
            }}
            sx={{ fontSize: "12px" }}
          >
            更新日志
          </MenuItem>
        )}
      </Menu>
      <CustomizedDialogs
        open={openHotkey}
        title="快捷键说明"
        footerName="我知道了"
        handleClose={closeHotkey}
      ></CustomizedDialogs>
      {openLog && <LogPanel onClose={() => setOpenLog(false)} />}
    </>
  );
};

export default Help;
