import { SxProps } from "@mui/system";
import React from "react";
import { Stack } from "@mui/material";
import VectorInput from "./vectorInput";
import { observer } from "mobx-react-lite";
import HcInput from "~/components/common/hcInput";
interface VectorPanelProps {
  vector: { x: string; y?: string; z?: string };
  sx?: SxProps;
  onBlur?: (e) => void;
  onChange: (k: string, v: string, name?: string) => void;
  name?: string;
}

const VectorPanel: React.FC<VectorPanelProps> = observer(
  ({ vector, sx = {}, onBlur, onChange, name }) => {
    if (!vector) return null;

    const sx2 = { ...sx, width: "47%", mt: 0.5, mr: 0.5 };

    return (
      <Stack direction="row" alignItems="center" flexWrap="wrap">
        <HcInput
          value={vector.x}
          onBlur={onBlur}
          name={name}
          startAdornment={vector.y === undefined ? "" : "x:"}
          sx={sx2}
          onChange={(e) => onChange("x", e.target.value, name)}
        />
        {vector.y !== undefined && (
          <HcInput
            value={vector.y}
            onBlur={onBlur}
            startAdornment="y:"
            sx={sx2}
            name={name}
            onChange={(e) => onChange("y", e.target.value, name)}
          />
        )}

        {vector.z !== undefined && (
          <HcInput
            value={vector.z}
            onBlur={onBlur}
            startAdornment="z:"
            sx={sx2}
            name={name}
            onChange={(e) => onChange("z", e.target.value, name)}
          />
        )}
      </Stack>
    );
  }
);

export default VectorPanel;
