import { DirSourceType } from "~/apis";
import { FileApi, FileUploadApi } from "./index";
import {
  getReq,
  myAxios,
  postReq,
  putReq,
  ResponseStatus,
} from "~/utils/axios";
import { IFileReturnData, IUploadFile, IUploadReturnData } from "./types";

export async function uploadFile({
  name,
  jsonData,
  sourceType,
  moduleIds,
  projectId,
  pid,
}: {
  name: string;
  sourceType: DirSourceType;
  jsonData: string;
  moduleIds: string;
  projectId: number;
  pid: number;
}) {
  const result = await postReq<IFileReturnData[]>(FileApi.create, {
    name,
    pic_key: "icon.png",
    source_type: sourceType,
    node_type: "file",
    json_data: jsonData,
    resources: moduleIds,
    project_id: projectId,
    pid,
    status: 1,
    project_type: "3d",
  });

  return result;
}
export async function updateFile(data: {
  id: number;
  name?: string;
  sourceType?: DirSourceType;
  json_data?: string;
  resources?: string;
  pid: number;
}) {
  const result = await putReq(FileApi.update, { ...data });

  return result;
}

export async function uploadPreview(
  name: string,
  callback: () => Promise<Blob>
) {
  const blob = await callback();
  const file = new File([blob], name + ".png");
  const fd = new FormData();
  fd.append("file", file);
  const result = await postReq<IUploadReturnData>(
    FileUploadApi.uploadMulti + "?p=" + Date.now(),
    fd
  );
  if (result.code !== ResponseStatus.Ok) {
    return null;
  }
  return result.data.files?.[0] as IUploadFile;
}

export async function getFileList(
  sourceType: DirSourceType,
  pid = 0,
  name?: string
): Promise<null | IFileReturnData[]> {
  const result = await getReq<IFileReturnData[]>(FileApi.list, {
    params: {
      source_type: sourceType,
      pid,
      project_type: "3d",
      name,
      status: 1,
    },
  });

  if (result.code === ResponseStatus.Ok) {
    return result.data ?? [];
  }
  return null;
}

export async function deleteFile(id: number) {
  const result = await myAxios.delete(FileApi.delete, { data: { id } });

  return result.code === ResponseStatus.Ok;
}

export async function updateFilePreview(
  id: number,
  callback: () => Promise<string>
) {
  try {
    const base64 = await callback();
    const result = await postReq(FileApi.pic, { id, data: base64 });
    return result.code !== ResponseStatus.Ok;
  } catch (err) {
    console.log("err: ", err);
    return false;
  }
}

export async function getFileDetail(id: number) {
  const result = await getReq(FileApi.detail + "/" + id);
  if (result.code === ResponseStatus.Ok) {
    return result.data?.info?.json_data;
  }
  return null;
}
export async function getFileInfo(id: number) {
  const result = await getReq(FileApi.detail + "/" + id);
  if (result.code === ResponseStatus.Ok) {
    return result.data?.info;
  }
  return null;
}

export async function createFileDir({
  name,
  sourceType,
}: {
  name: string;
  sourceType: DirSourceType;
}) {
  const result = await myAxios.post(FileApi.create, {
    name,
    pid: 0,
    source_type: sourceType,
    node_type: "dir",
    status: 1,
    project_type: "3d",
  });

  return result;
}

export async function editorFile(data: Partial<IFileReturnData>) {
  const result = await myAxios.put(FileApi.update, {
    ...data,
  });

  return result;
}
