import Oidc from "oidc-client";

//authServer 自己在环境变量中配置
const host: string = window.location.origin + window.location.pathname;
const authServer: string = SSO_HOST;
// console.log(Oidc, 'oidc')

const userManager: Oidc.UserManager = new Oidc.UserManager({
  authority: authServer, //server地址
  client_id: "product-app", //clientid
  client_secret: "xyz", //clientsecret
  post_logout_redirect_uri: `${host}`, //退出登录
  redirect_uri: host, //
  response_type: "id_token token", //Implicit
  scope: "email phone realname post",
  automaticSilentRenew: true,
  accessTokenExpiringNotificationTime: 10,
});

export default userManager;
