import React, { useState } from "react";
import {
  Box,
  SxProps,
  TableCell,
  TableCellProps,
  TextField,
  Typography,
} from "@mui/material";

interface EditorCellProps {
  value?: string;
  defaultValue?: string;
  onChange?: (evt) => void;
  onBlur?: (evt) => void;
  onFocus?: (evt) => void;
  sx?: SxProps;
  editor?: boolean;
  name?: string;
}

const EditorCell: React.FC<EditorCellProps & TableCellProps> = ({
  value,
  defaultValue,
  onChange,
  onBlur,
  onFocus,
  sx = {},
  name,
  editor = false,
  ...rest
}) => {
  const [editable, setEditable] = useState(editor);

  const handleDbclick = () => {
    setEditable(true);
  };

  const handleBlur = (e) => {
    setEditable(false);
    onBlur?.(e);
  };

  return (
    <TableCell
      onDoubleClick={handleDbclick}
      sx={{ minHeight: 10, height: "100%", width: 100, ...sx }}
      {...rest}
    >
      {editable ? (
        <TextField
          sx={{ height: 24, width: "100%" }}
          defaultValue={defaultValue}
          value={value}
          inputProps={{ sx: { padding: "0 8px" } }}
          onBlur={handleBlur}
          onChange={onChange}
          autoFocus
          onFocus={onFocus}
          name={name}
        />
      ) : (
        <Typography>{value || defaultValue}</Typography>
      )}
    </TableCell>
  );
};
export default EditorCell;
