export enum StoreName {
  Component = "component",
  Files = "files",
}

const StoreNames = [StoreName.Component, StoreName.Files];

export class InDb {
  opening = false;
  private _db: IDBDatabase;
  private static iDb = new InDb();
  private resFunctionList: ((...arg) => void)[] = [];
  static async getIdb() {
    await this.iDb.open();
    return this.iDb;
  }
  constructor() {
    //
  }
  open() {
    return new Promise((res, rej) => {
      if (this._db) {
        res(true);
        return;
      }

      this.resFunctionList.push(res);
      if (this.opening) {
        return;
      }
      this.opening = true;

      const request = indexedDB.open("HC", StoreNames.length);
      request.onerror = function (event) {
        console.log("打开数据库出错");
        rej(false);
      };
      request.onsuccess = (event) => {
        //@ts-ignore
        this._db = event.target.result;
        this._db.onclose = (ev) => {
          console.error("数据库已经被关闭");
          this._db = null;
          this.opening = false;
        };
        this.resFunctionList.forEach((f) => f());
        this.resFunctionList.length = 0;
      };
      request.onupgradeneeded = (event) => {
        console.log("升级");
        // 保存 IDBDataBase 接口
        //@ts-ignore
        this._db = event.target.result;

        for (let i = event.oldVersion; i < StoreNames.length; i++) {
          this._db.createObjectStore(StoreNames[i]);
        }
      };
    });
  }
  put(storeName: string, key: string | number, value: any) {
    const objectStore = this._db
      .transaction(storeName, "readwrite")
      .objectStore(storeName);
    objectStore.put(value, key);
  }
  get(storeName: string, key: string) {
    const transaction = this._db.transaction(storeName, "readwrite");
    return new Promise((res) => {
      const objectStore = transaction.objectStore(storeName);
      const req = objectStore.get(key);
      req.onsuccess = function (event) {
        res(event.target["result"]);
      };
    });
  }
  getAll(storeName: string): Promise<any[]> {
    const transaction = this._db.transaction(storeName, "readwrite");
    return new Promise((res) => {
      const objectStore = transaction.objectStore(storeName);
      const req = objectStore.getAll();
      req.onsuccess = function (event) {
        res(event.target["result"]);
      };
    });
  }
  clear(storeName: string) {
    const objectStore = this._db
      .transaction(storeName, "readwrite")
      .objectStore(storeName);

    objectStore.clear();
  }
  delete(storeName: string, key: string | number) {
    const objectStore = this._db
      .transaction(storeName, "readwrite")
      .objectStore(storeName);

    objectStore.delete(key);
  }
}
