import * as React from "react";
import TreeView from "@mui/lab/TreeView";
import TreeItem from "@mui/lab/TreeItem";

import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import ChevronRightIcon from "@mui/icons-material/ChevronRight";
import { Icon } from "@mui/material";
import { styled } from "@mui/material/styles";

import { userStore } from "~/stores/UserStore";

type StateType = {
  // open: boolean;
};
type MenuObj = {
  path?: string;
  name: string | number;
  title: string;
  role?: string;
  icon?: string;
};
type propType = {
  dataList?: Array<MenuObj>;
  handleClick?: Function;
  sx?: any;
  itemStyle?: any;
  defaultExpanded?: Array<string>;
  expanded?: Array<string>;
  onNodeToggle?: Function;
  onNodeSelect?: Function;
  selected?: string | Array<string>;
};
interface SMenu {
  state: StateType;
  props: propType;
}

const defaultStyle = {
  fontSize: "14px",
  height: "40px",
  width: "100%",
  display: "flex",
  alignItems: "center",
};

const SelfTreeItem = styled(TreeItem)(({ theme }) => ({
  "& .Mui-focused, & .Mui-selected, & .Mui-selected.Mui-focused": {
    // backgroundColor: "rgba(25, 118, 210, 1)",
    backgroundColor: "#fff",
  },
  "&.active, & .MuiTreeItem-content:hover": {
    // backgroundColor: "rgba(25, 118, 210, 0.2)", & .MuiTreeItem-content.Mui-selected, & .MuiTreeItem-content.Mui-selected:hover,
    backgroundColor: "#fff",
    color: "#1890ff",
  },
  "& .MuiTreeItem-content": {
    padding: "0 15px",
  },
}));

class SMenu extends React.Component {
  constructor(props) {
    super(props);
  }

  renderMenuItem = (menu) => {
    let { handleClick, itemStyle, selected } = this.props;
    let item = (
      <div style={itemStyle ? itemStyle : defaultStyle}>{menu.title}</div>
    );
    let menuName = menu.name;
    return (
      <SelfTreeItem
        className={selected === menuName ? "active" : ""}
        key={menuName}
        nodeId={typeof menuName === "string" ? menuName : String(menuName)}
        label={item}
        icon={menu.icon ? this.renderMenuIcon(menu.icon) : null}
        onClick={() => {
          handleClick && handleClick(menu);
        }}
      >
        {menu.children &&
          menu.children.map((item) => {
            return this.renderMenuItem(item);
          })}
      </SelfTreeItem>
    );
  };

  renderMenuIcon = (icon) => {
    return <i className={"iconfont " + icon}></i>;
  };

  onNodeToggle = (event: React.SyntheticEvent, nodeIds: string[]) => {
    let { onNodeToggle } = this.props;
    onNodeToggle && onNodeToggle(event, nodeIds);
  };

  isPermission = (role) => {
    let userRoleArr = userStore.UserRole;
    for (let item of userRoleArr) {
      if (role.indexOf(item) !== -1) {
        return true;
      }
    }
  };

  render() {
    let { dataList, sx, defaultExpanded, expanded } = this.props;
    let newList = dataList
      ? dataList.filter((item) => !item.role || this.isPermission(item.role))
      : [];
    return (
      <TreeView
        aria-label="file system navigator"
        defaultCollapseIcon={<ExpandMoreIcon />}
        defaultExpandIcon={<ChevronRightIcon />}
        sx={
          sx || {
            height: "100%",
            flexGrow: 1,
            maxWidth: 400,
            overflowY: "auto",
          }
        }
        defaultExpanded={defaultExpanded}
        expanded={expanded}
        onNodeToggle={this.onNodeToggle}
        // selected={selected}
      >
        {newList.map((item) => {
          return this.renderMenuItem(item);
        })}
      </TreeView>
    );
  }
}

export default SMenu;
