import { LOCAL_URL_REG } from "./errorMonitoring";

export default () => {
  if (!LOCAL_URL_REG.test(window.location.hostname)) {
    let headers = new Headers();
    headers.append("pragma", "no-cache");
    headers.append("cache-control", "no-cache");

    let init = {
      method: "GET",
      headers: headers,
    };

    fetch(location.origin + "/ver.txt", init).then((res) => {
      if (!res.ok) return;
      res.text().then((serverVersion) => {
        console.log("serverVersion: ", serverVersion);
        if (res.status)
          if (serverVersion) {
            console.info("%c localVersion: " + Build_VERSION, "color:#ff00ff");
            console.info("%c serverVersion: " + serverVersion, "color:#ff00ff");
            if (Build_VERSION !== serverVersion) {
              // const res = confirm("发现新版本，点击确认后升级");
              // if (res) {
              //   document.body.style.cssText = `
              //                       text-align: center;
              //                       display: flex;
              //                       align-items: center;
              //                       justify-content: center;
              //                       font-size: 30px;
              //                       flex-direction: column;
              //                       `;
              //   document.body.textContent = "新版本升级中!";
              //   window.location.reload();
              // }
              alert("您本地缓存版本已过期,请强制刷新浏览器(Shift+F5)");
            }
          }
      });
    });
  }
};
