import {
  Avatar,
  Dialog,
  DialogTitle,
  List,
  ListItem,
  ListItemAvatar,
  ListItemText,
} from "@mui/material";
import React, { useEffect, useState } from "react";

interface LogPanelProps {
  onClose: () => void;
}

let cacheLogs: string[];

const LogPanel: React.FC<LogPanelProps> = ({ onClose }) => {
  const [logs, setlogs] = useState<string[]>([]);

  useEffect(() => {
    if (cacheLogs) {
      setlogs(cacheLogs);
      return;
    }
    if (!location.host.includes("localhost")) {
      fetch("/log.txt")
        .then((res) => res.text())
        .then((str) => {
          const logs = str.split(/[\r\n]/).slice(0, 50);
          if (!cacheLogs) cacheLogs = logs;
          setlogs(logs);
        });
    }
  }, []);

  return (
    <Dialog onClose={onClose} open={true} maxWidth="md">
      <DialogTitle>更新日志</DialogTitle>
      <List sx={{ width: "400px", bgcolor: "background.paper" }}>
        {logs.map((log) => {
          const strs = log.split(",");
          if (
            !strs[1] ||
            ["fix", "feat", "refactor"].every(
              (s) => !strs[1].trim().startsWith(s)
            )
          ) {
            return;
          }

          return (
            <ListItem key={strs[2]}>
              <ListItemAvatar>
                <Avatar>{strs[0]}</Avatar>
              </ListItemAvatar>
              <ListItemText
                primary={strs[1]}
                secondary={strs[2] ? new Date(strs[2]).toLocaleString() : ""}
              />
            </ListItem>
          );
        })}
      </List>
    </Dialog>
  );
};
export default LogPanel;
