import { AppBar, Box, Stack, Toolbar } from "@mui/material";
import { styled } from "@mui/material/styles";
import { observer } from "mobx-react-lite";
import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import ProjectList from "~/components/header/projectList";
import PublishModal from "~/components/publish/publishModal";
import { ECategory } from "~/components/resourceManager/types";
import SharePanel from "~/components/sceneTab/share";
import { useStores } from "~/contexts/store-context";
import { message } from "~/utils/message";
import CodeInput from "../common/codeInput";

import UpDownButton from "../common/upDownButton";
import TopToolBar from "../toolBar";
import Help from "./help";
import Personal from "./personal";

interface HeaderProps {}

const Logo = styled("div")(({ theme }) => ({
  display: "flex",
  height: "46px",
  justifyContent: "center",
  alignItems: "center",
  fontSize: "18px",
  fontWeight: "bold",
  left: "5px",
  marginRight: "20px",
  marginLeft: "10px",
}));

const Header: React.FC<HeaderProps> = () => {
  const { fileStore } = useStores();
  const navigate = useNavigate();
  const [share, setShare] = useState(false);
  const [publish, setPublish] = useState(false);
  const [openCode, setOpenCode] = useState(false);
  const handleSave = () => {
    fileStore.save().then((r) => {
      r && message.success("保存成功");
    });
  };

  const handleExport = () => {
    fileStore.exportFile();
  };
  const handleImport = () => {
    fileStore.importFile();
  };
  const goBack = () => {
    navigate("/list/project");
  };
  const handleStartShare = () => {
    setShare(true);
  };
  const handleEndShare = () => {
    setShare(false);
  };
  const handleOpenPublish = () => {
    setPublish(true);
  };
  const handleClosePublish = () => {
    setPublish(false);
  };

  useEffect(() => {
    const callback = (e: KeyboardEvent) => {
      if (e.code === "Backquote" && e.altKey && e.ctrlKey) {
        setOpenCode(true);
      }
    };
    document.addEventListener("keydown", callback);
    return () => {
      document.removeEventListener("keydown", callback);
    };
  }, []);

  return (
    <AppBar
      position="static"
      sx={{ background: "#fff", color: "#424B54", height: 48, zIndex: 9 }}
    >
      <Toolbar
        disableGutters
        variant="dense"
        sx={{ justifyContent: "space-between" }}
      >
        <Stack flexDirection="row" alignItems="center">
          <Logo>
            <img src="/static/images/zhc.png" />
          </Logo>
          <ProjectList></ProjectList>
        </Stack>
        <TopToolBar />
        <Box
          sx={{
            display: "flex",
            justifyContent: "flex-end",
            marginRight: "10px",
          }}
        >
          <Help />
          <UpDownButton
            iconName="icon-3Dpingtaituihui"
            title="返回"
            onClick={goBack}
          />
          <UpDownButton
            iconName="icon-3Dfenxiang"
            title="分享"
            onClick={handleStartShare}
            disabled={!fileStore.currentFileId}
          />
          <UpDownButton
            iconName="icon-3Dfabu"
            title="发布"
            onClick={handleOpenPublish}
            disabled={!fileStore.currentFileId}
          />
          <UpDownButton
            iconName="icon-3Dbaocun"
            title="保存"
            onClick={handleSave}
          />
          <UpDownButton
            iconName="icon-3Dyulan"
            title="预览"
            disabled={!fileStore.currentFileId}
            onClick={() => {
              const w = window.open("about:blank");
              w.location.href = "/preview.html" + location.search;
            }}
          />
          <Personal />
          {share && (
            <SharePanel
              target={fileStore.currentFileId}
              open={share}
              onClose={handleEndShare}
            />
          )}
          {publish && (
            <PublishModal
              target={fileStore.currentFileId}
              onClose={handleClosePublish}
              type="app"
              category={ECategory.File}
              targetObj={fileStore.fileList.find(
                (item) => item.id === fileStore.currentFileId
              )}
            />
          )}
          {openCode && (
            <CodeInput open={openCode} onClose={() => setOpenCode(false)} />
          )}
        </Box>
      </Toolbar>
    </AppBar>
  );
};

export default observer(Header);
