import React, { useState } from "react";
import {
  Box,
  CardMedia,
  Grid,
  Popover,
  Skeleton,
  SxProps,
  TextField,
  Typography,
} from "@mui/material";
import ImageIcon from "@mui/icons-material/Image";
import { IDataItemProps } from "./types";
import { Observer } from "mobx-react-lite";
import PreviewBox from "../common/previewBox";
import { AppConfig } from "@hc/engine";
import { App } from "~/intance";
import { IMG_CDN } from "~/utils/url";
import BlurPopover from "../common/blurPopover";

import HcInput from "~/components/common/hcInput";
interface DataListProps {
  list: IDataItemProps[];
  onContextMenu?: (event: React.MouseEvent<HTMLElement, MouseEvent>) => void;
  component?: React.ElementType<any>;
  componentProps?: (l) => Record<string, any>;
  onAddData?: () => void;
  selectId?: string | number;
  editorId?: string | number;
  onChangeEnd?: (evt: React.FocusEvent<HTMLInputElement>) => void;
  onClick?: (a: IDataItemProps) => void;
  onMouseDown?: (a: IDataItemProps, evt: React.MouseEvent) => void;
  loading?: boolean;
  canPreview?: boolean;
  PreviewContainer?: React.ElementType;
  onDoubleClick?: (d: IDataItemProps) => void;
  xs?: number;
  addTitle?: string;
}

const CardStyles: SxProps = {
  width: "100%",
  textAlign: "center",
  height: "auto",
  minHeight: 124,
};
const MediaCardStyles: SxProps = {
  width: "100%",
  height: 0,
  paddingTop: "100%",
  backgroundSize: "100%",
};
const MediaCardStyles2: SxProps = {
  width: "100%",
  height: 120,
  backgroundSize: "100%",
};

let timer: NodeJS.Timeout;

const DataList: React.FC<DataListProps> = ({
  list,
  onContextMenu,
  component,
  componentProps,
  onAddData,
  editorId,
  onChangeEnd,
  onClick,
  loading,
  canPreview,
  PreviewContainer = "div",
  onDoubleClick,
  onMouseDown,
  xs = 6,
  addTitle,
  selectId,
}) => {
  const [anchorEl, setAnchorEl] = useState<HTMLButtonElement | null>(null);
  const [selectData, setSelectData] = useState(null);

  if (loading) {
    return (
      <Observer>
        {() => (
          <Grid container flexWrap="wrap" spacing={1} alignContent="baseline">
            {Array.from({ length: 4 }).map((v, index) => (
              <Grid item key={index} xs={xs}>
                <Skeleton variant="rectangular" width="100%" height={100} />
              </Grid>
            ))}
          </Grid>
        )}
      </Observer>
    );
  }

  const handleClick = (
    event: React.MouseEvent<HTMLButtonElement>,
    a: IDataItemProps
  ) => {
    if (editorId) {
      return;
    }

    const target = event.currentTarget;
    timer && clearTimeout(timer);
    timer = setTimeout(
      () => {
        if (canPreview && !event.ctrlKey && !event.metaKey) {
          setAnchorEl(target);
          setSelectData(a);
        }
        onClick?.(a);
      },
      onDoubleClick && (onClick || canPreview) ? 200 : 0
    );
  };

  const handleMouseDown = (a, evt) => {
    onMouseDown?.(a, evt);
  };

  const handleDbclick = (d: IDataItemProps) => {
    timer && clearTimeout(timer);
    onDoubleClick?.(d);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const getImage = (a: IDataItemProps) => {
    if (a.data.key === "background") {
      return AppConfig.Texture_URL + a.preview;
    }

    return App.prefix + a.preview;
  };

  return (
    <Observer>
      {() => (
        <>
          <Grid
            container
            flexWrap="wrap"
            spacing={1}
            onContextMenu={onContextMenu}
            alignContent="baseline"
          >
            {onAddData && (
              <Grid item xs={xs}>
                <Box
                  component={component || "div"}
                  sx={{
                    ...CardStyles,
                    display: "flex",
                    flexDirection: "column",
                    justifyContent: "center",
                    alignItems: "center",
                    height: "100%",
                    border: "2px dashed #ccc",
                    boxShadow: "none",
                  }}
                  onClick={onAddData}
                >
                  <CardMedia
                    component="img"
                    src={IMG_CDN + "space.png"}
                    sx={{ width: "50%" }}
                  />
                  {addTitle && (
                    <Typography sx={{ color: "#69aaff" }}>
                      {addTitle}
                    </Typography>
                  )}
                  {/* <IconButton>
                    <AddOutlinedIcon fontSize="large" />
                  </IconButton> */}
                </Box>
              </Grid>
            )}
            {list.map((a) => (
              <Grid item xs={xs} key={a.id}>
                <Box
                  component={component || "div"}
                  data={a.data ? JSON.stringify(a.data) : undefined}
                  sx={
                    selectId === a.id
                      ? {
                          ...CardStyles,
                          border: "1px solid #399ad7",
                          boxShadow: "none",
                          color: "#399ad7",
                        }
                      : CardStyles
                  }
                  {...(componentProps ? componentProps(a) : {})}
                  onClick={(event) => handleClick(event, a)}
                  onDoubleClick={() => handleDbclick(a)}
                  onMouseDown={(evt) => handleMouseDown(a, evt)}
                  draggable={!editorId}
                >
                  {a.preview ? (
                    <CardMedia
                      image={getImage(a)}
                      sx={xs === 12 ? MediaCardStyles2 : MediaCardStyles}
                    />
                  ) : (
                    <ImageIcon sx={{ fontSize: 80 }} />
                  )}
                  {editorId === a.id ? (
                    <HcInput
                      size="small"
                      defaultValue={a.title}
                      onBlur={onChangeEnd}
                      InputProps={{ sx: { height: 30 } }}
                      autoFocus
                    />
                  ) : (
                    <Typography
                      title={a.title}
                      variant={selectId === a.id ? "h6" : "subtitle1"}
                      sx={{
                        overflow: "hidden",
                        whiteSpace: "nowrap",
                        textOverflow: "ellipsis",
                      }}
                    >
                      {a.title}
                    </Typography>
                  )}
                </Box>
              </Grid>
            ))}
          </Grid>
          {canPreview && (
            <BlurPopover
              open={Boolean(anchorEl)}
              anchorEl={anchorEl}
              onClose={handleClose}
              anchorOrigin={{
                vertical: "bottom",
                horizontal: "right",
              }}
              onContextMenu={(evt) => {
                evt.stopPropagation();
                evt.preventDefault();
              }}
            >
              <Box
                component={PreviewContainer}
                sx={{ p: 1, width: 300, height: 300 }}
              >
                <PreviewBox data={selectData?.data} />
              </Box>
            </BlurPopover>
          )}
        </>
      )}
    </Observer>
  );
};
export default DataList;
