import { Grid } from "@mui/material";
import React, { useEffect, useState } from "react";
import { MarkList, MarkReturnData } from "~/apis/types";
import { useHeaderContenxt } from "~/contexts/header-context";
import { commonStore } from "~/stores/commonStore";
import { fileStore } from "~/stores/fileStore";
import { rootStore } from "~/stores/RootStore";
import { userStore } from "~/stores/UserStore";
import { getReq, ResponseStatus } from "~/utils/axios";
import { Bus } from "~/utils/bus";
import { previewChannel } from "~/utils/previewChannel";
import { debounceCallback } from "~/utils/time";

import EmptyPanel from "../common/empty";
import ApplyShopPanel from "./applyShop";
import ContentItem, { CollectStatus } from "./contentItem";
import { EMarketType } from "./types";

interface ContentListProps {
  trade: string;
  category: string;
  xs?: number;
  api: string;
  type: EMarketType;
  query: string;
}

const ContentList: React.FC<ContentListProps> = ({
  trade,
  category,
  xs = 4,
  api,
  type,
  query,
}) => {
  const [list, setList] = useState<MarkList[]>([]);
  const [applyInfo, setApplyInfo] = useState({
    open: false,
    info: null as MarkList,
  });
  const [BuyList, setBuyList] = useState<string[]>([]);
  const [CollectList, setCollectList] = useState<string[]>([]);
  const { closeMarket } = useHeaderContenxt();

  const update = async (query = "") => {
    let params =
      trade === "0"
        ? {
            category,
            name: query,
            limit: 100,
            page: 1,
            Size: 100,
            Current: 1,
          }
        : {
            category,
            trade,
            name: query,
            limit: 100,
            page: 1,
            Size: 100,
            Current: 1,
          };
    let res = await getReq<MarkReturnData>(api, {
      params: params,
    });

    if (res.code === ResponseStatus.Ok && res.data?.list) {
      setList(res.data.list);
      setBuyList(res.data.Boughten ?? []);
      setCollectList(res.data.Collects ?? []);
    } else {
      setList([]);
      setBuyList([]);
      setCollectList([]);
    }
  };

  const isBuy = (shop: MarkList) => {
    if (type === EMarketType.Buyed) {
      return true;
    }

    if (shop.possessor_code === userStore.userCode) {
      return null;
    }

    return (BuyList && BuyList.includes(shop.product_code)) || !!shop.is_bought;
  };
  const isCollect = (shop: MarkList) => {
    if (type === EMarketType.Coll) {
      return CollectStatus.True;
    }
    if (type === EMarketType.Buyed) {
      return CollectStatus.None;
    }

    if (shop.possessor_code === userStore.userCode) {
      return CollectStatus.Disable;
    }

    return CollectList && CollectList.includes(shop.product_code)
      ? CollectStatus.True
      : CollectStatus.False;
  };

  const applyStart = (shop: MarkList) => {
    setApplyInfo({ open: true, info: shop });
  };

  const applyend = () => {
    setApplyInfo({ open: false, info: null });
  };

  useEffect(() => {
    update(query);
  }, [trade]);
  useEffect(() => {
    debounceCallback(() => {
      update(query);
    }, 300);
  }, [api, query]);

  useEffect(() => {
    const dispose = Bus.on("BuySuccess", () => {
      update();
    });
    return () => {
      dispose();
    };
  }, []);

  useEffect(() => {
    const callback = (evt) => {
      if (evt?.data.type === "update-list") {
        update();
      } else if (evt?.data.type === "apply-shop") {
        applyStart(evt?.data.shop);
      }
    };
    previewChannel.addEventListener("message", callback);
    return () => {
      previewChannel.removeEventListener("message", callback);
    };
  }, []);

  return (
    <>
      {list.length === 0 && <EmptyPanel />}
      <Grid container spacing={1}>
        {list.map((l, index) => (
          <Grid item xs={xs} key={l.record_id || index}>
            <ContentItem
              shop={l}
              applyStart={applyStart}
              update={update}
              isBuy={isBuy(l)}
              isCollect={isCollect(l)}
            />
          </Grid>
        ))}
      </Grid>
      <ApplyShopPanel
        open={applyInfo.open}
        shop={applyInfo?.info}
        onClose={applyend}
        onCloseMark={closeMarket}
        fileStore={fileStore}
        commonStore={commonStore}
        defaultProjectId={rootStore.projectId}
      />
      {/* <Pagination count={10} color="primary" /> */}
    </>
  );
};
export default ContentList;
