import {
  Utils,
  ObjectId,
  Entity,
  Interaction,
  BaseWidget,
  ISetParam,
  CompareType,
  WidgetValue,
  HCFactory,
} from "@hc/engine";
import {
  action,
  computed,
  makeAutoObservable,
  observable,
  runInAction,
  toJS,
  when,
} from "mobx";
import { InteractionConfig } from "~/components/interaction/config";
import { EventLabel } from "~/components/interaction/dataList";
import { App } from "~/intance";
import { fileStore } from "./fileStore";

let idIndex = 0;
let confIndex = 0;

export type RotateDir = -1 | 1;

export interface IInterActDataOption {
  [key: string]: any;
}

export interface IInteractionActionOption {
  label: string;
  id: string;
  type: string;
  data: IInterActDataOption[];
  target?: boolean;
  param?: boolean;
}

export type IInteractionCond = {
  isAll: boolean;
  name: string;
  id: string;
  conditions: IInteractionCondItem[];
  actions: IInteractionActionOption[];
};

export type IInteractionCondItem = {
  value1: WidgetValue;
  value2: WidgetValue;
  type: CompareType;
  id: string;
};

export interface IInteractionOption {
  label: string;
  id: string;
  type: Interaction.TriggerType;
  objectId: ObjectId<Interaction.Interaction>;
  conditions: IInteractionCond[];
  actions: IInteractionActionOption[];
}

export enum EditorIntMode {
  Modal = 1,
  List = 2,
}

/** */
export class InteractionStore {
  tabIndex = 0;
  interactions: IInteractionOption[] = [];
  realInteractions: IInteractionOption[] = [];
  entityInteractions: Record<number, IInteractionOption[]> = {};
  currentEventIndex = -1;
  currentActionIndex = -1;
  currentActionDetailIndex = -1;
  currentConditionIndex = -1;
  currentCompareIndex = -1;
  tempCondition: IInteractionCond = {
    isAll: true,
    name: "情形",
    conditions: [],
    actions: [],
    id: "-1",
  };
  newAddActionId = "";
  mode: EditorIntMode = EditorIntMode.List;
  selectEntity: Entity = null;
  private useReal = true;
  private _step = 0;
  private _portalContainer: HTMLElement = null;
  private _portalContainer3: HTMLElement = null;
  private _isEditor = false;

  constructor() {
    makeAutoObservable(this, { Id: false, mode: false });

    when(
      () => !!App.intance,
      () => {
        App.intance.on("loadFile", () => {
          const ints = App.intance.Database.InteractionTable.objects;
          this.load(ints);
        });
        App.intance.on("newFile", () => {
          this.reset();
        });
        App.intance.Editor.selectControl.on((event) => {
          if (event.type === "select") {
            runInAction(() => {
              if (
                event.entitys?.length === 1 &&
                event.entitys[0] instanceof Entity
              ) {
                this.selectEntity = event.entitys[0];
              } else {
                this.selectEntity = null;
              }
            });
          }
        });
      }
    );
  }
  get step() {
    return this._step;
  }
  set step(v: number) {
    this._step = v;
  }
  get isEditor() {
    return this._isEditor;
  }
  set isEditor(v: boolean) {
    this._isEditor = v;
  }
  get portalContainer() {
    return this._portalContainer;
  }
  set portalContainer(v: HTMLElement) {
    this._portalContainer = v;
  }
  get portalContainer3() {
    return this._portalContainer3;
  }
  set portalContainer3(v: HTMLElement) {
    this._portalContainer3 = v;
  }
  get Id() {
    return (idIndex++).toString();
  }
  @computed
  get RealInteractionList() {
    if (this.SelectEntity) {
      let list = this.entityInteractions[this.SelectEntity.Id];

      if (!list) {
        list = [];
        this.entityInteractions[this.SelectEntity.Id] = list;
      }

      return this.entityInteractions[this.SelectEntity.Id];
    } else return this.realInteractions;
  }
  get InteractionList() {
    if (this.useReal || this.mode === EditorIntMode.List) {
      return this.RealInteractionList;
    } else {
      return this.interactions;
    }
  }
  get SelectEntity() {
    return this.selectEntity;
  }
  get CurrentActionDetail() {
    return this.CurrentAction?.data[this.currentActionDetailIndex];
  }
  get CurrentInteraction() {
    return this.InteractionList[this.currentEventIndex];
  }
  get CurrentAction() {
    if (!this.CurrentInteraction) return;
    const condition = this.CurrentCondition;
    if (condition) {
      return condition.actions[this.currentActionIndex];
    } else return this.CurrentInteraction.actions[this.currentActionIndex];
  }
  get CurrentCondition() {
    return this.CurrentInteraction?.conditions?.[this.currentConditionIndex];
  }
  set UseReal(v: boolean) {
    this.useReal = v;
  }
  get NewAddActionId() {
    return this.newAddActionId;
  }
  set NewAddActionId(n: string) {
    this.newAddActionId = n;
  }
  @action
  changeTabIndex(index: number) {
    this.tabIndex = index;
  }
  @action
  addInteraction(interact: Partial<IInteractionOption>) {
    if (this.mode === EditorIntMode.List) {
      this.currentEventIndex =
        this.RealInteractionList.push({
          label: interact.label,
          type: interact.type,
          id: this.Id,
          actions: [],
          conditions: [],
          objectId: null,
        }) - 1;
    } else
      this.currentEventIndex =
        this.interactions.push({
          label: interact.label,
          type: interact.type,
          id: this.Id,
          actions: [],
          conditions: [],
          objectId: null,
        }) - 1;
    this.currentActionIndex = -1;
    this.currentActionDetailIndex = -1;
  }
  addActionItem(act: Partial<IInteractionActionOption>) {
    if (this.CurrentInteraction) {
      let data = [];
      if (InteractionConfig[act.type]?.defaultActionData) {
        data = InteractionConfig[act.type].defaultActionData();
        this.currentActionDetailIndex = 0;
      } else this.currentActionDetailIndex = -1;
      const id = this.Id;
      this.currentActionIndex =
        (this.CurrentCondition ?? this.CurrentInteraction).actions.push({
          label: act.label,
          type: act.type,
          target: act.target,
          param: act.param,
          id,
          data,
        }) - 1;
      if ((act.target || act.param) && this.mode === EditorIntMode.Modal) {
        this.newAddActionId = id;
      }
    }
  }
  @action
  addActionDataItem(objectId: ObjectId<Entity>) {
    const currentAction = this.CurrentAction;
    if (!currentAction) return;

    let data: IInterActDataOption =
      InteractionConfig[currentAction.type]?.defaultData?.(objectId);
    data.id = this.Id;
    if (data)
      this.currentActionDetailIndex = this.CurrentAction.data.push(data) - 1;

    return data;
  }
  @action
  removeActionDataItem(objectId: ObjectId) {
    Utils.arrayRemoveIf(
      this.CurrentAction.data,
      (d) => d.objectId === objectId
    );
    this.currentActionDetailIndex = this.CurrentAction.data.length > 0 ? 0 : -1;
  }
  @action
  addParamItem(key: string) {
    const currentAction = this.CurrentAction;
    if (!currentAction) return;
    this.currentActionDetailIndex =
      currentAction.data.push({
        id: this.Id,
        key,
        value: {
          type: "value",
          value: "",
        },
      } as ISetParam) - 1;
  }
  @action
  addNewCondition() {
    Object.assign(this.tempCondition, {
      isAll: true,
      name: "情形" + confIndex++,
      conditions: [],
      actions: [],
    });
  }
  @action
  addNewCompare(index?: number) {
    const newCf: IInteractionCondItem = {
      value1: { type: "value", value: "" },
      value2: { type: "value", value: "" },
      type: CompareType.Equal,
      id: this.Id,
    };

    if (index !== undefined) {
      this.tempCondition.conditions.splice(index + 1, 0, newCf);
      this.currentCompareIndex = index + 1;
    } else {
      this.currentCompareIndex = this.tempCondition.conditions.push(newCf) - 1;
    }
  }
  removeCompare(index: number) {
    this.tempCondition.conditions.splice(index, 1);
    this.currentCompareIndex =
      this.tempCondition.conditions.length === 0 ? -1 : 0;
  }
  saveConditions() {
    if (this.CurrentInteraction.actions.length > 0) {
      observable(this.tempCondition.actions).replace(
        this.CurrentInteraction.actions
      );
      this.CurrentInteraction.actions.length = 0;
    }

    this.tempCondition.id = this.Id;

    if (this.CurrentCondition) {
      Object.assign(this.CurrentCondition, toJS(this.tempCondition));
    } else {
      this.currentConditionIndex =
        this.CurrentInteraction.conditions.push(toJS(this.tempCondition)) - 1;
      this.currentActionIndex = -1;
      this.currentActionDetailIndex = -1;
    }
    if (this.mode === EditorIntMode.List) {
      this.updateDatabase();
    }
    return this.tempCondition.id;
  }
  editorCondition() {
    const condition = this.CurrentCondition;
    if (condition) {
      Object.assign(this.tempCondition, toJS(condition));
    }
  }
  @action
  removeParamItem(key: string) {
    Utils.arrayRemoveIf(this.CurrentAction.data, (d) => d.key === key);
    this.currentActionDetailIndex = this.CurrentAction.data.length > 0 ? 0 : -1;
  }
  remove() {
    let isModify = false;
    if (this.currentActionDetailIndex >= 0) {
      this.CurrentAction.data.splice(this.currentActionDetailIndex, 1);
      this.currentActionDetailIndex = this.getArray0Index(
        this.CurrentAction.data
      );
      if (this.currentActionDetailIndex === -1) {
        this.remove();
      }
      isModify = true;
    } else if (this.currentActionIndex >= 0) {
      if (this.CurrentCondition) {
        this.CurrentCondition.actions.splice(this.currentActionIndex, 1);
        this.currentActionIndex = this.getArray0Index(
          this.CurrentCondition.actions
        );
        isModify = true;
      } else {
        this.CurrentInteraction.actions.splice(this.currentActionIndex, 1);
        this.currentActionIndex = this.getArray0Index(
          this.CurrentInteraction.actions
        );
        isModify = true;
      }
    } else if (this.currentConditionIndex >= 0) {
      this.CurrentInteraction.conditions.splice(this.currentConditionIndex, 1);
      this.currentConditionIndex = this.getArray0Index(
        this.CurrentInteraction.conditions
      );
      isModify = true;
    } else if (this.currentEventIndex >= 0) {
      if (this.mode === EditorIntMode.Modal) {
        this.interactions.splice(this.currentEventIndex, 1);
        this.currentEventIndex = this.getArray0Index(this.interactions);
      } else {
        this.removeRealList();
      }
      isModify = true;
    }

    if (this.mode === EditorIntMode.List && isModify) {
      this.updateDatabase();
    }
  }
  @action
  setAllIndex(
    index: number,
    actIndex: number,
    cofIndex: number,
    intactIndex: number
  ) {
    this.currentActionDetailIndex = index !== undefined ? index : -1;
    this.currentActionIndex = actIndex !== undefined ? actIndex : -1;
    this.currentConditionIndex = cofIndex !== undefined ? cofIndex : -1;
    this.currentEventIndex =
      intactIndex !== undefined ? intactIndex : undefined;

    if (this.currentEventIndex !== -1) {
      this.tabIndex = 1;
    }

    if (index !== undefined && this.mode === EditorIntMode.List) {
      this.step = 3;
      this.isEditor = true;
    }
  }
  @action
  removeRealList() {
    if (this.SelectEntity) {
      const list = this.entityInteractions[this.SelectEntity.Id];
      list.splice(this.currentEventIndex, 1);
      this.entityInteractions[this.SelectEntity.Id] = [...list];
      this.currentEventIndex = this.getArray0Index(list);
    } else {
      this.realInteractions.splice(this.currentEventIndex, 1);
      this.currentEventIndex = this.getArray0Index(this.realInteractions);
    }
  }
  @action
  handleUp(actions, index) {
    if (index === 0) {
      return;
    }
    let temp = actions[index - 1];
    actions[index - 1] = actions[index];
    actions[index] = temp;
    if (this.mode === 2) {
      this.updateDatabase();
    }
  }
  @action
  handleDown(actions, index) {
    if (index === actions.length - 1) {
      return;
    }
    let temp = actions[index + 1];
    actions[index + 1] = actions[index];
    actions[index] = temp;
    if (this.mode === 2) {
      this.updateDatabase();
    }
  }
  save() {
    this.save2RealInteractions();
    this.updateDatabase();
  }
  save2VsInteractions() {
    if (this.SelectEntity) {
      const arr = this.entityInteractions[this.SelectEntity.Id] ?? [];
      observable(this.interactions).replace(toJS(arr));
    } else {
      observable(this.interactions).replace(toJS(this.realInteractions));
    }
  }
  save2RealInteractions() {
    if (this.SelectEntity) {
      this.entityInteractions[this.SelectEntity.Id] = toJS(this.interactions);
    } else observable(this.realInteractions).replace(toJS(this.interactions));
  }
  updateState(callback: () => void) {
    callback();
  }
  updateDatabase() {
    const table = App.intance.Database.InteractionTable;
    const currentTableIds = new WeakSet();
    const intactList = [...toJS(this.realInteractions)];
    for (const [, ins] of Object.entries(this.entityInteractions)) {
      intactList.push(...toJS(ins));
    }
    for (const interaction of intactList) {
      let interact: Interaction.Interaction;
      if (interaction.objectId) {
        interact = interaction.objectId.Object;
        interact.List = [];
      } else {
        interact = new Interaction.Interaction(
          interaction.type,
          this.SelectEntity?.ObjectId as ObjectId<Entity>
        );
        table.append(interact);
        interaction.objectId =
          interact.ObjectId as ObjectId<Interaction.Interaction>;
      }
      if (interaction.conditions.length > 0) {
        for (const condition of interaction.conditions) {
          const conditionInt = new Interaction.Condition();
          conditionInt.isAll = condition.isAll;
          for (const cp of condition.conditions) {
            const compare = new Interaction.Compare();
            compare.value1 = toJS(cp.value1);
            compare.value2 = toJS(cp.value2);
            compare.type = cp.type;
            conditionInt.addCompare(compare);
          }
          for (const act of condition.actions) {
            if (!act.type) continue;
            const wg = this.addWidget(act);
            if (wg) conditionInt.add(wg);
          }
          interact.add(conditionInt);
        }
      } else {
        for (const act of interaction.actions) {
          if (!act.type) continue;
          const wg = this.addWidget(act);
          if (wg) interact.add(wg);
        }
      }
      currentTableIds.add(interact.ObjectId);
    }

    //移除数据库中不用的交互
    table.remove(table.objects.filter((o) => !currentTableIds.has(o.ObjectId)));

    fileStore.save(false);
  }
  private addWidget(act: IInteractionActionOption) {
    const handleData = InteractionConfig[act.type]?.handleData;

    let data = handleData ? handleData(act.data) : toJS(act.data);

    const wg = HCFactory.CreateObject(act.type) as BaseWidget;
    if (wg) {
      wg.fromJSON(data);
    }
    return wg;
  }
  @action
  reset() {
    this.interactions.length = 0;
    this.realInteractions.length = 0;
    this.tempCondition.actions.length = 0;
    this.entityInteractions = {};
    this.resetIndexs();
  }
  resetIndexs() {
    this.currentEventIndex = -1;
    this.currentActionIndex = -1;
    this.currentActionDetailIndex = -1;
    this.currentCompareIndex = -1;
    this.currentConditionIndex = -1;
    this.tabIndex = 0;
  }
  private load(intact: Interaction.Interaction[]) {
    this.reset();
    for (const int of intact) {
      let ins = this.realInteractions;
      if (int.Target) {
        ins = this.entityInteractions[int.Target.Index];
        if (!ins) {
          ins = [];
        }
      }
      // this.currentEventIndex++;
      let containers: IInteractionCond[] = [];
      let actions: IInteractionActionOption[] = [];

      if (int.List[0]?.IsContainer) {
        containers = (int.List as Interaction.Condition[]).map((c) => ({
          name: c.Name,
          isAll: c.isAll,
          id: this.Id,
          conditions: this.loadCompares(c.compares),
          actions: this.loadActions(c.list),
        }));
      } else {
        actions = this.loadActions(int.List);
      }
      ins.push({
        label: EventLabel[int.TriggerType],
        type: int.TriggerType,
        id: this.Id,
        conditions: containers,
        actions: actions,
        objectId: int.ObjectId as ObjectId<Interaction.Interaction>,
      });
      if (int.Target) {
        this.entityInteractions[int.Target.Index] = ins;
      }
    }
  }
  loadActions(list: BaseWidget[]) {
    return list.map((wg) => {
      const Con = wg.constructor as any;
      return {
        label: Con.schema.label,
        type: Con.type,
        target: true,
        id: this.Id,
        data: InteractionConfig[Con.type].loadWidgetData?.(wg) ?? [],
      };
    });
  }
  loadCompares(list: Interaction.Compare[]): IInteractionCondItem[] {
    return list.map((cp) => {
      return {
        value1: cp.value1,
        value2: cp.value2,
        type: cp.type,
        id: this.Id,
      };
    });
  }
  getActionAllIds(it: IInteractionOption) {
    const ids: string[] = [it.id];
    const getActList = (list: IInteractionActionOption[]) => {
      for (const data of list) {
        ids.push(data.id);
      }
    };

    if (it.conditions.length > 0) {
      for (const cf of it.conditions) {
        ids.push(cf.id);
        getActList(cf.actions);
      }
    } else {
      getActList(it.actions);
    }
    return ids;
  }
  isUsedEvent(type: string) {
    return !!this.RealInteractionList.find((int) => int.type === type);
  }
  getExpnadIds(int: IInteractionOption) {
    const ids: string[] = [int.id];
    for (const act of int.actions) {
      ids.push(act.id);
    }
    for (const c of int.conditions) {
      ids.push(c.id);
      for (const act of c.actions) {
        ids.push(act.id);
      }
    }
    return ids;
  }
  isUsedEntity(id: ObjectId) {
    if (!this.CurrentAction) return false;
    return !!this.CurrentAction.data.find((d) => d.objectId === id);
  }
  private getArray0Index<T>(arr: T[]) {
    return arr.length > 0 ? 0 : -1;
  }
}

export const interactionStore = new InteractionStore();
