import { ParseResult } from "@babel/parser";
import { File as BabelFile, Program } from "@babel/types";
import { HCFiler, ImportEntity, Viewer } from "@hc/engine";
import {
  Button,
  Paper,
  Stack,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Box,
  Drawer,
  Typography,
  Divider,
} from "@mui/material";
import { Observer } from "mobx-react-lite";
import React, { useEffect, useState, useRef } from "react";
import { updateFilePreview } from "~/apis/fileReq";
import { useLeftStores } from "~/contexts/leftStoreContext";
import { message } from "~/utils/message";
import CloseIcon from "@mui/icons-material/Close";
import HcInput from "../common/hcInput";
import { HStack } from "../common/stack";
import { app } from "../editor";
import ResourceTab from "../resourceTab";
import EditorCell from "./editorCell";
import { IComponentData, IComponentProperty } from "./types";
import { getComponent } from "~/utils/component";

interface ComponentDataPanelProps {
  getAst: () => ParseResult<BabelFile>;
  getCode: () => string;
  generateDemo: () => void;
  propertys: IComponentProperty[];
  onClose: (evt?, r?: string) => void;
  current: IComponentData;
  format: () => void;
  hasErrror: () => boolean;
  updatePropName: (d) => void;
  getView: (d) => void;
  apply: (camera: any[]) => void;
}

let nameIndex = 0;
let idIndex = Date.now();
let viewer: Viewer;
let usedUUid = new Set<string>();

const filer = new HCFiler();

const ComponentDataPanel: React.FC<ComponentDataPanelProps> = ({
  getAst,
  getCode,
  generateDemo,
  propertys,
  onClose,
  current,
  format,
  hasErrror,
  updatePropName,
  getView,
  apply,
}) => {
  const [componentName, setComponentName] = useState(
    current?.title || "未命名" + nameIndex++
  );
  const canvasRef = useRef<HTMLCanvasElement>(null);
  const { commonStore } = useLeftStores();
  const [startSelect, setStartSelect] = useState(false);

  const handleEndSelect = () => {
    setStartSelect(false);
  };
  const handleStartSelect = () => {
    setStartSelect(true);
  };

  const handleChangeName = (e) => {
    setComponentName(e.target.value);
  };

  const parseCodeEntity = (program: Program) => {
    const Component = getComponent(getCode());
    const c = new Component();
    usedUUid.clear();

    for (const en of c.entitys) {
      if (en instanceof ImportEntity) {
        const value = en["url"];
        if (commonStore.assetMap.has(value))
          usedUUid.add(commonStore.assetMap.get(value));
        else {
          const uid = value.split("/")[0];
          if (uid && current.moduleIds && current.moduleIds.includes(uid)) {
            usedUUid.add(uid);
          }
        }
      }
    }

    return c.entitys.length > 0;
  };

  const handleApply = () => {
    filer.clear();
    viewer.cameraManager.ArcRotateControl.writeFile(filer);
    apply(filer.Data);
  };

  const handleUpdate = async () => {
    if (!componentName) {
      message.error("请输入物模型名称");
      return;
    }

    const ast = getAst();

    if (!ast) {
      message.error("代码有误");
      return;
    }

    const hasEns = parseCodeEntity(ast.program);
    if (hasEns) {
      filer.clear();
      viewer.cameraManager.ArcRotateControl.writeFile(filer);

      const result = await commonStore.updateComponent(
        {
          ...current,
          code: getCode(),
          title: componentName,
          propertys: [...propertys],
          moduleIds: Array.from(usedUUid).join(","),
        },
        filer.Data
      );
      if (result) {
        await updateFilePreview(current.id, () => viewer.getPreviewUrl(true));
        message.success("物模型更新成功");
        commonStore.load();
      }

      onClose();
    } else {
      message.error("物模型部件为空");
    }
  };

  const handleConfirm = () => {
    if (hasErrror()) {
      message.error("代码存在错误,请先修复");
      return;
    }

    if (current?.id) {
      handleUpdate();
    } else {
      handleSave();
    }
  };

  const handleSave = async () => {
    if (!componentName) {
      message.error("请输入物模型名称");
      return;
    }

    const ast = getAst();

    if (!ast) {
      message.error("代码有误");
      return;
    }

    const hasEns = parseCodeEntity(ast.program);
    if (hasEns) {
      const comData = {
        title: componentName,
        id: -1,
        code: getCode(),
        propertys: [...propertys],
      };

      filer.clear();
      viewer.cameraManager.ArcRotateControl.writeFile(filer);

      const id = await commonStore.addComponent(
        comData,
        Array.from(usedUUid).join(","),
        filer.Data
      );
      if (id) {
        await updateFilePreview(id, () => viewer.getPreviewUrl(true));
        commonStore.load();
        message.success("物模型创建成功");
      }
      onClose();
    } else {
      message.error("物模型部件为空");
    }
  };

  const handleChangeProp = (prop: IComponentProperty) => {
    updatePropName(prop);
  };

  useEffect(() => {
    if (canvasRef.current) {
      viewer = app.createView(canvasRef.current, { defaultLight: true });
      viewer.sceneControl.createDefaultEnv();
      getView(viewer);
    }
    return () => {
      viewer && viewer.destory();
    };
  }, []);

  return (
    <Observer>
      {() => (
        <>
          <HcInput
            id="name"
            title="名称"
            value={componentName}
            placeholder="请输入模型名称"
            onChange={handleChangeName}
          />
          <TableContainer
            component={Paper}
            sx={{ marginTop: 5, minHeight: 200 }}
          >
            <Table size="small" aria-label="property">
              <TableHead>
                <TableRow>
                  <TableCell>属性</TableCell>
                  <TableCell>值类型</TableCell>
                  <TableCell>默认值</TableCell>
                  <TableCell>名称</TableCell>
                  <TableCell>备注</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {propertys.map((row) => (
                  <TableRow
                    key={row.name}
                    sx={{ "&:last-child td, &:last-child th": { border: 0 } }}
                  >
                    <TableCell component="th" scope="row">
                      {row.prop}
                    </TableCell>
                    <TableCell>{row.type}</TableCell>
                    <TableCell>{row.value.toString()}</TableCell>
                    <EditorCell
                      defaultValue={row.name}
                      onBlur={(e) =>
                        handleChangeProp({ ...row, name: e.target.value })
                      }
                    />
                    <TableCell>{row.remark}</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
          <Box sx={{ height: "40%", outline: "1px solid #ccc" }}>
            <canvas ref={canvasRef}></canvas>
          </Box>
          <Stack
            direction="row"
            spacing={1}
            alignItems="center"
            sx={{ marginTop: 5 }}
          >
            <Button variant="contained" onClick={handleConfirm}>
              {current?.id ? "更新" : "保存"}
            </Button>
            <Button variant="contained" onClick={handleApply}>
              应用&lt;Ctrl+S&gt;
            </Button>
            {/* <Button variant="contained" onClick={format}>
              格式化
            </Button> */}
            <Button variant="contained" onClick={generateDemo}>
              生成模型代码
            </Button>
            <Button variant="contained" onClick={handleStartSelect}>
              选择模型
            </Button>
          </Stack>
          <Drawer
            open={startSelect}
            onClose={handleEndSelect}
            anchor="right"
            PaperProps={{ sx: { width: 320, p: 1 } }}
            sx={{ width: 0 }}
            ModalProps={{ disableAutoFocus: true, disableEnforceFocus: true }}
            variant="persistent"
          >
            <HStack justifyContent="space-between" alignItems="center">
              <Typography variant="h6">选择模型</Typography>
              <Button
                onClick={handleEndSelect}
                size="large"
                startIcon={<CloseIcon />}
              ></Button>
            </HStack>
            <Divider sx={{ my: 1 }} />
            <ResourceTab readonly />
          </Drawer>
        </>
      )}
    </Observer>
  );
};
export default ComponentDataPanel;
