import {
  Backdrop,
  Box,
  CircularProgress,
  FormControl,
  FormControlLabel,
  FormLabel,
  Radio,
  RadioGroup,
  Stack,
  Tab,
  Tabs,
} from "@mui/material";
import React, { useEffect, useState } from "react";
import { FileApi } from "~/apis";
import { IFileReturnData } from "~/apis/types";
import { useEditorContext } from "~/contexts/editor-context";
import { SET_2D_TYPE } from "~/reducers/editorReducers";
import { getReq, ResponseStatus } from "~/utils/axios";
import { getFileUrl } from "~/utils/url";

import CollapseContainer from "../common/collapseContainer";
import DragCard from "../common/dragCard";
import SearchInput from "../common/searchInput";
import { a11yProps } from "../common/tabPanel";
import DataList from "../dataList";

const tabs = [
  {
    value: 0,
    label: "图标",
  },
  {
    value: 1,
    label: "设计",
  },
];

const D2Panel: React.FC = () => {
  const { editorState, dispatch } = useEditorContext();
  const [loading, setLoading] = useState(false);
  const [dirlist, setDirList] = useState<IFileReturnData[]>([]);
  const [query, setQuery] = useState("");
  const [collkeys, setCollkeys] = useState<Set<number>>(new Set());
  const [selectId, setSelectId] = useState<string | number>(null);
  const [value, setValue] = useState(0);

  const handleExpand = async (key: number, evt: React.MouseEvent) => {
    evt.stopPropagation();
    if (evt.button === 0) {
      if (collkeys.has(key)) {
        collkeys.delete(key);
      } else {
        collkeys.add(key);
        updateDirList(key as number);
      }
      setCollkeys(new Set(collkeys));
    }

    setSelectId(key);
  };

  const handleChangeQuery = (evt) => {
    setQuery(evt.target.value);
  };

  const switchTab = (e, v) => {
    setValue(v);
  };

  const handleChange = (evt, value) => {
    dispatch({ type: SET_2D_TYPE, render2DType: value });
  };

  const getReqPromise = async (pid = 0, query?: string) => {
    if (value === 0) {
      return await getReq<IFileReturnData[]>(FileApi.publicList, {
        params: { pid, source_type: "unsymbol", status: 1, name: query },
      });
    } else {
      return await getReq<IFileReturnData[]>(FileApi.list, {
        params: {
          source_type: "symbol",
          pid: pid,
          status: 1,
        },
      });
    }
  };

  const updateTree = async () => {
    setLoading(true);
    const result = await getReqPromise(0, query);

    if (result.code === ResponseStatus.Ok && result.data) {
      const data = result.data;
      updateDirList(Array.from(collkeys), data);
    } else {
      setDirList([]);
    }
    setLoading(false);
  };

  const updateDirList = async (
    key: number | number[],
    list: IFileReturnData[] = dirlist
  ) => {
    if (!Array.isArray(key)) {
      key = [key];
    }
    const kMap = new Map<number, IFileReturnData[]>();
    for (const k of key) {
      const res = await getReqPromise(k);
      if (res.code === ResponseStatus.Ok && res.data) {
        kMap.set(k, res.data);
      }
    }

    for (const d of list) {
      if (kMap.has(d.id)) {
        d.children = kMap.get(d.id).map((l) => ({
          title: l.name.split(".")[0] ?? l.file_key,
          data: {
            key: "2D",
            url: getFileUrl(l),
            name: l.name.split(".")[0] ?? l.file_key,
            modelId: l.uuid,
          },
          preview: getFileUrl(l, true),
          id: l.id,
        }));
        break;
      }
    }

    setDirList([...list]);
  };

  useEffect(() => {
    updateTree();
  }, [value, query]);

  return (
    <>
      <Tabs
        sx={{ height: "50px", width: "100%" }}
        value={value}
        indicatorColor="secondary"
        textColor="inherit"
        onChange={switchTab}
        variant="fullWidth"
      >
        {tabs.map((t) => (
          <Tab
            key={t.value}
            label={t.label}
            value={t.value}
            {...a11yProps(t.value)}
          />
        ))}
      </Tabs>
      <Stack sx={{ height: "calc(100% - 50px)" }}>
        <Stack flexDirection="row" sx={{ height: 50 }}>
          <SearchInput
            value={query}
            onChange={handleChangeQuery}
            placeholder="搜索图标"
            sx={{ flex: 1 }}
          />
        </Stack>
        <Box sx={{ flex: 1, overflow: "auto" }}>
          {dirlist.map(
            (dir) =>
              dir.node_type === "dir" && (
                <CollapseContainer
                  title={dir.name}
                  open={collkeys.has(dir.id)}
                  onExpand={handleExpand}
                  target={dir.id}
                  key={dir.id}
                  select={dir.id === selectId}
                >
                  <DataList
                    list={dir.children ?? []}
                    component={DragCard}
                    componentProps={(f) => ({
                      "data-nodeid": f.id,
                      "data-nodetype": "file",
                    })}
                    canPreview
                  />
                </CollapseContainer>
              )
          )}
        </Box>
        <FormControl sx={{ height: 60 }}>
          <FormLabel>图标类型</FormLabel>
          <RadioGroup
            aria-labelledby="图标类型"
            value={editorState.render2DType}
            row
            onChange={handleChange}
            sx={{
              "&>label": {
                mr: "8px",
              },
            }}
          >
            <FormControlLabel value="dom" control={<Radio />} label="图标" />
            <FormControlLabel value="gui" control={<Radio />} label="标注" />
            {/* <FormControlLabel
              value="texture"
              control={<Radio />}
              label="贴图"
            /> */}
          </RadioGroup>
        </FormControl>
      </Stack>
      <Backdrop
        sx={{ color: "#fff", zIndex: (theme) => theme.zIndex.drawer + 1 }}
        open={loading}
        onClick={() => setLoading(false)}
      >
        <CircularProgress color="inherit" />
      </Backdrop>
    </>
  );
};
export default D2Panel;
