import { Utils } from "@hc/engine";
import { IEditorState } from "~/contexts/editor-context";
import { convert2UiData } from "~/utils/adapter";
import { fixedNotZero } from "~/utils/utils";

export const SELECT_ENTITY = "SELECt_ENTITY";
export const UPDATE = "UPDATE_POSITION";
export const UPDATE_ENTITY = "UPDATE";
export const UPDATE_NAME = "UPDATE_NAME";
export const TOGGLE_VISIABLE = "TOGGLE_VISIABLE";
export const TOGGLE_PICKABLE = "TOGGLE_PICKABLE";
export const TOGGLE_SHADOW = "TOGGLE_SHADOW";

export const SELECT_GUI = "SELECT_GUI";
export const UPDATE_GUI = "UPDATE_GUI";

export const SET_2D_TYPE = "SET_2D_TYPE";

export const SELECT_DOM = "SELECT_DOM";

export const UPDATE_OTHER = "UPDATE_OTHER";
export const UPDATE_DOM = "UPDATE_DOM";

export const CLEAR = "CLEAR";

export function editorReducers(
  state: IEditorState,
  action: { type: string } & Partial<IEditorState>
): IEditorState {
  const { type, ...data } = action;
  switch (type) {
    case SELECT_ENTITY: {
      const entity = action.entity;
      if (!entity) return { ...state, entity: null, gui: null, dom: null };
      const pos = entity.Position;
      const ro = entity.Rotation;
      const sc = entity.Scale;
      return {
        ...state,
        entity: action.entity,
        gui: null,
        position: convert2UiData(pos, (n) => fixedNotZero(n, 2)),
        rotation: convert2UiData(ro, (n) => fixedNotZero(Utils.rad2Deg(n), 2)),
        scale: convert2UiData(sc, (n) => fixedNotZero(n, 2)),
        name: entity.Name,
        visiable: entity.Visiable,
        pickable: entity.IsPickable,
        enableShadow: entity.EnableShadow,
        type: entity.constructor["type"],
      };
    }
    case UPDATE_ENTITY: {
      const entity = state.entity;
      if (!entity) return state;
      const pos = entity.Position;
      const ro = entity.Rotation;
      const sc = entity.Scale;
      return {
        ...state,
        position: convert2UiData(pos, (n) => fixedNotZero(n, 2)),
        rotation: convert2UiData(ro, (n) => fixedNotZero(Utils.rad2Deg(n), 2)),
        scale: convert2UiData(sc, (n) => fixedNotZero(n, 2)),
      };
    }
    case UPDATE: {
      return {
        ...state,
        ...data,
      };
    }
    case UPDATE_NAME: {
      return {
        ...state,
        name: data.name,
      };
    }
    case TOGGLE_VISIABLE: {
      return {
        ...state,
        visiable: !state.visiable,
      };
    }
    case TOGGLE_PICKABLE: {
      return {
        ...state,
        pickable: !state.pickable,
      };
    }
    case TOGGLE_SHADOW: {
      return {
        ...state,
        enableShadow: !state.enableShadow,
      };
    }
    case SELECT_GUI: {
      const gui = action.gui;
      if (!gui) return { ...state, entity: null, gui: null };
      const pos = gui.Position;
      const ro = { x: gui.Rotation };
      const sc = gui.Scale;

      return {
        ...state,
        entity: null,
        dom: null,
        gui: gui,
        name: gui.Name,
        position: pos,
        rotation: convert2UiData(ro, (n) => fixedNotZero(Utils.rad2Deg(n), 2)),
        scale: convert2UiData(sc),
        type: gui.constructor["type"],
        other: {
          Width: gui.Width,
          Height: gui.Height,
        },
      };
    }
    case UPDATE_GUI: {
      const gui = state.gui;
      if (!gui) return state;
      const pos = gui.Position;
      const ro = { x: gui.Rotation };
      const sc = gui.Scale;

      return {
        ...state,
        gui: gui,
        name: gui.Name,
        position: pos,
        rotation: convert2UiData(ro, (n) => fixedNotZero(Utils.rad2Deg(n), 2)),
        scale: convert2UiData(sc),
      };
    }
    case SET_2D_TYPE: {
      return {
        ...state,
        render2DType: data.render2DType,
      };
    }
    case SELECT_DOM: {
      const dom = action.dom;
      if (!dom) return { ...state, entity: null, gui: null, dom: null };
      const pos = dom.Position;
      const ro = { x: dom.Rotation };
      const sc = dom.Scale;

      return {
        ...state,
        entity: null,
        gui: null,
        dom,
        name: dom.Name,
        position: pos,
        rotation: convert2UiData(ro),
        scale: convert2UiData(sc),
        type: dom.constructor["type"],
        other: {
          width: dom.Width,
          height: dom.Height,
          HorAlign: dom.HorAlign,
          VerAlign: dom.VerAlign,
        },
      };
    }
    case UPDATE_DOM: {
      const dom = state.dom;
      if (!dom) return state;
      const pos = dom.Position;

      return {
        ...state,
        dom,
        name: dom.Name,
        position: pos,
        other: {
          width: dom.Width,
          height: dom.Height,
          HorAlign: dom.HorAlign,
          VerAlign: dom.VerAlign,
        },
      };
    }
    case UPDATE_OTHER: {
      return {
        ...state,
        other: action.other,
      };
    }
    case CLEAR: {
      return {
        ...state,
        entity: null,
        dom: null,
        gui: null,
      };
    }
    default:
      break;
  }
  return state;
}
