import { Box, InputAdornment, TextField, Typography } from "@mui/material";
import { observer } from "mobx-react-lite";
import React from "react";
import { CommonSelectEntity } from "~/components/common/commonInteraction";
import { useInteractionStoresContext } from "~/contexts/interactionStoreContext";

const OpacityDetail: React.FC = () => {
  const store = useInteractionStoresContext();

  const detail = store.CurrentActionDetail;

  const handleChange = (e) => {
    store.updateState(() => {
      detail[e.target.name] = e.target.value;
    });
  };

  return (
    <Box>
      <CommonSelectEntity value={detail} />
      <Typography>不透明度</Typography>
      <TextField
        name="opacity"
        value={detail.opacity}
        onChange={handleChange}
        InputProps={{
          endAdornment: <InputAdornment position="start">%</InputAdornment>,
        }}
      />
      <TextField
        value={detail.time}
        hiddenLabel
        variant="outlined"
        size="small"
        name="time"
        onChange={handleChange}
        InputProps={{
          startAdornment: (
            <InputAdornment position="start">时间</InputAdornment>
          ),
          endAdornment: <InputAdornment position="start">毫秒</InputAdornment>,
        }}
      />
    </Box>
  );
};
export default observer(OpacityDetail);
