import {
  Checkbox,
  Dialog,
  DialogActions,
  DialogContent,
  FormControlLabel,
  TextField,
  Button,
} from "@mui/material";
import React, { useEffect, useState } from "react";
import { DirSourceType, MarketApi } from "~/apis";
import { MarkList } from "~/apis/types";
import { App } from "~/intance";
import { CommonStore } from "~/stores/commonStore";
import { FileStore } from "~/stores/fileStore";
import { postReq, ResponseStatus } from "~/utils/axios";
import { Bus } from "~/utils/bus";
import {
  loadOnlineComponent,
  loadOnlineModel,
  updateUrl,
} from "~/utils/loadFile";
import { message } from "~/utils/message";
import DialogHeader from "../common/dialogHeader";
import SelectProjectList from "../common/projectList";
import SelectDirList from "../common/selectDirList";
import { HStack } from "../common/stack";
import { ECategory } from "./types";

interface ApplyShopProps {
  open: boolean;
  onClose: () => void;
  shop?: MarkList;
  defaultProjectId?: number;
  fileStore: FileStore;
  commonStore: CommonStore;
  onCloseMark?: () => void;
  needProject?: boolean;
  onSuccess?: (...arg) => void;
}

const ApplyShopPanel: React.FC<ApplyShopProps> = ({
  open,
  onClose,
  shop,
  defaultProjectId,
  fileStore,
  commonStore,
  onCloseMark,
  needProject,
  onSuccess,
}) => {
  const [useCurrent, setUseCurrent] = useState(true);
  const [newName, setNewName] = useState(shop?.product_name);
  const [dirId, setDirId] = useState(null);
  const [projectId, setProject] = useState(null);

  const isDisable = () => {
    if (shop?.real_type === ECategory.Project) return false;

    if (needProject && !projectId && !defaultProjectId) return true;
    if (
      shop?.real_type === ECategory.Component ||
      shop?.real_type === ECategory.Model
    )
      return !dirId;

    return false;
  };

  const apply = async (isAdd = false) => {
    if (!shop) return;
    if (isDisable()) {
      message.error("请先选择项目或者目录!");
      return;
    }

    if (!newName) {
      message.error("请输入名称!");
      return;
    }

    const data: Record<string, any> = {
      name: newName,
      id: shop.real_code,
      projectId: projectId ?? defaultProjectId,
    };

    let callback: (id: number) => void;

    if (shop?.real_type === ECategory.File) {
      callback = (id: number) => {
        fileStore.loadFileList().then((res) => {
          if (id && isAdd) {
            fileStore.load(id);
            updateUrl(id);
            onCloseMark?.();
            message.success("新建场景应用并打开成功!");
          } else {
            message.success("应用新建成功，请到场景列表查看");
          }
        });
      };
    } else if (shop?.real_type === ECategory.Project) {
      data.projectId = undefined;
      callback = (id: number) => {
        if (isAdd && id) {
          location.search = "?projectId=" + id;
        } else {
          message.success("项目创建成功,请到项目列表查看");
        }
      };
    } else {
      if (!dirId) {
        message.error("请先选择目录");
        return;
      }
      data.pid = dirId;
      if (shop?.real_type === ECategory.Component) {
        callback = (id: number) => {
          if (id && isAdd) {
            loadOnlineComponent(id, App.intance);
            onCloseMark?.();
            message.success("新建组件并应用到场景");
          } else {
            message.success("新建组件成功，请到模型资源下查看");
          }
          commonStore.load();
        };
      } else {
        callback = (id: number) => {
          if (id && isAdd) {
            loadOnlineModel(id, App.intance);
            onCloseMark?.();
            message.success("应用模型并应用到场景成功");
          } else {
            message.success("应用模型成功,请到模型资源查看");
          }
          Bus.trigger({ type: "ApplyModel", target: null });
        };
      }
    }

    const res = await postReq(MarketApi.pull, data);
    if (res.code === ResponseStatus.Ok) {
      callback(res.data?.id);
      onSuccess?.(projectId);
    }
  };

  const selectDir = (id: number) => {
    setDirId(id);
  };

  const selectProject = (id: number) => {
    setProject(id);
  };

  useEffect(() => {
    setNewName(shop?.product_name ?? "");
  }, [shop]);

  return (
    <Dialog open={open} onClose={onClose} maxWidth="md" fullWidth>
      <DialogHeader onClose={onClose} title="应用素材" />
      <DialogContent sx={{ overflow: "initial" }}>
        <HStack>
          {shop?.real_type === ECategory.File && (
            <FormControlLabel
              label="使用当前项目"
              checked={useCurrent}
              control={<Checkbox />}
              onChange={(e, c) => setUseCurrent(c)}
            />
          )}
          <TextField
            value={newName}
            onChange={(e) => setNewName(e.target.value)}
            label="新名称"
            size="small"
          />
        </HStack>
        {(!useCurrent || needProject) &&
          shop?.real_type !== ECategory.Project && (
            <SelectProjectList selectId={projectId} onClick={selectProject} />
          )}
        {shop?.real_type === ECategory.Component && (
          <SelectDirList
            sourceType={DirSourceType.Component}
            projectId={projectId ?? defaultProjectId}
            onClick={selectDir}
            selectId={dirId}
          />
        )}
        {shop?.real_type === ECategory.Model && (
          <SelectDirList
            sourceType={DirSourceType.Model}
            projectId={projectId ?? defaultProjectId}
            onClick={selectDir}
            selectId={dirId}
          />
        )}
      </DialogContent>
      <DialogActions>
        <Button onClick={() => apply(false)}>应用</Button>
        <Button onClick={() => apply(true)}>
          {shop?.real_type === ECategory.Project
            ? "应用并打开"
            : "应用并添加到场景"}
        </Button>
      </DialogActions>
    </Dialog>
  );
};
export default ApplyShopPanel;
