import { Utils } from "@hc/engine";
import {
  Box,
  Button,
  Checkbox,
  FormControlLabel,
  InputAdornment,
  TextField,
  Typography,
} from "@mui/material";
import { observer } from "mobx-react-lite";
import React, { useState } from "react";
import LabelInput from "~/components/common/labelInput";
import Vector3Panel from "~/components/common/vector3Panel";
import { useInteractionStoresContext } from "~/contexts/interactionStoreContext";
import { App } from "~/intance";
import { fixedNotZero } from "~/utils/utils";

interface MoveCameraProps {}

const MoveCameraDetail: React.FC<MoveCameraProps> = () => {
  const store = useInteractionStoresContext();
  const [useCamera, setUseCamera] = useState(false);

  const detail = store.CurrentActionDetail;

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    store.updateState(() => {
      detail[e.target.name] = e.target.value;
    });
  };

  const handleChangeVector = (k: string, v: string) => {
    store.updateState(() => {
      detail.target[k] = v;
    });
  };

  const handleUseCamera = (evt) => {
    // setUseCamera(c);
    const camera = App.intance.Viewer.cameraManager.Control;
    store.updateState(() => {
      detail.alpha = fixedNotZero(Utils.rad2Deg(camera.Alpha), 2);
      detail.beta = fixedNotZero(Utils.rad2Deg(camera.Beta), 2);
      detail.radius = fixedNotZero(camera.Radius, 2);
      detail.target.x = fixedNotZero(camera.Target.x, 2);
      detail.target.y = fixedNotZero(camera.Target.y, 2);
      detail.target.z = fixedNotZero(camera.Target.z, 2);
    });
  };

  return (
    <Box>
      {/* <FormControlLabel
        checked={useCamera}
        control={<Checkbox />}
        label="设置为当前相机位置"
        onChange={handleUseCamera}
      /> */}
      <Button onClick={handleUseCamera} variant="contained">
        使用当前相机位置
      </Button>
      <Typography>相机目标位置</Typography>
      {[
        { label: "横向旋转", key: "alpha" },
        { label: "纵向旋转", key: "beta" },
        { label: "观察半径", key: "radius" },
      ].map((opt) => (
        <LabelInput
          label={opt.label}
          name={opt.key}
          key={opt.key}
          value={detail[opt.key]}
          onChange={handleChange}
        />
      ))}
      <Typography>朝向</Typography>
      <Vector3Panel
        vector={detail.target}
        onChange={handleChangeVector}
        sx={{ flex: 1 }}
      />
      <TextField
        value={detail.time}
        hiddenLabel
        variant="outlined"
        size="small"
        name="time"
        onChange={handleChange}
        InputProps={{
          startAdornment: (
            <InputAdornment position="start">时间</InputAdornment>
          ),
          endAdornment: <InputAdornment position="start">毫秒</InputAdornment>,
        }}
      />
    </Box>
  );
};
export default observer(MoveCameraDetail);
