import React, { useEffect, useState } from "react";
import UpDownButton from "../common/upDownButton";
import Menu from "@mui/material/Menu";
import MenuItem from "@mui/material/MenuItem";
import { userStore } from "../../stores/UserStore";
interface PersonalProps {}
const Personal: React.FC<PersonalProps> = (PersonalProps) => {
  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);
  const open = Boolean(anchorEl);
  const handlePersonal = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };
  return (
    <>
      <UpDownButton
        iconName="icon-3Duser"
        title="用户"
        onClick={handlePersonal}
      />
      <Menu
        id="basic-menu"
        anchorEl={anchorEl}
        open={open}
        onClose={handleClose}
        MenuListProps={{
          "aria-labelledby": "basic-button",
        }}
      >
        <MenuItem sx={{ fontSize: "12px" }}>{userStore.UserName}</MenuItem>
        <MenuItem sx={{ fontSize: "12px" }} onClick={userStore.logOut}>
          退出登录
        </MenuItem>
      </Menu>
    </>
  );
};

export default Personal;
