import { IconButton, List, MenuItem, Stack } from "@mui/material";
import { Observer, observer } from "mobx-react-lite";
import React, { useState, useEffect } from "react";
import PublishModal from "~/components/publish/publishModal";
import { useLeftStores } from "~/contexts/leftStoreContext";
import useContextMenu from "~/hooks/useContextMenu";
import { debounceCallback } from "~/utils/time";
import CollapseContainer from "../common/collapseContainer";
import DragCard from "../common/dragCard";
import SpaceAddHint from "../common/spaceAddHint";
import DataList from "../dataList";
import EditorComponentModal from "../editorModal";
import { IComponentData } from "../editorModal/types";
import { ECategory } from "~/components/resourceManager/types";
import SearchInput from "../common/searchInput";
import IconAli from "../common/iconAli";
import { DirSourceType } from "~/apis";
import { getFileList } from "~/apis/fileReq";
let clickComponent: IComponentData;

const CommonTab: React.FC = observer(() => {
  const { commonStore } = useLeftStores();
  const [publish, setPublish] = useState(false);
  const [query, setQuery] = useState("");
  const [openMenu, closeMenu, MenuContainer] = useContextMenu((event) => {
    clickComponent = getClickComponent(event.target as HTMLElement);
  });

  const handleOpen = (id: number) => {
    commonStore.startEditor(id);
  };
  const handleClose = (evt, reasion: string) => {
    if (reasion === "backdropClick") return;
    commonStore.open = false;
  };
  const handleOpenPublish = () => {
    closeMenu();
    setPublish(true);
  };
  const handleClosePublish = () => {
    setPublish(false);
  };
  const handleDbClick = (data: IComponentData, dirID: number) => {
    commonStore.open = true;
    commonStore.selectId = dirID as number;
  };
  const handleUpdateTree = () => {
    commonStore.load(query);
  };
  useEffect(() => {
    debounceCallback(() => {
      handleUpdateTree();
    }, 500);
  }, [query]);

  const handleChangeQuery = (evt) => {
    setQuery(evt.target.value);
  };
  const getClickComponent = (el: HTMLElement) => {
    let data = el.getAttribute("data-data");

    if (data) {
      return JSON.parse(data);
    }

    while (el && !el.hasAttribute("data-data")) {
      el = el.parentElement;
      if (!el || el.id === "leftPanel") return null;
    }

    data = el.getAttribute("data-data");

    if (data) {
      return JSON.parse(data);
    }
  };

  const handleDeleteCom = () => {
    if (!clickComponent) return;
    commonStore.deleteComponent(clickComponent);

    closeMenu();
  };

  const handleExpand = (key: number, evt: React.MouseEvent) => {
    evt.stopPropagation();
    if (evt.button === 0) {
      commonStore.toggleExpand(key);
    }
    commonStore.selectId = key as number;
    commonStore.current = null;
  };

  const clear = (evt: React.MouseEvent) => {
    if (evt.button === 2) {
      commonStore.selectId = 0;
      commonStore.current = null;
    }
  };

  const handleAddDir = () => {
    closeMenu();
    commonStore.addDir();
  };

  const handleRenameNode = () => {
    closeMenu();
    commonStore.startRenameDir();
  };
  const handleDeleteNode = () => {
    closeMenu();
    commonStore.deleteDir();
  };

  const handleMouseDown = (c: any, evt: React.MouseEvent) => {
    evt.stopPropagation();
    commonStore.current = c;
  };

  const rightClickEditor = () => {
    handleDbClick(commonStore.current, commonStore.selectId);
    closeMenu();
  };

  return (
    <Observer>
      {() => (
        <>
          <Stack flexDirection="row" sx={{ height: "28px" }}>
            <SearchInput
              value={query}
              onChange={handleChangeQuery}
              placeholder="搜索组件"
              sx={{ flex: 1 }}
            />
            <IconButton
              onClick={handleAddDir}
              aria-label="delete"
              color="primary"
              sx={{ width: 28, ml: 1 }}
            >
              <IconAli name="icon-3Da-tianjia3x" fontSize={28} />
            </IconButton>

            {/* <Button onClick={handleAddDir}>新建目录</Button> */}
          </Stack>
          <List
            sx={{
              height: "90%",
              width: "100%",
              overflow: "auto",
              paddingTop: "0px",
            }}
            onContextMenu={openMenu}
            onMouseDown={clear}
          >
            {/* <CollapseContainer
            target="test"
            onExpand={handleExpand}
            open={collkeys.has("test")}
            title="测试"
          >
            <DataList
              onAddData={handleOpen}
              list={commonStore.components as IDataItemProps[]}
              component={DragCard}
              onDoubleClick={(c: any) => handleDbClick(c)}
              canPreview
            />
            
          </CollapseContainer> */}

            {/* {commonStore.dirList.length === 0 && !query && (
            <SpaceAddHint onClick={handleAddDir} title="新建目录开始创建" />
          )} */}

            {commonStore.dirList.map(
              (dir) =>
                dir.node_type === "dir" && (
                  <CollapseContainer
                    title={dir.name}
                    open={commonStore.isExpand(dir.id)}
                    onExpand={handleExpand}
                    target={dir.id}
                    key={dir.id}
                    editor={dir.editor}
                    onBlur={(evt) => {
                      if (evt.target.value) dir.name = evt.target.value;
                      commonStore.handleDir(dir);
                    }}
                    select={dir.id === commonStore.selectId}
                  >
                    <DataList
                      onContextMenu={openMenu}
                      list={dir.children ?? []}
                      component={DragCard}
                      componentProps={(f) => ({
                        "data-nodeid": f.id,
                        "data-nodetype": "file",
                      })}
                      // onClick={onClick}
                      canPreview
                      onMouseDown={handleMouseDown}
                      onAddData={() => handleOpen(dir.id)}
                      onDoubleClick={(c: any) => handleDbClick(c, dir.id)}
                    />
                  </CollapseContainer>
                )
            )}

            {commonStore.open && (
              <EditorComponentModal
                onClose={handleClose}
                current={commonStore.current}
              />
            )}
            {publish && (
              <PublishModal
                target={commonStore.current && commonStore.current.id}
                onClose={handleClosePublish}
                type="file"
                category={ECategory.Component}
                targetObj={commonStore.current}
              />
            )}
            <MenuContainer>
              {!commonStore.current && (
                <MenuItem key="new" onClick={handleAddDir}>
                  新建目录
                </MenuItem>
              )}
              {!!commonStore.selectId && !commonStore.current && (
                <MenuItem key="rename" onClick={handleRenameNode}>
                  重命名
                </MenuItem>
              )}
              {!!commonStore.selectId && !commonStore.current && (
                <MenuItem key="delete-dir" onClick={handleDeleteNode}>
                  删除目录
                </MenuItem>
              )}
              {commonStore.current && (
                <MenuItem key="delete" onClick={handleDeleteCom}>
                  删除
                </MenuItem>
              )}
              {commonStore.current && (
                <MenuItem key="publish" onClick={handleOpenPublish}>
                  发布
                </MenuItem>
              )}
              {commonStore.current && (
                <MenuItem key="edit" onClick={rightClickEditor}>
                  编辑
                </MenuItem>
              )}
            </MenuContainer>
          </List>
        </>
      )}
    </Observer>
  );
});

export default CommonTab;
