import Editor, { BeforeMount, Monaco, loader } from "@monaco-editor/react";
import React, { useRef } from "react";

import { defaultCode } from "../commonTab/defaultCode";
import { TEditor } from "./types";

loader.config({
  paths: {
    vs: "/static/libs/monaco-editor/vs",
  },
});

interface EditorPanelProps {
  updateProperyty: () => void;
  getEditor: (editor) => void;
  onChange: (errors: string[]) => void;
  onMount: () => void;
  code?: string;
}

const CodeEditor: React.FC<EditorPanelProps> = ({
  updateProperyty,
  onChange,
  getEditor,
  onMount,
  code,
}) => {
  const editorRef = useRef<TEditor>(null);
  const mRef = useRef<Monaco>(null);

  const beforeMount: BeforeMount = async (monaco) => {
    const typescript = monaco.languages.typescript;
    typescript.javascriptDefaults.setCompilerOptions({
      noLib: false,
      allowNonTsExtensions: true, // required to prevent Uncaught Error: Could not find file: 'inmemory://model/1'.
    });

    let libContent = await fetch("https://hc3dol.gkiiot.com/hc.d.ts").then(
      (res) => res.text()
    );

    const startIndex = libContent.indexOf("declare");
    libContent =
      "declare module HC { \n" + libContent.slice(startIndex) + "\n}";

    typescript.javascriptDefaults.addExtraLib(libContent, "hc.d.ts");
  };

  const handleEditorDidMount = (editor: TEditor, monaco: Monaco) => {
    getEditor(editor);
    editorRef.current = editor;
    mRef.current = monaco;

    editor.addCommand(
      monaco.KeyMod.CtrlCmd | monaco.KeyCode.KeyS,
      function (evt) {
        updateProperyty();
      }
    );
    onMount();
  };

  const handleChange = (marks) => {
    onChange(marks.map((s) => s.message));
  };

  return (
    <Editor
      onMount={handleEditorDidMount}
      height="100%"
      defaultLanguage="javascript"
      defaultValue={code ?? defaultCode}
      options={{ minimap: { enabled: false } }}
      onValidate={handleChange}
      beforeMount={beforeMount}
    />
  );
};

export default CodeEditor;
