import React, { useState, useEffect } from "react";
import {
  MenuItem,
  TextField,
  Paper,
  Grid,
  CardMedia,
  SxProps,
} from "@mui/material";
import Dialog from "@mui/material/Dialog";
import {
  Box,
  Button,
  DialogActions,
  DialogContent,
  DialogTitle,
  FormControl,
  InputLabel,
} from "@mui/material";
import Select, { SelectChangeEvent } from "@mui/material/Select";
import { styled } from "@mui/material/styles";
import { OpusApi, MarketApi, AppApi } from "~/apis";
import { getReq, postReq, ResponseStatus } from "~/utils/axios";
import { message } from "~/utils/message";
import { AppConfig } from "@hc/engine";
import { IDataItemProps } from "../dataList/types";
import { IComponentData } from "../editorModal/types";
import Tags from "~/components/common/hcTagsInput";
interface PublishModalProps {
  target: number;
  onClose: () => void;
  type: string;
  category;
  targetObj?: IDataItemProps | IComponentData;
}
const Item = styled(Paper)(({ theme }) => ({
  ...theme.typography.body2,
  textAlign: "center",
  color: theme.palette.text.secondary,
  height: 135,
  lineHeight: "128px",
  cursor: "pointer",
}));

const MediaCardStyles: SxProps = {
  width: "100%",
  height: 135,
  backgroundSize: "100%",
};
const PublishModal: React.FC<PublishModalProps> = ({
  target,
  onClose,
  type,
  category,
  targetObj,
}) => {
  const [name, setName] = React.useState(targetObj ? targetObj.title : "");
  const [industry, setIndustry] = React.useState("");
  const [sourceId, setSourceId] = React.useState("");
  const [tags, setTags] = React.useState("");
  const [remark, setRemark] = React.useState("");
  const [picKey, setPicKey] = React.useState("");
  const [picData, setPicData] = React.useState("");
  const [trade, setTrade] = React.useState("");
  const [trades, setTrades] = React.useState([]);
  const [score, setScore] = React.useState(0);
  const [accept, setAccept] = React.useState("image/png, image/jpeg");
  const [appIds, setAppIds] = React.useState<Array<number | string>>([]);
  const publish = async () => {
    const result = await postReq(OpusApi.publish, {
      name: name,
      opus_type: "3d",
      source_id: category === "M0001" ? appIds : [target],
      source_type: type,
      pic_key: picKey,
      pic_data: picData,
      tags: tags,
      remark: remark,
      industry_id: trade,
      category: category,
      score: score,
    });
    if (result.code === ResponseStatus.Ok) {
      message.success("发布成功");
      onClose();
    }
  };
  const getTrade = async () => {
    const result = await getReq(MarketApi.trade);
    if (result.code === ResponseStatus.Ok) {
      let list = result.data && result.data.list;
      setTrades([...list]);
      setTrade(list[0].value);
    }
  };

  const handleConfirm = () => {
    publish();
  };
  const handleCancel = () => {
    onClose();
  };
  const handleEnterKey = (e) => {
    if (e.nativeEvent.keyCode === 13) {
      //e.nativeEvent获取原生的事件对像
      handleConfirm();
    }
  };

  const handleChangeTrade = (event: SelectChangeEvent) => {
    setTrade(event.target.value);
  };
  const handleChangeName = (e: React.ChangeEvent<HTMLInputElement>) => {
    setName(e.target.value);
  };
  // const handleChangeTags = (e: React.ChangeEvent<HTMLInputElement>) => {
  //   setTags(e.target.value);
  // };
  const handleChangeRemark = (e: React.ChangeEvent<HTMLInputElement>) => {
    setRemark(e.target.value);
  };
  const handleChangeScore = (e: React.ChangeEvent<HTMLInputElement>) => {
    setScore(Number(e.target.value));
  };
  const onChangeFile = (event) => {
    const file = event.target.files[0];
    if (!file) {
      return;
    }
    setPicKey(file.name);
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = function (e) {
      if (typeof e.target.result === "string") {
        setPicData(e.target.result);
      }
    };
  };

  const getApp = async (param?: {}) => {
    let queryParam = {
      project_type: "3d",
      ...param,
    };
    const result = await getReq(AppApi.list, { params: queryParam });
    if (result.code === ResponseStatus.Ok) {
      let list = result.data || [];
      let arr = [];
      for (let item of list) {
        arr.push(item.id);
      }
      setAppIds(arr);
    }
  };

  useEffect(() => {
    getTrade();
    const image = new Image();
    image.crossOrigin = "";
    image.src = AppConfig.assetURL + targetObj.preview;
    image.onload = function () {
      const base64 = getBase64Image(image);
      setPicKey(targetObj.title);
      setPicData(base64);
    };
    if (category === "M0001") {
      getApp({ project_id: target });
    }
  }, []);
  function getBase64Image(img) {
    const canvas = document.createElement("canvas");
    canvas.width = img.width;
    canvas.height = img.height;
    const ctx = canvas.getContext("2d");
    ctx.drawImage(img, 0, 0, img.width, img.height);
    const ext = img.src.substring(img.src.lastIndexOf(".") + 1).toLowerCase();
    const dataURL = canvas.toDataURL("image/" + ext);
    return dataURL;
  }
  const handleChangeTags = (val) => {
    setTags(val.join(","));
  };
  return (
    <Dialog open onClose={onClose}>
      <DialogTitle>发布</DialogTitle>
      <DialogContent>
        <Box sx={{ m: 1 }}>
          <InputLabel htmlFor="component-simple">名称</InputLabel>
          <TextField
            sx={{ width: "500px" }}
            id="outlined-multiline-static"
            size="small"
            value={name}
            onChange={handleChangeName}
          />
        </Box>
        <Grid container sx={{ ml: 1, width: "500px" }}>
          <Grid item xs={6}>
            <InputLabel id="demo-simple-select-autowidth-label">
              行业
            </InputLabel>
            <FormControl sx={{ width: "240px" }}>
              <Select
                labelId="demo-simple-select-autowidth-label"
                id="demo-simple-select-autowidth"
                value={trade}
                onChange={handleChangeTrade}
                autoWidth
                size="small"
              >
                {trades.map((trade) => (
                  <MenuItem
                    key={trade.id}
                    value={trade.value}
                    sx={{ width: "240px" }}
                  >
                    {trade.label}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
          </Grid>
          <Grid item xs={6}>
            <InputLabel htmlFor="component-simple">引用积分</InputLabel>
            <TextField
              sx={{ width: "250px" }}
              id="outlined-multiline-static"
              size="small"
              value={score}
              onChange={handleChangeScore}
              type="number"
            />
          </Grid>
        </Grid>
        {/* {category === "M0001" && (
          <Box sx={{ m: 1 }}>
            <InputLabel htmlFor="component-simple">场景</InputLabel>
            <TextField
              sx={{ width: "240px" }}
              id="outlined-multiline-static"
              defaultValue="Default Value"
              size="small"
              value={sourceId}
            />
          </Box>
        )} */}

        {/* <Box sx={{ m: 1 }}>
          <InputLabel htmlFor="component-simple">标签</InputLabel>
          <TextField
            sx={{ width: "500px" }}
            id="outlined-multiline-static"
            size="small"
            value={tags}
            onChange={handleChangeTags}
            placeholder="多标签按逗号隔开"
          />
        </Box> */}
        <Box sx={{ m: 1 }}>
          <InputLabel htmlFor="component-simple">标签</InputLabel>
          <Tags onChange={handleChangeTags} />
        </Box>
        <Box sx={{ m: 1 }}>
          <InputLabel htmlFor="component-simple">描述</InputLabel>
          <TextField
            sx={{ width: "500px" }}
            id="outlined-multiline-static"
            multiline
            rows={3}
            value={remark}
            onChange={handleChangeRemark}
          />
        </Box>
        <Box sx={{ m: 1 }}>
          <InputLabel htmlFor="component-simple">封面</InputLabel>
          <Box
            sx={{
              display: "flex",
              "& > :not(style)": {
                m: 1,
                width: 240,
                height: 135,
              },
            }}
          >
            <label
              style={{ cursor: "pointer", opacity: 0, position: "absolute" }}
            >
              <input
                type="file"
                onChange={onChangeFile}
                accept={accept}
                style={{ display: "none" }}
              />
            </label>
            <Item elevation={3}>
              {!picData && "文件上传"}
              {picData && (
                // <img src={picData} height="100%"></img>
                <CardMedia image={picData} sx={MediaCardStyles} />
              )}
            </Item>
          </Box>
        </Box>
      </DialogContent>
      <DialogActions>
        <Button color="primary" onClick={handleCancel}>
          取消
        </Button>
        <Button color="primary" onClick={handleConfirm}>
          确认发布
        </Button>
      </DialogActions>
    </Dialog>
  );
};
export default PublishModal;
