import { Entity, Interaction, MoveType, ObjectId, Utils } from "@hc/engine";
import React from "react";
import MoveActionDetail from "./actionDetails/move";
import RotateActionDetail from "./actionDetails/rotate";
import SetVariableDetail from "./actionDetails/variableDetail";
import { convert2UiData, uiVector2Vector } from "~/utils/adapter";
import { toJS } from "mobx";
import HighlightDetail from "./actionDetails/highlight";
import WaitDetail from "./actionDetails/wait";
import DispatchDetail from "./actionDetails/dispatch";
import ScaleDetail from "./actionDetails/scale";
import OpenDetail from "./actionDetails/open";
import DisplayDetail from "./actionDetails/display";
import OpacityDetail from "./actionDetails/opacity";
import MoveCameraDetail from "./actionDetails/moveCamera";
import { EventLabel } from "./dataList";
import { getUUID } from "~/utils/common";

export const getName = (objectId: ObjectId<Entity>) => {
  return objectId?.Object?.Name ?? "";
};

export const getScaleData = (objectId: ObjectId<Entity>, bySize: boolean) => {
  const en = objectId.Object;
  if (!en) return { x: "x", y: "Y", z: "Z" };
  if (bySize) {
    const size = en.BoundingBox.Size;
    return {
      x: (2 * size.x).toString(),
      y: (2 * size.y).toString(),
      z: (2 * size.z).toString(),
    };
  } else {
    return convert2UiData(en.Scale);
  }
};

export const InteractionConfig: {
  [key: string]: {
    detail: JSX.Element; //动作详情UI
    schema: any;
    defaultData?: (evt) => any; //添加元件目标时的初始数据
    handleData?: (evt) => any; //传递给元件之前处理成元件所需要的数据
    defaultActionData?: () => any[]; //添加默认动作数据
    getDesc?: (d) => string; //获取元件描述
    loadWidgetData?: (wg) => any; //加载元件数据到store
  };
} = {
  [Interaction.Move.type]: {
    detail: <MoveActionDetail />,
    schema: {
      label: Interaction.Move.schema.label,
    },
    defaultData: (objectId: ObjectId) => ({
      objectId,
      target: { x: "0", y: "0", z: "0" },
      time: "1000",
      type: MoveType.To,
    }),
    handleData: (data: any[]) =>
      data.map((d) => ({
        objectId: d.objectId,
        time: parseFloat(d.time) / 1000,
        target: uiVector2Vector(d.target),
        type: d.type,
        animationType: "",
      })),
    loadWidgetData(wg: Interaction.Move) {
      const data = [];
      for (const p of wg.List) {
        data.push({
          objectId: p.objectId,
          type: p.type,
          animationType: p.animationType,
          time: (p.time * 1000).toString(),
          target: convert2UiData(p.target),
        });
      }
      return data;
    },
    getDesc: (d) => {
      const name = getName(d.objectId);

      if (!name) return "未选择元件";

      return `${name} ${d.type === MoveType.Offset ? "经过" : "到达"} ${
        d.target.x
      },${d.target.y},${d.target.z} 移动 ${d.time}毫秒`;
    },
  },
  [Interaction.Rotate.type]: {
    detail: <RotateActionDetail />,
    schema: {
      label: Interaction.Rotate.schema.label,
    },
    defaultData: (objectId: ObjectId) => ({
      objectId,
      pivot: { x: "0", y: "0", z: "0" },
      axis: { x: "0", y: "1", z: "0" },
      time: "1000",
      type: MoveType.To,
      dir: 1,
      angle: "90",
    }),
    handleData: (data: any[]) =>
      data.map((d) => ({
        objectId: d.objectId,
        time: parseFloat(d.time) / 1000,
        angle: Utils.deg2rad(parseFloat(d.angle)) || 0,
        type: d.type,
        pivot: { ...d.pivot },
        axis: { ...d.axis },
        animationType: "",
        dir: d.dir,
      })),
    loadWidgetData(wg: Interaction.Rotate) {
      const data = [];
      for (const p of wg.List) {
        data.push({
          objectId: p.objectId,
          type: p.type,
          angle: Utils.rad2Deg(p.angle).toString(),
          animationType: p.animationType,
          time: (p.time * 1000).toString(),
          pivot: p.pivot,
          axis: p.axis || { x: "0", y: "1", z: "0" },
          dir: p.dir,
        });
      }
      return data;
    },
    getDesc: (d) => {
      const name = getName(d.objectId);

      if (!name) return "未选择元件";

      if (!d.axis) d.axis = { x: 0, y: 1, z: 0 };

      return `${getName(d.objectId)} 绕${d.axis?.x},${d.axis?.y},${
        d.axis?.z
      },基于点${d.pivot?.x},${d.pivot?.y},${d.pivot?.z} ${
        d.type === MoveType.Offset ? "经过" : "到达"
      } ${d.angle}° 旋转 ${d.time}毫秒`;
    },
  },
  [Interaction.Scale.type]: {
    detail: <ScaleDetail />,
    schema: {
      label: Interaction.Scale.schema.label,
    },
    defaultData: (objectId: ObjectId<Entity>) => ({
      objectId,
      bySize: true,
      scale: getScaleData(objectId, true),
      pivot: { x: "0", y: "0", z: "0" },
      time: "1000",
    }),
    handleData: (data: any[]) =>
      data.map((d) => ({
        objectId: d.objectId,
        bySize: d.bySize,
        time: parseFloat(d.time) / 1000,
        scale: { ...d.scale },
        pivot: { ...d.pivot },
        animationType: "",
      })),
    loadWidgetData(wg) {
      const data = [];
      for (const p of wg.List) {
        data.push({
          objectId: p.objectId,
          type: p.type,
          bySize: p.bySize,
          scale: p.scale,
          pivot: p.pivot,
          animationType: p.animationType,
          time: (p.time * 1000).toString(),
        });
      }
      return data;
    },
    getDesc: (d) => {
      const name = getName(d.objectId);

      if (!name) return "未选择元件";

      return `${getName(d.objectId)} ${d.bySize ? "按尺寸" : "按比例"}基于锚点${
        d.pivot.x
      },${d.pivot.y},${d.pivot.z} 缩放到${d.scale.x},${d.scale.y},${
        d.scale.z
      } 缩放 ${d.time}毫秒`;
    },
  },
  [Interaction.Select.type]: {
    detail: null,
    schema: {
      label: Interaction.Select.schema.label,
    },
    defaultData: (objectId: ObjectId) => ({
      objectId,
      type: "select",
    }),
    loadWidgetData(wg) {
      const data = [];
      for (const p of wg.List) {
        data.push({
          objectId: p.objectId,
          type: p.type,
        });
      }
      return data;
    },
  },
  [Interaction.SetVariable.type]: {
    detail: <SetVariableDetail />,
    schema: {
      label: Interaction.SetVariable.schema.label,
    },
    defaultData: (key: string) => ({
      key,
      value: {
        type: "value",
        value: "",
      },
    }),
    handleData: (data: any[]) => toJS(data),
    loadWidgetData(wg) {
      return wg.List;
    },
    getDesc: (d) => {
      return `${d.key}为${d.value.value}`;
    },
  },
  [Interaction.MoveCamera.type]: {
    detail: <MoveCameraDetail />,
    schema: {
      label: Interaction.MoveCamera.schema.label,
    },
    defaultData: null,
    handleData: (data: any[]) => {
      const d = data[0];
      if (d)
        return {
          alpha: d.alpha ? Utils.deg2rad(parseFloat(d.alpha)) : undefined,
          beta: d.beta ? Utils.deg2rad(parseFloat(d.beta)) : undefined,
          radius: d.radius ? parseFloat(d.radius) : undefined,
          target: d.target ? uiVector2Vector(d.target) : undefined,
          time: d.time ? parseFloat(d.time) / 1000 : undefined,
        };
    },
    defaultActionData: () => [
      {
        alpha: "180",
        beta: "0",
        radius: "100",
        target: { x: "", y: "", z: "" },
        time: "1000",
        current: false,
      },
    ],
    getDesc: (d) => {
      return `相机横向旋转${d.alpha},纵向旋转${d.beta},移动距离${d.radius},朝向${d.target.x},${d.target.y},${d.target.z} 移动 ${d.time}毫秒`;
    },
    loadWidgetData(wg) {
      return [
        {
          alpha: Utils.rad2Deg(wg._alpha),
          beta: Utils.rad2Deg(wg._beta),
          radius: wg._radius.toString(),
          target: convert2UiData(wg._target),
          time: wg._time,
          current: false,
        },
      ];
    },
  },
  [Interaction.Open.type]: {
    detail: <OpenDetail />,
    schema: {
      label: Interaction.Open.schema.label,
    },
    defaultData: null,
    loadWidgetData(wg) {
      return [{ ...wg.data }];
    },
    defaultActionData: () => [
      {
        type: "url",
        value: "",
        target: "_blank",
      },
    ],
    handleData: (data: any[]) => {
      const d = data[0];
      if (d)
        return {
          ...d,
        };
    },
    getDesc(d) {
      if (d.type === "url") {
        if (!d.value) {
          return "未输入地址";
        } else {
          return `打开${d.value}`;
        }
      }
      return "打开链接";
    },
  },
  [Interaction.Highlight.type]: {
    detail: <HighlightDetail />,
    schema: {
      label: Interaction.Highlight.schema.label,
    },
    defaultData: (objectId: ObjectId) => ({
      objectId,
      color: "#ffffff",
    }),
    loadWidgetData(wg) {
      return wg.List;
    },
    getDesc(d) {
      return `高亮${getName(d.objectId)}，颜色为 ${d.color}`;
    },
  },
  [Interaction.Display.type]: {
    detail: <DisplayDetail />,
    schema: {
      label: Interaction.Display.schema.label,
    },
    defaultData: (objectId: ObjectId) => ({
      objectId,
      type: "Show",
    }),
    loadWidgetData(wg) {
      return wg.List;
    },
    getDesc(d) {
      const name = getName(d.objectId);

      if (!name) return "未选择元件";

      let label = "";
      if (d.type === "Show") label = "显示";
      else if (d.type === "Hidden") label = "隐藏";
      else label = "切换";
      return `${label} ${getName(d.objectId)}`;
    },
  },
  [Interaction.Wait.type]: {
    detail: <WaitDetail />,
    schema: {
      label: Interaction.Wait.schema.label,
    },
    handleData: (data: any[]) => {
      const d = data[0];
      if (d)
        return {
          time: parseFloat(d.time),
        };
    },
    defaultActionData: () => [
      {
        time: "1000",
      },
    ],
    getDesc: (d) => {
      return `等待${d.time}毫秒`;
    },
    loadWidgetData(wg) {
      return [
        {
          time: wg._time.toString(),
        },
      ];
    },
  },
  [Interaction.Dispatch.type]: {
    detail: <DispatchDetail />,
    schema: {
      label: Interaction.Dispatch.schema.label,
    },
    defaultData: (objectId: ObjectId) => ({
      objectId,
      triggerNames: [],
    }),
    handleData: (data: any[]) => {
      return data.map((d) => ({
        objectId: d.objectId,
        triggerNames: d.triggerNames.map((t) => t.type),
      }));
    },
    getDesc: (d) => {
      const names = d.triggerNames.filter((t) => !!t);
      return `触发${!d.objectId ? "页面" : getName(d.objectId)}的${names
        .map((t) => t.label)
        .join(",")}事件`;
    },
    loadWidgetData(wg) {
      return wg.List.map((t) => {
        return {
          objectId: t.objectId,
          triggerNames: t.triggerNames.map((tn) => ({
            type: tn,
            label: EventLabel[tn],
            id: getUUID(),
          })),
        };
      });
    },
  },
  [Interaction.Opacity.type]: {
    detail: <OpacityDetail />,
    schema: {
      label: Interaction.Opacity.schema.label,
    },
    defaultData: (objectId: ObjectId) => ({
      objectId,
      opacity: "100",
      time: "1000",
      animationType: "",
    }),
    handleData(data: any[]) {
      return data.map((d) => ({
        ...d,
        objectId: d.objectId,
        opacity: d.opacity / 100,
        time: d.time / 1000,
      }));
    },
    loadWidgetData(wg) {
      const data = [];
      for (const p of wg.List) {
        data.push({
          objectId: p.objectId,
          animationType: p.animationType,
          time: (p.time * 1000).toString(),
          opacity: (p.opacity * 100).toString(),
        });
      }
      return data;
    },
    getDesc: (d) => {
      const name = getName(d.objectId);

      if (!name) return "未选择元件";

      return `设置${getName(d.objectId)} 的透明度为${d.opacity}% 用时 ${
        d.time
      }毫秒`;
    },
  },
};
