import React, { useEffect, useState } from "react";
import Menu from "@mui/material/Menu";
import MenuItem from "@mui/material/MenuItem";
import { useStores } from "~/contexts/store-context";
import Typography from "@mui/material/Typography";
import { observer } from "mobx-react-lite";
import { useNavigate } from "react-router-dom";
import { getReq, ResponseStatus } from "~/utils/axios";
import { ProjectData } from "~/apis/types";
import { ProjectApi } from "~/apis";
import { ProjectList } from "~/apis/types";
import { reaction } from "mobx";
import { debounceCallback } from "~/utils/time";
interface ProjectListProps {}

const Project: React.FC<ProjectListProps> = (ProjectListProps) => {
  const navigate = useNavigate();
  const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);
  const [list, setList] = React.useState<ProjectList[]>([]);
  const { rootStore } = useStores();
  const open = Boolean(anchorEl);
  const hotkey = () => {};
  const handleClose = () => {
    setAnchorEl(null);
  };
  const [page, setPage] = React.useState({
    page: 1,
    limit: 20,
    project_type: "3d",
  });
  const update = async () => {
    const data = await getReq<ProjectData>(ProjectApi.list, { params: page });
    if (data.code === ResponseStatus.Ok) {
      let list = data.data.list.filter(
        (item) => item.id !== rootStore.projectId
      );
      setList(list);
    } else {
      setList([]);
    }
  };
  useEffect(() => {
    reaction(
      () => rootStore.projectId,
      () => {
        update();
      }
    );
  }, [page]);
  useEffect(() => {
    update();
  }, [page]);
  let timer;
  const mouseEnter = () => {
    return (event) => {
      let currentTarget = event.currentTarget;
      timer = setTimeout(() => {
        setAnchorEl(currentTarget);
      }, 500);
    };
  };
  const mouseLeave = () => {
    return () => {
      clearTimeout(timer);
    };
  };
  return (
    <>
      <Typography
        onMouseEnter={mouseEnter()}
        onMouseLeave={mouseLeave()}
        variant="h6"
        noWrap
        component="div"
        sx={{
          mr: 2,
          color: "#444",
          display: { xs: "none", md: "flex" },
          cursor: "pointer",
        }}
      >
        {rootStore.projectName || "Untitled"}
      </Typography>
      <Menu
        id="basic-menu"
        anchorEl={anchorEl}
        open={open}
        onClose={handleClose}
        MenuListProps={{
          "aria-labelledby": "basic-button",
        }}
        sx={{
          cursor: "pointer",
        }}
      >
        <MenuItem
          sx={{ fontSize: "12px" }}
          onClick={() => {
            navigate("/list/project");
          }}
        >
          全部项目
        </MenuItem>
        {list.map((item) => (
          <MenuItem
            key={item.id}
            sx={{ fontSize: "12px" }}
            onClick={() => {
              navigate("/editor");
              window.location.search = "?projectId=" + item.id;
            }}
          >
            {item.name}
          </MenuItem>
        ))}
      </Menu>
    </>
  );
};

export default observer(Project);
