import { Button, DialogTitle, Divider, Stack, Typography } from "@mui/material";
import React from "react";
import CloseIcon from "@mui/icons-material/Close";

interface DialogHeaderProps {
  onClose: (e, r?) => void;
  title: string;
  [key: string]: any;
}

const DialogHeader: React.FC<DialogHeaderProps> = ({
  onClose,
  title,
  sx,
  ...other
}) => {
  return (
    <DialogTitle sx={sx}>
      <Stack flexDirection="row" justifyContent="space-between" {...other}>
        <Typography variant="h5">{title}</Typography>
        <Button
          onClick={onClose}
          size="large"
          startIcon={<CloseIcon />}
        ></Button>
      </Stack>
    </DialogTitle>
  );
};
export default DialogHeader;
