import {
  Checkbox,
  DialogContent,
  FormControlLabel,
  Typography,
} from "@mui/material";
import Dialog from "@mui/material/Dialog";
import DialogTitle from "@mui/material/DialogTitle";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import ListItemText from "@mui/material/ListItemText";
import * as React from "react";
import { ProjectApi } from "~/apis";
import { ProjectData, ProjectList } from "~/apis/types";
import { useStores } from "~/contexts/store-context";
import { rootStore } from "~/stores/RootStore";
import { getReq, ResponseStatus } from "~/utils/axios";
import { message } from "~/utils/message";
import FolderIcon from "@mui/icons-material/Folder";
import DataList from "~/views/largeScreen/DataList";

interface SelectProjectListProps {
  onClick: (id) => void;
  disableId?: number;
  selectId?: number;
}

const SelectProjectList: React.FC<SelectProjectListProps> = ({
  onClick,
  disableId,
  selectId,
}) => {
  const [list, setList] = React.useState<ProjectList[]>([]);
  const [page, setPage] = React.useState({
    page: 1,
    limit: 20,
    project_type: "3d",
  });

  const update = async () => {
    const data = await getReq<ProjectData>(ProjectApi.list, { params: page });
    if (data.code === ResponseStatus.Ok) {
      setList(data.data.list);
    } else {
      setList([]);
    }
  };

  React.useEffect(() => {
    update();
  }, [page]);

  return (
    <List
      sx={{
        pt: 0,
        width: 500,
        maxHeight: 300,
        overflow: "auto",
        outline: "1px solid #ccc",
        p: "14px",
      }}
      subheader={<Typography>项目列表</Typography>}
    >
      {list.map((d) => (
        <ListItem
          disabled={disableId === d.id}
          autoFocus
          button
          key={d.id}
          onClick={() => onClick(d.id)}
          sx={{
            "&:hover": { backgroundColor: "#ccc" },
            backgroundColor: selectId === d.id ? "#ccc" : undefined,
          }}
        >
          <FolderIcon sx={{ mr: 2, color: "#df9c26" }} />
          <ListItemText primary={d.name} />
        </ListItem>
      ))}
    </List>
  );
};
export default SelectProjectList;
