import { IVariableOption, WidgetValue } from "@hc/engine";
import { TextField, TextFieldProps } from "@mui/material";
import { observer } from "mobx-react-lite";
import React from "react";
import {
  CommonSelectEntity,
  CommonSelectParam,
} from "~/components/common/commonInteraction";
import CommonSelect from "~/components/common/select";
import { getSelectKey, ValueOptions } from "../utils";

interface ValueTypeSelectProps {
  onChange: (evt) => void;
  widgetValue: WidgetValue;
  variables: IVariableOption[];
  Container?: React.FC;
  selectProps?: TextFieldProps;
  containerProps?: any;
  onChangeValue?: (evt) => void;
  bool?: boolean;
}

const ValueTypeSelect: React.FC<ValueTypeSelectProps> = ({
  onChange,
  widgetValue,
  variables,
  Container = React.Fragment,
  selectProps = {},
  onChangeValue,
  bool,
}) => {
  const renderValueItem = () => {
    switch (widgetValue.type) {
      case "value":
        if (bool)
          return (
            <CommonSelect
              options={[
                { label: "真", value: 1 },
                { label: "假", value: 0 },
              ]}
              value={widgetValue.value ? 1 : 0}
              onChange={onChangeValue}
            />
          );
        else
          return (
            <TextField
              size="small"
              label="值"
              value={widgetValue.value}
              onChange={onChangeValue}
            />
          );
      case "param":
        return (
          <CommonSelectParam
            value={widgetValue.value}
            variables={variables}
            onChange={onChangeValue}
          />
        );
      case "entity":
        return (
          <CommonSelectEntity
            value={widgetValue}
            key="value"
            onChange={onChangeValue}
          />
        );
      default:
        return null;
    }
  };

  const value = React.useMemo(() => {
    return getSelectKey(widgetValue);
  }, [widgetValue.type, widgetValue.entityKey]);

  return (
    <Container>
      <CommonSelect
        options={ValueOptions}
        fullWidth
        value={value}
        sx={{ my: 1 }}
        onChange={onChange}
        {...selectProps}
      />
      {renderValueItem()}
    </Container>
  );
};
export default observer(ValueTypeSelect);
