import {
  HCFiler,
  ParticleSystem,
  ParticleSystemGroup,
  Viewer,
} from "@hc/engine";
import {
  Box,
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  Divider,
  FormControlLabel,
  Checkbox,
  TextField,
} from "@mui/material";
import React, { useEffect, useRef, useState } from "react";
import { DirSourceType } from "~/apis";
import { updateFile, updateFilePreview, uploadFile } from "~/apis/fileReq";
import { App } from "~/intance";
import { IResponseResult, ResponseStatus } from "~/utils/axios";
import { message } from "~/utils/message";
import { parseParticle } from "~/utils/parse";
import { isDebugBBL } from "~/utils/utils";

import DialogHeader from "../common/dialogHeader";
import CommonSelect from "../common/select";
import { HStack } from "../common/stack";
import { IDataItemProps } from "../dataList/types";
import { app } from "../editor";
import ParticleSystemProperty from "./particleSystemProperty";
import { ParticleTemplate } from "./templates";

interface PreviewProps {
  open: boolean;
  onClose: () => void;
  onUpdate: () => void;
  current: IDataItemProps;
  projectId: number;
  dirId: number;
}

let viewer: Viewer;

const Preview: React.FC<PreviewProps> = ({
  open,
  onClose,
  onUpdate,
  current,
  projectId,
  dirId,
}) => {
  const canvasRef = useRef<HTMLCanvasElement>(null);
  const [pcs, setPCS] = useState<ParticleSystem>(null);
  const [isGroup, setIsGroup] = useState(false);
  const [groupName, setGroupName] = useState("粒子特效组");
  const [pcsList, setPCSList] = useState<ParticleSystem[]>([]);

  const useTemplate = (evt) => {
    const tempKey = evt.target.value;
    if (pcs) pcs.destory();
    for (const pcs of pcsList) {
      pcs.destory();
    }
    if (tempKey) {
      const jsonData = ParticleTemplate[tempKey];

      if (Array.isArray(jsonData)) {
        setIsGroup(true);
        let newPcs = new ParticleSystemGroup();
        newPcs.fromJSON(jsonData);
        setPCSList(newPcs.systems);
        setPCS(newPcs.systems[0]);
        newPcs.Intance.start();
      } else {
        let newPcs = getNewPCS(false);
        if (jsonData) newPcs.fromJSON(jsonData);
        newPcs.Intance.start();
        setIsGroup(false);
        setPCSList([]);
        setPCS(newPcs);
      }
    }
  };

  const handleApply = () => {
    if (isGroup && pcsList.length > 1) {
      const group = new ParticleSystemGroup();
      group.systems = pcsList.map((p) => p.clone());
      app.Database.ParticleSystemSpace.append(group);
    } else {
      pcs && app.Database.ParticleSystemSpace.append(pcs.clone());
    }
    onClose();
  };

  const handleGroup = (evt, checked) => {
    setPCSList([pcs]);
    setIsGroup(checked);
  };
  const getNewPCS = (isStart = true) => {
    let newPcs = new ParticleSystem({
      image: "textures/flare.png",
      capacity: 100,
    });
    if (isStart) newPcs.Intance.start();
    return newPcs;
  };

  const addPCS = () => {
    const pcs = getNewPCS();
    setPCSList([...pcsList, pcs]);
    setPCS(pcs);
  };
  const removePCS = () => {
    const index = pcsList.indexOf(pcs);
    if (index !== -1) {
      pcs.destory();
      pcsList.splice(index, 1);
      setPCSList([...pcsList]);
      setPCS(pcsList[0]);
    }
  };

  const getParticleJSON = () => {
    const filer = new HCFiler();
    if (isGroup) {
      if (pcsList.length === 0) return null;
      const pcsGroup = new ParticleSystemGroup();
      pcsGroup.Name = groupName;
      pcsGroup.systems.push(...pcsList);
      pcsGroup.writeFile(filer);
    } else {
      if (!pcs) return null;
      pcs.writeFile(filer);
    }
    return JSON.stringify({
      version: 1,
      isGroup,
      data: filer.Data,
    });
  };

  const handleParticle = async () => {
    const jsonData = getParticleJSON();

    if (!jsonData) {
      message.error("特效数据有误");
      return;
    }
    let res: IResponseResult;
    if (current) {
      res = await updateFile({
        id: current.id,
        name: isGroup ? groupName : pcs.Name,
        json_data: jsonData,
        sourceType: DirSourceType.Particles,
        resources: "",
        pid: dirId,
      });
    } else {
      res = await uploadFile({
        name: isGroup ? groupName : pcs.Name,
        jsonData: jsonData,
        sourceType: DirSourceType.Particles,
        moduleIds: "",
        projectId,
        pid: dirId,
      });
    }

    if (res.code === ResponseStatus.Ok) {
      let id = current?.id ?? res.data?.[0]?.id;
      await updateFilePreview(id, () => viewer.getPreviewUrl(true));
      onUpdate();
    }

    onClose();
  };

  const initParticle = () => {
    if (current) {
      const jsonData = current.data.json;
      const pcs = parseParticle(jsonData);
      if (pcs instanceof ParticleSystemGroup) {
        setIsGroup(true);
        setGroupName(pcs.Name);
        setPCSList([...pcs.systems]);
        setPCS(pcs.systems[0]);
      } else {
        setPCS(pcs);
      }
      pcs.Intance.start();
    } else {
      let pcs = getNewPCS();
      setPCS(pcs);
    }
  };

  useEffect(() => {
    setTimeout(() => {
      if (canvasRef.current) {
        window["vw"] = viewer = App.intance.createView(canvasRef.current, {
          defaultLight: true,
        });
        initParticle();
      }
    }, 0);
    return () => {
      viewer && viewer.destory();
    };
  }, []);

  return (
    <Dialog
      PaperProps={{ sx: { height: "80vh" } }}
      maxWidth="lg"
      fullWidth
      onClose={onClose}
      open={open}
    >
      <DialogHeader
        onClose={onClose}
        title="添加特效"
        className="draggable-dialog-title"
      />
      <DialogContent>
        <HStack sx={{ height: "10%", alignItems: "center" }}>
          <FormControlLabel
            checked={isGroup}
            control={<Checkbox />}
            onChange={handleGroup}
            label="粒子组"
          />
          {isGroup && (
            <>
              <TextField
                size="small"
                label="组名称"
                InputProps={{ sx: { height: 30, width: 120 } }}
                value={groupName}
                onChange={(e) => setGroupName(e.target.value)}
              />
              {pcsList.map((p, index) => (
                <Button
                  variant="outlined"
                  size="small"
                  sx={{ height: 30, lineHeight: "30px" }}
                  key={index}
                  color={p === pcs ? "secondary" : "primary"}
                  onClick={() => setPCS(p)}
                >
                  {p.Name}
                </Button>
              ))}
              <Button onClick={addPCS}>添加</Button>
              <Button onClick={removePCS}>删除</Button>
            </>
          )}
        </HStack>
        <HStack sx={{ height: "90%" }}>
          {pcs ? (
            <ParticleSystemProperty
              pcs={pcs}
              sx={{ width: "60%", height: "100%" }}
            />
          ) : (
            <Box sx={{ width: "60%", height: "100%" }}></Box>
          )}
          <Divider orientation="vertical" />
          <Box sx={{ flex: 1 }}>
            <canvas ref={canvasRef}></canvas>
          </Box>
        </HStack>
      </DialogContent>
      <DialogActions>
        <CommonSelect
          defaultValue="default"
          options={[
            { label: "默认模板", value: "default" },
            { label: "下雪", value: "snow" },
            { label: "烟花", value: "fireworks" },
            { label: "下雨", value: "rain" },
          ]}
          onChange={useTemplate}
        />
        <Button onClick={handleApply}>应用</Button>
        <Button onClick={handleParticle}>{current ? "更新" : "保存"}</Button>
        {isDebugBBL() && (
          <Button
            size="small"
            onClick={() => {
              pcs &&
                pcs.Intance.getScene().debugLayer.show({
                  embedMode: true,
                });
            }}
          >
            调试
          </Button>
        )}
      </DialogActions>
    </Dialog>
  );
};
export default Preview;
