import {
  MenuItem,
  Grid,
  Divider,
  Stack,
  SxProps,
  Tab,
  Tabs,
  FormControl,
} from "@mui/material";
import Select, { SelectChangeEvent } from "@mui/material/Select";
import React, { useEffect, useState } from "react";
import { MarketApi } from "~/apis";
import { CategoryData, CategoryList } from "~/apis/types";
import { getReq, ResponseStatus } from "~/utils/axios";

import { a11yProps, TabPanel } from "../common/tabPanel";
import ContentList from "./contentList";
import { EMarketType } from "./types";

interface ResourceContentProps {
  type: EMarketType;
  contentType: 0 | 1 | 2;
  query: string;
  sx?: SxProps;
}

const ResourceContent: React.FC<ResourceContentProps> = ({
  type,
  contentType,
  query,
  sx = {},
}) => {
  const [catergorys, setCategorys] = useState<CategoryList[]>([]);
  const [catergory, setCategory] = useState<number>(-1);
  const [trade, setTrade] = useState("");
  const [trades, setTrades] = React.useState([]);
  const handleChange = (evt, value) => {
    setCategory(value);
  };
  const getXs = () => {
    if (contentType === 0) return 3;
    if (contentType === 1) return 3;
    else return 12;
  };

  const getApi = () => {
    switch (type) {
      case EMarketType.Coll:
        return MarketApi.collections;
      case EMarketType.Buyed:
        return MarketApi.buyList;
      case EMarketType.Shop:
        return MarketApi.list;
      default:
        return "";
    }
  };
  const getTrade = async () => {
    const result = await getReq(MarketApi.trade);
    if (result.code === ResponseStatus.Ok) {
      let list = result.data && result.data.list;
      setTrades([...list]);
      setTrade("0");
    }
  };
  useEffect(() => {
    getTrade();
    (async () => {
      const res = await getReq<CategoryData>(MarketApi.category);
      if (res.code === ResponseStatus.Ok && res.data?.list) {
        const list = res.data.list.filter((c) => c.label !== "物模型");
        list.reverse();
        setCategorys(list);
        setCategory(list[0]?.id);
      } else {
        setCategorys([]);
      }
    })();
  }, []);
  const handleChangeTrade = (event: SelectChangeEvent) => {
    setTrade(event.target.value);
  };
  return (
    <>
      <Stack sx={{ borderBottom: 1, borderColor: "divider", ...sx }}>
        <Tabs
          value={catergory}
          onChange={handleChange}
          aria-label="resource list"
          centered
        >
          {catergorys.map((c) => (
            <Tab key={c.id} label={c.label} value={c.id} {...a11yProps(c.id)} />
          ))}
        </Tabs>

        <Divider />
        <Grid container sx={{ ml: 1, mt: 1, mb: 0.5, width: "300px" }}>
          <Grid>
            <FormControl sx={{ width: "300px" }}>
              <Select
                labelId="demo-simple-select-autowidth-label"
                id="demo-simple-select-autowidth"
                value={trade}
                onChange={handleChangeTrade}
                autoWidth
                size="small"
              >
                <MenuItem key={0} value="0" sx={{ width: "300px" }}>
                  全部行业
                </MenuItem>
                {trades.map((trade) => (
                  <MenuItem
                    key={trade.id}
                    value={trade.value}
                    sx={{ width: "300px" }}
                  >
                    {trade.label}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
          </Grid>
        </Grid>
        {catergorys.map((c, index) => (
          <TabPanel
            key={c.id}
            value={catergory}
            currentValue={c.id}
            style={{ flex: 1, overflowY: "auto" }}
          >
            <ContentList
              trade={trade}
              api={getApi()}
              category={c.value === "M0004" ? "M0003,M0004" : c.value}
              xs={getXs()}
              type={type}
              query={query}
            />
          </TabPanel>
        ))}
      </Stack>
    </>
  );
};
export default ResourceContent;
