import { IVector3 } from "@hc/engine";
import { Box, Button, LinearProgress, Stack, Typography } from "@mui/material";
import { Observer } from "mobx-react-lite";
import React from "react";
import { App } from "~/intance";
import { isDebugBBL } from "~/utils/utils";
import CmdHistory from "./cmdHistory";

import FPS from "./fps";

interface BottomPanelProps {
  vector: IVector3;
  process: { loading: boolean; value: number };
}

const BottomPanel: React.FC<BottomPanelProps> = ({ vector, process }) => {
  const handleDebugger = () => {
    App.intance.Viewer.sceneControl.debugger({
      globalRoot: document.getElementById("editor"),
      overlay: true,
    });
  };

  return (
    <Observer>
      {() => (
        <Stack
          direction="row"
          alignItems="center"
          justifyContent="space-between"
          sx={{
            height: 30,
            color: "#ccc",
            background: "#1d1d1d",
          }}
        >
          <Stack
            direction="row"
            alignItems="center"
            spacing={2}
            sx={{ flex: 1 }}
          >
            <Typography sx={{ ml: 2 }}>x: {vector.x.toFixed(3)}</Typography>
            <Typography>y: {vector.y.toFixed(3)}</Typography>
            <Typography>z: {vector.z.toFixed(3)}</Typography>
          </Stack>
          <CmdHistory />
          <Stack
            direction="row"
            alignItems="center"
            justifyContent="flex-end"
            sx={{ flex: 1 }}
          >
            {process.loading && (
              <Box
                sx={{
                  height: 10,
                  display: "flex",
                  width: "50%",
                  alignItems: "center",
                }}
              >
                <Typography sx={{ mr: 2 }}>加载资源</Typography>
                <LinearProgress
                  sx={{
                    width: "50%",
                    height: 10,
                  }}
                  variant="determinate"
                  value={process.value}
                />
              </Box>
            )}
            <FPS />
            {isDebugBBL() && (
              <Button size="small" onClick={handleDebugger}>
                调试
              </Button>
            )}
          </Stack>
        </Stack>
      )}
    </Observer>
  );
};
export default BottomPanel;
