import React, { Fragment, Suspense } from "react";
import { Routes, Route, Navigate, Outlet } from "react-router-dom";
import EditorLayout from "~/layouts/editor-layout";
import LoadingScreen from "./../components/loadingScreen";
import ListLayout from "../views/list";
type IRouteConfig = {
  path?: string;
  component?: React.LazyExoticComponent<React.FC>;
  guard?: React.FC;
  layout?: React.FC;
  routes?: IRouteConfig[];
  parent?: boolean;
  index?: boolean;
  redirect?: string;
};

// const RequireAuth: React.FC<{ children?: React.ReactElement<any, any> }> = ({
//   children,
// }) => {
//   const token = localStorage.getItem("token");

//   if (token) return children;

//   return <Navigate to="/login" replace />;
// };
// guard: RequireAuth,
export const RoutesConfig: IRouteConfig[] = [
  {
    path: "/",
    parent: true,
    routes: [
      {
        path: "/list",
        parent: true,
        // redirect: "/list/project",
        layout: ListLayout,
        routes: [
          {
            index: true,
            path: "project",
            component: React.lazy(() => import("../views/list/Project")),
          },
          {
            path: "model",
            component: React.lazy(() => import("../views/list/Model")),
          },
          {
            path: "component",
            component: React.lazy(() => import("../views/list/Component")),
          },
          {
            path: "collect",
            component: React.lazy(() => import("../views/list/Collect")),
          },
          {
            path: "works",
            component: React.lazy(() => import("../views/list/Works")),
          },
          {
            path: "purchased",
            component: React.lazy(() => import("../views/list/Purchased")),
          },
          {
            path: "deploy",
            component: React.lazy(() => import("../views/list/Deploy")),
          },
          {
            path: "examine",
            component: React.lazy(() => import("../views/list/Examine")),
          },
          {
            path: "recycle",
            component: React.lazy(() => import("../views/list/Recycle")),
          },
        ],
      },
      {
        layout: EditorLayout,
        path: "/editor",
        component: React.lazy(() => import("../views/editor")),
      },
    ],
  },

  {
    path: "/login",
    component: React.lazy(() => import("../views/login")),
  },
  {
    path: "*",
    component: React.lazy(() => import("../views/404")),
  },
];

const renderList = (rs: IRouteConfig[]) => {
  return rs.map((route, i) => {
    const Guard = route.guard || Fragment;
    const Layout = route.layout || Fragment;
    const Component = route.component || Fragment;
    return (
      <Route
        key={i}
        path={route.path}
        index={route.index}
        element={
          // route.redirect ? (
          //   <Navigate to={route.redirect} />
          // ) : (
          <Guard>
            <Layout>
              <Component />
              {route.parent && <Outlet />}
            </Layout>
          </Guard>
          // )
        }
      >
        {route.routes && renderList(route.routes)}
      </Route>
    );
  });
};

export const RenderRoutes: React.FC<{ routes: IRouteConfig[] }> = ({
  routes,
}) => {
  return (
    <Suspense fallback={<LoadingScreen />}>
      <Routes>{renderList(routes)}</Routes>
    </Suspense>
  );
};
