import { BaseObject, Component, Entity, HCFactory, Viewer } from "@hc/engine";
import { Box, SxProps } from "@mui/material";
import React, { memo, useEffect, useRef, useState } from "react";
import { App } from "~/intance";
import { parseParticle } from "~/utils/parse";
import { CircularProgressWithLabel } from "./circularProgressWithLabel";

interface PreviewBoxProps {
  sx?: SxProps;
  getViewer?: (vw: Viewer) => void;
  data: Record<string, any>;
}

let viewer: Viewer;
let dispose: () => void;
let entity: BaseObject;
let timer2: NodeJS.Timeout;
let timer3: NodeJS.Timeout;
let timer4: NodeJS.Timeout;
let isMouted = false;

const PreviewBox: React.FC<PreviewBoxProps> = ({
  sx = {
    width: "100%",
    height: "100%",
  },
  getViewer,
  data,
}) => {
  const canvasRef = useRef<HTMLCanvasElement>(null);
  const [progress, setProgress] = useState({ start: false, value: 0 });

  const endLoad = () => {
    timer4 && clearInterval(timer4);
    setProgress({ start: false, value: 0 });
  };

  const render = () => {
    setProgress({ start: true, value: 0 });
    if (data && viewer) {
      viewer.Scene.particleSystems.forEach((n) => n.dispose());
      if (data?.key === "particle") {
        const fileData = data.json;
        const particle = parseParticle(fileData);
        particle.Intance.start();
        setProgress({ start: false, value: 0 });
      }

      if (data.key === "2D") {
        return null;
      }

      const en = HCFactory.CreateObject(data.key, data);
      entity = en;
      viewer.Scene.meshes.forEach((n) => n.dispose());

      if (en instanceof Entity) {
        timer4 = setInterval(() => {
          setProgress((prevProgress) => {
            let value = prevProgress.value;
            if (value < 90) value += 5;
            else {
              if (value < 99) {
                value += 1;
              }
            }
            return {
              start: true,
              value,
            };
          });
        }, 800);
        if (viewer.sceneControl) viewer.sceneControl.createDefaultEnv();
        en.RenderObject;
        if (en instanceof Component) {
          en.watchLoad().then(() => {
            viewer.zoomAll();
            viewer.cameraManager.ArcRotateControl.Alpha = Math.PI / 4;
            viewer.cameraManager.ArcRotateControl.Beta = Math.PI / 3;
            endLoad();
          });
        } else
          dispose = en.once("load", () => {
            timer2 = setTimeout(() => {
              viewer.zoomAll();
              viewer.cameraManager.ArcRotateControl.Alpha = Math.PI / 4;
              viewer.cameraManager.ArcRotateControl.Beta = Math.PI / 3;
            }, 0);
            endLoad();
          });
      }
    }
  };

  useEffect(() => {
    if (canvasRef.current) {
      isMouted = true;
      viewer && viewer.destory();
      viewer = App.intance.createView(canvasRef.current, {
        defaultLight: true,
      });
      getViewer?.(viewer);
    }
    return () => {
      isMouted = false;
      setTimeout(() => {
        if (!isMouted) {
          viewer && viewer.destory();
        } else {
          isMouted = false;
        }

        timer2 && clearTimeout(timer2);
        timer4 && clearTimeout(timer4);
      }, 100);
    };
  }, []);

  useEffect(() => {
    timer3 && clearTimeout(timer3);
    timer3 = setTimeout(() => {
      render();
    }, 100);
  }, [data]);

  return (
    <Box sx={{ ...sx, position: "relative" }}>
      <canvas ref={canvasRef}></canvas>
      {progress.start && (
        <Box
          sx={{
            position: "absolute",
            left: 0,
            right: 0,
            top: 0,
            bottom: 0,
            margin: "auto",
            width: "50px",
            height: "50px",
          }}
        >
          <CircularProgressWithLabel value={progress.value} label="" />
        </Box>
      )}
    </Box>
  );
};
export default memo(PreviewBox, (prev, nowV) => {
  return (
    prev.data.key === nowV.data.key &&
    prev.data.url === nowV.data.url &&
    prev.data.json === nowV.data.json
  );
});
