import { Box, InputAdornment, TextField } from "@mui/material";
import { observer } from "mobx-react-lite";
import React from "react";
import { useInteractionStoresContext } from "~/contexts/interactionStoreContext";

const WaitDetail: React.FC = () => {
  const store = useInteractionStoresContext();

  if (!store.CurrentActionDetail) return null;

  const detail = store.CurrentActionDetail;

  const handleChange = (evt) => [
    store.updateState(() => {
      detail.time = evt.target.value;
    }),
  ];

  return (
    <Box>
      <TextField
        variant="outlined"
        size="small"
        name="time"
        label="等待"
        value={detail.time}
        onChange={handleChange}
        InputProps={{
          endAdornment: <InputAdornment position="start">毫秒</InputAdornment>,
        }}
      />
    </Box>
  );
};
export default observer(WaitDetail);
