import React, { useEffect } from "react";
import { when } from "mobx";
import AppBar from "@mui/material/AppBar";
import Tabs from "@mui/material/Tabs";
import Tab from "@mui/material/Tab";
import Box from "@mui/material/Box";
import DragCard from "../common/dragCard";
import DataList from "../dataList";
import skyList from "./skyList";
import imageList from "./imageList";
import { useStores } from "~/contexts/store-context";
import { SkyboxMode } from "@hc/engine";
import { rootStore } from "~/stores/RootStore";
import { App } from "~/intance";
import HcTabs from "~/components/common/hcTabs";
const app = App.intance;
interface TabPanelProps {
  children?: React.ReactNode;
  dir?: string;
  index: number;
  value: number;
}

function TabPanel(props: TabPanelProps) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`full-width-tabpanel-${index}`}
      aria-labelledby={`full-width-tab-${index}`}
      {...other}
    >
      {children}
    </div>
  );
}
export default function FullWidthTabs() {
  const { fileStore } = useStores();
  const [value, setValue] = React.useState(0);

  const handleChange = (event: React.SyntheticEvent, newValue: number) => {
    setValue(newValue);
  };
  const handleClick = (d) => {
    App.intance.Viewer.sceneControl.Skybox = d.data.url;
    rootStore.BgType = App.intance.Viewer.sceneControl.Mode = SkyboxMode.Image;
    fileStore.setLoadProcess(true);
    let per = 0;
    let step = 10;
    let timer = setInterval(() => {
      per += step;
      if (per >= 90) {
        step = 1;
      }
      if (per >= 98) {
        clearInterval(timer);
        return;
      }
      fileStore.setLoadProcess(true, per);
    }, 1000);
    App.intance.Viewer.sceneControl.LoadObserable.addOnce((e) => {
      clearInterval(timer);
      fileStore.setLoadProcess(false);
    });
    return null;
  };
  useEffect(() => {
    when(
      () => !!App.intance,
      () => {
        const app = App.intance;
      }
    );
  }, []);
  const tabs = [
    {
      children: (
        <Box sx={{ flex: 1 }}>
          <DataList
            component={DragCard}
            list={skyList}
            onDoubleClick={handleClick}
          />
        </Box>
      ),
      index: 0,
      value: 0,
      label: "天空盒",
    },
    {
      children: (
        <Box sx={{ flex: 1 }}>
          <DataList
            component={DragCard}
            list={imageList}
            onDoubleClick={handleClick}
          />
        </Box>
      ),
      index: 1,
      value: 1,
      label: "图片",
    },
  ];
  return (
    <>
      <Box sx={{ bgcolor: "background.paper", width: 500 }}>
        <HcTabs
          tabs={tabs}
          value={value}
          onChange={handleChange}
          variant="fullWidth"
        ></HcTabs>
      </Box>
    </>
  );
}
